<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-lg-8 col-lg-offset-2">
        <h2 class="title">管理員帳號管理</h2>
      </div>
    </div>
    <hr />

    <table class="table table-bordered table-striped">
      <thead>
        <tr>
          <th scope="col">帳號</th>
          <th scope="col">姓名</th>
          <th scope="col">權限</th>
          <th scope="col"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(form,index) in forms">
          <td>{{ form.form_username }}</td>
          <td>{{ form.form_name }}</td>
          <td>{{ form.form_authority }}</td>
          <td>
            <button class="btn btn-primary" @click="change_authority(form)">編輯</button>
            <button class="btn btn btn-danger" @click="delete_data(form)" >刪除</button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- <router-link :to="`admin_account/new_account`"> -->
    <button class="btn btn-success" @click="new_admin()">新增管理員</button>
    <!-- </router-link> -->
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      new_form_name: "",
      forms: [],
      forms_2: [],
    };
  },
  created() {
    var self = this;
    self.getFormList();
  },
  mounted() {
    $("#wrapper").addClass("toggled");
  },
  methods: {
    //一進來取表單資料
    getFormList() {
      var self = this;
      this.axios
        .get(
          "/rise_api/public/index.php/admin_account/get_admin"
        )
        .then(response => {
          var forms = (self.forms = []);
          $.each(response.data, function(key, value) {
            forms.push({
              form_username: value.username,
              form_name: value.name,
              form_authority: value.authority
            });
          });

        });
      this.axios
        .get(
          "/rise_api/public/index.php/permit/get_petmit"
        )
        .then(response => {
          var forms_2 = (self.forms_2 = []);
          $.each(response.data, function(key, value) {
            forms_2.push({
              form_authority: value.group_name
            });
          });
        });
    },
    new_admin() {
      this.$swal({
        title: "新增管理員",
        html:
          '<p align="left">帳號:</p>' +
          '<input id="swal-input1" class="swal2-input">' +
          '<p align="left">密碼:</p>' +
          '<input id="swal-input2" type="password" class="swal2-input">' +
          '<p align="left">名稱:</p>' +
          '<input id="swal-input3" class="swal2-input">' +
          '<p align="left">權限:</p>' +
          `<select class="browser-default custom-select" id="select_id">
          ${this.forms_2.map(
            cat =>
              `<option value="${cat.form_authority}">${cat.form_authority}</option>`
          )}
          </select>`,
        focusConfirm: false,
        confirmButtonText: "新增",
        preConfirm: () => {
          return this.axios
            .post(
              "/rise_api/public/index.php/admin_account/new_admin",
              {
                admin_username: document.getElementById("swal-input1").value,
                admin_password: document.getElementById("swal-input2").value,
                admin_name: document.getElementById("swal-input3").value,
                admin_authority: document.getElementById("select_id").value
              }
            )
            .then(response => {
              this.$swal({
                icon: "success",
                title: "新增完成",
                showConfirmButton: false,
                timer: 1500
              }).then(response2 => {
                if (response.data == "1") {
                  this.getFormList();
                } else {
                }
              });
            });
        }
      });
    },
    delete_data(rowItem) {
      this.$swal({
        text: `確定要刪除帳號為${rowItem.form_username}使用者嗎?`,
        icon: "question",
        showCancelButton: true,
        confirmButtonText: "確定",
        cancelButtonText: "取消",
        preConfirm: () => {
          return this.axios
            .delete(
              "/rise_api/public/index.php/admin_account/delete_admin",
              {
                params: {
                  admin_id: rowItem.form_username
                }
              }
            )
            .then(response => {
              if ((response.data = "1")) {
                this.$swal({
                  icon: "success",
                  title: "刪除完成",
                  showConfirmButton: false,
                  timer: 1500
                }).then(response => {
                  this.getFormList();
                });
              }
            });
        }
      });
    },
    change_authority(rowItem) {
      this.$swal({
        title: `請確定要修正狀態的帳號為${rowItem.form_username}`,
        html:
          '<br>'+
          '<p align="left">權限:</p>' +
          `<select class="browser-default custom-select" id="select_id">
          ${this.forms_2.map(
            cat =>
              `<option value="${cat.form_authority}">${cat.form_authority}</option>`
          )}
          </select>`+'<br/><br/>'+
          '<p align="left">姓名:</p>'+`<input id="swal-input3" class="swal2-input" value="${rowItem.form_name}"/>`,
        inputAttributes: {
          autocapitalize: "off"
        },
        inputValue: rowItem.form_authority,
        showCancelButton: true,
        confirmButtonText: "修改",
        cancelButtonText: "取消",
        showLoaderOnConfirm: true,
        preConfirm: value => {
          return this.axios
            .put(
              "/rise_api/public/index.php/admin_account/admin_authority_update",
              {
                admin_id: rowItem.form_username,
                admin_authority: document.getElementById("select_id").value,
                admin_name:document.getElementById("swal-input3").value
              }
            )
            .then(response => {
              if (response.data == "1") {
                this.$swal({
                  icon: "success",
                  title: "修改完成",
                  showConfirmButton: false,
                  timer: 1500
                }).then(response => {
                  this.getFormList();
                });
              }
            });
        }
      });
    }
  }
};
</script>

<style>
</style>



