<!--
	題目選項樣式(一般版)
-->
<template>
  <div class="question-choice">
    <div class="choice row my-2 mx-0 p-1" v-for="(choice, idx) in choiceList">
      <div class="col-md p-0">
        <input class="choice-title px-2" type="text" placeholder="選項" v-model="choice.title" />
      </div>
      <div class="delete-question" @click="deleteChoice(choice,idx)">
        <font-awesome-icon icon="times-circle" />
      </div>
    </div>
    <div class="option-btn my-2">
      <button class="btn" @click="addChoice()">新增選項</button>
    </div>
  </div>
</template>

<script>
export default {
  props: ["choiceList"],
  methods: {
    addChoice(idx = this.choiceList.length) {
      var choice_obj = {
        title: ""
      };
      this.choiceList.splice(idx, 0, choice_obj);
    },
    deleteChoice(choice, rowItem) {
      this.$swal({
        title: `確定要刪除選項-${choice.title}嗎?`,
        text: "(此刪除僅畫面中刪除，若確定要刪除請點擊儲存)",
        icon: "question",
        showCancelButton: true,
        confirmButtonText: "確定",
        cancelButtonText: "取消"
      }).then(Response => {
        if (Response.value) {
          this.choiceList.splice(rowItem, 1);
        }
      });
    }
  }
};
</script>

<style>
/*-------------------------------*/
/*     Question-choice styles    */
/*-------------------------------*/

.choice {
  display: flex;
  background-color: #bdd7ee;
  border: 1px solid #41719c;
  border-radius: 0.3rem;
}
.choice-title {
  width: 100%;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 0.3rem;
  outline: none;
}

/*-------------------------------*/
/*     Option button styles    */
/*-------------------------------*/
.option-btn button {
  padding: 0.1rem 0.75rem;
  color: #fff;
  font-size: 14px;
  border-color: #5a825a;
  background-color: #a0bea0;
}

.option-btn button:hover {
  color: #fff;
  padding: 0.15rem 0.75rem;
  background-color: #a0bea0;
}
</style>



