<template>
  <span v-if="needHelper">
    <button
      type="button"
      class="btn btn-primary"
      @click="runHelper"
    >
      {{ helperTitle }}
    </button>
  </span>
</template>

<script>
import _ from 'lodash';

import { getEchoMan } from '../../utils/lang';

export default {
  props: ['helper', 'callback'],
  data() {
    return {
      needHelper: false,
      helperFunc: getEchoMan(),
      helperTitle: '',
    };
  },
  created() {
    if (!_.isNil(this.helper) && !_.isNil(this.helper.func)) {
      this.needHelper = true;

      this.helperFunc = this.helper.func;
      this.helperTitle = this.helper.title;
    }
  },
  methods: {
    runHelper() {
      if (this.needHelper) {
        const self = this;

        new Promise((resolve) => {
          self.helperFunc.call(null, self.callback);
          resolve(true);
        }).then((result) => result);
      }
    },
  },
  computed: {},
};
</script>

<style></style>



