import Vue from 'vue';
import Router from 'vue-router';
import axios from 'axios';

//後台
import Layout from '@/components/Layout.vue';
import Login from '@/management/Login.vue';
import Wrapper from '@/management/admin/Wrapper.vue'; //包含左邊的sidebar(選單)和上面header(登出)
import Review from '@/management/admin/Review.vue'; //回答紀錄
import Design from '@/management/admin/Design.vue'; //製作問卷
import Form from '@/management/admin/Form.vue'; //製作問卷==>編輯
import form_authority from '@/management/admin/form_authority.vue'; //表單設定==>權限
import Chart from '@/management/admin/Chart.vue'; //圖表設定
import admin_account from '@/management/admin/admin_account.vue'; //管理員帳號管理
import admin_log from '@/management/admin/admin_log.vue'; //管理員操作紀錄
import user_log from '@/management/admin/user_log.vue'; //使用者操作紀錄
import user_account from '@/management/admin/user_account.vue'; //使用者帳號管理
import store from '../store/index.js';
import group_permit from '@/management/admin/group_permit.vue'; //群組權限設定
import group_permit_edit from '@/management/admin/group_permit_edit.vue'; //群組權限設定==>編輯
import apptracker from '@/management/admin/apptracker.vue'; //apptracker
//import apptracker_test from '@/management/admin/apptracker_test'  //apptracker
import error404 from '@/view/error404.vue'; //404頁面

import DynaCompanyList from '@/dyna-form/company/index.vue';
import DynaCompanyEdit from '@/dyna-form/company/edit.vue';
import DynaStartupList from '@/dyna-form/startup/index.vue';
import DynaStartupEdit from '@/dyna-form/startup/edit.vue';
import DynaList from '@/dyna-form/list/index.vue';
import DynaEditor from '@/dyna-form/editor/index.vue';

Vue.use(Router);

let router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/management/login',
    },
    {
      //後台
      path: '/management',
      component: Layout,
      children: [
        {
          path: '',
          redirect: 'login',
        },
        {
          path: 'login',
          name: 'Login',
          component: Login,
        },
        {
          path: 'admin',
          component: Wrapper,
          children: [
            {
              path: '', //審查問卷
              redirect: 'review',
              meta: {
                requiresAuth: true,
              },
            },
            {
              path: 'review', //回答紀錄
              name: 'review',
              component: Review,
              meta: {
                requiresAuth: true,
              },
            },
            {
              path: 'design', //製作問卷
              name: 'design',
              component: Design,
              meta: {
                title: '問卷設計',
                requiresAuth: true,
              },
            },
            {
              path: 'design/:form/:version', //製作問卷==>編輯
              name: 'Form_design',
              component: Form,
              meta: {
                title: '問卷設計',
                isLoggedIn: true,
              },
            },
            {
              path: 'authority/:form', //製作問卷==>權限設定
              name: 'form_authority',
              component: form_authority,
              meta: {
                title: '問卷權限設定',
                isLoggedIn: true,
              },
            },
            {
              path: 'chart', //圖表設定
              name: 'chart',
              component: Chart,
              meta: {
                requiresAuth: true,
              },
            },
            {
              path: 'admin_account', //管理員帳號管理
              name: 'admin_account',
              component: admin_account,
              meta: {
                requiresAuth: true,
              },
            },
            {
              path: 'user_account', //使用者帳號管理
              name: 'user_account',
              component: user_account,
              meta: {
                requiresAuth: true,
              },
            },
            {
              path: 'admin_log', //管理員操作紀錄
              name: 'admin_log',
              component: admin_log,
              meta: {
                requiresAuth: true,
              },
            },
            {
              path: 'user_log', //會員紀錄
              name: 'user_log',
              component: user_log,
              meta: {
                requiresAuth: true,
              },
            },
            {
              path: 'group_permit', //群組權限設定
              name: 'group_permit',
              component: group_permit,
              meta: {
                requiresAuth: true,
              },
            },
            {
              path: 'group_permit/:group_name/:all_form', //群組權限設定編輯
              name: 'group_permit_edit',
              component: group_permit_edit,
              meta: {
                title: '群組權限設定編輯',
                requiresAuth: true,
              },
            },
            {
              path: 'apptracker', //apptracker
              name: 'apptracker',
              component: apptracker,
              meta: {
                requiresAuth: true,
              },
            },
          ],
        },
      ],
    },
    {
      path: '/dyna',
      component: Layout,
      children: [
        {
          path: 'company',
          component: Wrapper,
          children: [
            {
              path: '',
              redirect: 'list',
              meta: {
                requiresAuth: true,
              },
            },
            {
              path: 'list',
              name: 'DynaCompanyList',
              component: DynaCompanyList,
              meta: {
                requiresAuth: true,
              },
            },
            {
              path: 'edit',
              name: 'DynaCompanyEdit',
              component: DynaCompanyEdit,
              meta: {
                requiresAuth: true,
              },
            },
          ],
        },
        {
          path: 'startup',
          component: Wrapper,
          children: [
            {
              path: '',
              redirect: 'list',
              meta: {
                requiresAuth: true,
              },
            },
            {
              path: 'list',
              name: 'DynaStartupList',
              component: DynaStartupList,
              meta: {
                requiresAuth: true,
              },
            },
            {
              path: 'edit',
              name: 'DynaStartupEdit',
              component: DynaStartupEdit,
              meta: {
                requiresAuth: true,
              },
            },
          ],
        },
        {
          path: 'form',
          component: Wrapper,
          children: [
            {
              path: '',
              redirect: 'list',
              meta: {
                requiresAuth: true,
              },
            },
            {
              path: 'list/:formKey',
              name: 'DynaList',
              component: DynaList,
              meta: {
                requiresAuth: true,
              },
            },
            {
              path: 'editor/:formKey',
              name: 'DynaEditor',
              component: DynaEditor,
              meta: {
                requiresAuth: true,
              },
            },
          ],
        },
      ],
    },
    {
      path: '*',
      component: error404,
    },
  ],
});

router.beforeEach((to, from, next) => {
  // console.log(to);
  // console.log(from);
  // console.log(next);

  //後台的才需要判斷登入
  if (to.matched['0'].path == '/management') {
    //判斷式避免轉回首頁又出現一次
    if (to.name != 'Login') {
      axios
        .post('/rise_api/public/index.php/login/check_token', {
          authority: localStorage.authority,
          token: localStorage.token,
        })
        .then((response) => {
          if (response.data != '1') {
            Vue.swal
              .fire({
                icon: 'error',
                title: 'Oops...',
                text: '登入狀態錯誤，請重新登入',
              })
              .then(() => {
                next('/management/login');
              });
          }
        });
    }

    //超過兩小時 刪除登入的內容
    if (parseInt(new Date().getTime() / 1000) - localStorage.time > 14400) {
      localStorage.removeItem('token');
      localStorage.removeItem('authority');
      localStorage.removeItem('time');
      Vue.swal
        .fire({
          icon: 'error',
          title: 'Oops...',
          text: '登入逾時，請重新登入',
        })
        .then(() => {
          next('/management/login');
        });
    }

    //第一種權限 (編輯問卷 問卷權限設定 群組權限設定)
    if (to.matched.some((record) => record.meta.isLoggedIn)) {
      if (store.getters.isLoggedIn) {
        const url_name = to.name;
        if (url_name == 'Form_design' || url_name == 'form_authority') {
          axios
            .post('/rise_api/public/index.php/design/get_edit_user', {
              form_id: to.params.form,
            })
            .then((response) => {
              //用登入的權限群組去查看回傳是否為1
              axios
                .post('/rise_api/public/index.php/design/check_permit', {
                  group_name: localStorage.authority,
                })
                .then((response2) => {
                  // 1是admin權限 || 創建人和登入者一樣
                  if (
                    response2.data == 1 ||
                    response.data[0].create_name == localStorage.username
                  ) {
                  }
                  //製作問卷==>編輯
                  else if (url_name == 'Form_design') {
                    var edit_user = JSON.parse(response.data[0].edit_user);
                    if (edit_user[0].indexOf(localStorage.username) === -1) {
                      no_permit(next);
                    } else {
                    }
                  }
                  //製作問卷==>權限設定
                  else {
                    no_permit(next);
                  }
                });
              //權限是admin_group或是建立表的作者 都可以成功進入頁面
            });
        } else {
          console.log('error');
        }
        next();
        return;
      }
    } else {
      next();
    }

    //第二種權限
    if (to.matched.some((record) => record.meta.requiresAuth)) {
      if (store.getters.isLoggedIn) {
        axios
          .post('/rise_api/public/index.php/permit/get_petmit_sidebar', {
            authority: localStorage.authority,
          })
          .then((response) => {
            const data = response.data;
            var check_permit = [];

            //問卷設計
            data.form.name.forEach((name, index) => {
              var url = data.form.url[index];
              // 塞回到 vue 的 form
              check_permit.push(`${url}`);
              // this.form.push(name + "," + url);
            });
            //帳號管理
            data.user.name.forEach((name, index) => {
              var url = data.user.url[index];
              check_permit.push(`${url}`);
            });

            //系統紀錄
            data.log.name.forEach((name, index) => {
              var url = data.log.url[index];
              check_permit.push(`${url}`);
            });

            //權限管理
            data.permit.name.forEach((name, index) => {
              var url = data.permit.url[index];
              check_permit.push(`${url}`);
            });
            //apptracker
            data.app.name.forEach((name, index) => {
              var url = data.app.url[index];
              check_permit.push(`${url}`);
            });

            //用登入的權限群組去查看回傳是否為1
            axios
              .post('/rise_api/public/index.php/design/check_permit', {
                group_name: localStorage.authority,
              })
              .then((response2) => {
                let a = to.name == 'group_permit_edit' ? true : false;
                //1表示 admin權限
                if (response2.data == 1) {
                }
                //如果是群組權限設定頁面
                else if (a) {
                  if (check_permit.indexOf('group_permit') == -1) {
                    no_permit(next);
                  }
                }
                //-1表示 該群組的權限中 沒有這個url的名稱
                else if (check_permit.indexOf(to.name) == -1) {
                  no_permit(next);
                } else {
                }
              });
          });
        next();
        return;
      }
    } else {
      next();
    }
  }
  //前台
  else {
    next();
  }
});

//權限不足alter
function no_permit(a) {
  Vue.swal
    .fire({
      icon: 'error',
      title: '權限不足',
      text: '權限不足，請確認是否有此頁面的權限',
      confirmButtonText: '返回製作問卷頁面',
    })
    .then(() => {
      a('/management/admin/design');
    });
}

export default router;



