<!--
  製作問卷頁
-->
<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-lg-8 col-lg-offset-2">
        <h2 class="title">製作問卷</h2>
      </div>
    </div>
    <hr />

    <table class="table table-bordered table-striped">
      <thead>
        <tr>
          <th scope="col">問卷名稱</th>
          <th scope="col">版本編號</th>
          <th scope="col">報表類型</th>
          <th scope="col">建立時間</th>
          <th scope="col">最後更新時間</th>
          <th scope="col">
            <form class="form-inline my-lg-0">
              <input
                class="form-control mr-2"
                type="search"
                placeholder="問卷名稱"
                v-model="new_form_name"
              />
              <button class="btn btn-success" @click="createForm">新增</button>
            </form>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(form, index) in forms">
          <td v-show="form.edit_type">{{ form.form_name }}</td>
          <td v-show="form.edit_type">
            <select
              class="browser-default custom-select"
              v-model="form.selectitem"
              @change="choose(form)"
            >
              <option v-for="n in form.version" :value="n">V{{ n }}</option>
            </select>
          </td>
          <td v-show="form.edit_type">{{ form.report_name }}</td>
          <td v-show="form.edit_type">{{ form.created_time }}</td>
          <td v-show="form.edit_type">{{ form.last_update_time }}</td>
          <td v-show="form.edit_type">
            <router-link
              v-show="form.edit_type"
              :to="`design/${form.form_id}/${form.selectitem}`"
            >
              <button class="btn btn-primary">編輯</button>
            </router-link>
            <button
              v-if="form.published"
              v-show="form.edit_type"
              class="btn btn-outline-info"
              @click="change_published(form)"
            >
              下架
            </button>
            <button
              v-else
              v-show="form.edit_type"
              class="btn btn-info"
              @click="change_published(form)"
            >
              上架
            </button>
            <!-- <button :class="[form.published ? 'btn btn-outline-info' : 'btn btn-info', '']">{{form.up_or_down}}</button> -->
            <button class="btn btn-primary" @click="copyFormToNew(form)">
              複製
            </button>
            <button class="btn btn btn-danger" @click="deleteForm(form)">
              刪除
            </button>
            <button
              class="btn btn-secondary"
              v-show="form.edit_type"
              @click="copy_Form(form)"
            >
              新增版本
            </button>
            <router-link
              v-show="form.authority_type"
              :to="`authority/${form.form_id + `.` + form.form_name}`"
            >
              <button class="btn btn-warning">權限設定</button>
            </router-link>
            <button
              v-if="form.published"
              class="btn btn-dark"
              @click="copy(form)"
            >
              訪客連結
            </button>
            <button
              v-else
              class="btn btn-dark"
              tooltip.hover
              title="請先確認是否上架"
              disabled="disabled"
            >
              訪客連結
            </button>
            <!--button class="btn btn-dark" @click="copy(form)" tooltip.hover title="請先確認是否上架" disabled="disabled">訪客連結</button-->
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
import $ from "jquery";
import Vue from "vue";
// 製作問卷最右邊新增可編輯權限按鈕   (php回傳使用者回來和Local的比對 一樣的代表是創建者 就顯示編輯權限按鈕)
// 點進編輯權限後  可把人拉回來  (所謂的編輯 其實是看不看的到 並不是有沒有編輯按鈕出現) btn btn btn-dark
const getReportName = (report_type) => {
  let type = parseInt(report_type);
  if (type === 1) {
    return "一般報表";
  } else {
    return "競爭力報表";
  }
};

export default {
  data() {
    return {
      new_form_name: "",
      forms: [],
      showModal: false,
    };
  },
  created() {
    var self = this;
    self.getFormList();
  },
  mounted() {
    //進入動畫
    $("#wrapper").addClass("toggled");
  },
  methods: {
    //一進來取表單資料
    getFormList() {
      var self = this;
      this.axios
        .get("/rise_api/public/index.php/design/getFormList")
        .then((response) => {
          var forms = (self.forms = []);
          $.each(response.data, function (key, value) {
            forms.push({
              form_id: value.id,
              form_name: value.name,
              created_time: value.created_time,
              version: parseInt(value.version),
              report_type: parseInt(value.report_type),
              report_name: getReportName(value.report_type),
              last_update_time: value.last_updated_time,
              published: value.published == "1" ? true : false,
              selectitem: parseInt(value.n_version),
              authority_type:
                value.create_name == localStorage.username ? true : false, //表單創建者
              edit_type:
                value.create_name == localStorage.username ? true : false, //表單創建者
              edit_user: value.edit_user,
            });
            //console.log("1111");
            //console.log(value);
            //檢查編輯權限有此使用者  有的話將edit_type改為true
            var edit_user = JSON.parse(value.edit_user);
            //表示是空的
            if (edit_user[0].indexOf(localStorage.username) > -1) {
              forms[key].edit_type = true;
            }
          });
          //傳權限名稱給後端，回傳1表示admin(未來增加||可擴充)
          this.axios
            .post("/rise_api/public/index.php/design/check_permit", {
              group_name: localStorage.authority,
            })
            .then((response) => {
              if (response.data == 1) {
                $.each(forms, function (key, value) {
                  forms[key].authority_type = response.data == 1 ? true : false;
                  forms[key].edit_type = response.data == 1 ? true : false;
                });
              }
            });
        });
    },
    //上架(下架)按鈕
    change_published(rowItem) {
      let up_or_down_str = rowItem.published ? "下架" : "上架";
      let alert_str = `你確定要${up_or_down_str}${rowItem.form_name}的V${rowItem.version}版本嗎`;
      this.$swal({
        text: alert_str,
        icon: "question",
        showCancelButton: true,
        confirmButtonText: "確定",
        cancelButtonText: "取消",
        preConfirm: () => {
          return this.axios
            .put("/rise_api/public/index.php/design/publish_change", {
              form_id: rowItem.form_id,
              n_version: rowItem.selectitem,
              mode: rowItem.published,
            })
            .then((response) => {
              this.$swal({
                icon: "success",
                title: "修改完成",
                showConfirmButton: false,
                timer: 1500,
              });
              if (response.data == "1") {
                rowItem.published = false;
              } else {
                rowItem.published = true;
              }
            });
        },
      });
    },
    //新增問卷
    createForm() {
      var self = this;
      if (self.new_form_name != "") {
        if (!self.formExist(self.new_form_name)) {
          this.$swal({
            text: `確定要新增${self.new_form_name}嗎?`,
            icon: "question",
            showCancelButton: true,
            confirmButtonText: "確定",
            cancelButtonText: "取消",
            preConfirm: () => {
              return this.axios
                .post("/rise_api/public/index.php/design/createForm", {
                  form_name: self.new_form_name,
                  final_score: JSON.stringify({ dimensions: [], level: [] }),
                  create_name: localStorage.username,
                })
                .then((response) => {
                  self
                    .$swal({
                      icon: "success",
                      title: "新增完成",
                      showConfirmButton: false,
                      timer: 1500,
                    })
                    .then(() => {
                      if (response.data == "1") {
                        self.getFormList();
                        self.new_form_name = "";
                      } else {
                        // self.published = true;
                      }
                    });
                });
            },
          });
        } else {
          this.$swal({
            icon: "error",
            text: "問卷名稱已存在!",
          });
        }
      } else {
        this.$swal({
          icon: "error",
          text: "問卷名稱不可為空白!",
        });
      }
    },
    deleteForm(rowItem) {
      this.$swal({
        text: `確定要刪除${rowItem.form_name}的V${rowItem.selectitem}版本嗎?`,
        icon: "question",
        showCancelButton: true,
        confirmButtonText: "確定",
        cancelButtonText: "取消",
        preConfirm: () => {
          return this.axios
            .delete("/rise_api/public/index.php/design/deleteForm", {
              params: {
                form_id: rowItem.form_id,
                version: rowItem.selectitem,
              },
            })
            .then((response) => {
              this.$swal({
                icon: "success",
                title: "刪除完成",
                showConfirmButton: false,
                timer: 1500,
              });
              if (response.data == "1") {
                rowItem.published = false;
              } else {
                rowItem.published = true;
              }
            });
        },
      });
    },
    //新增版本
    copy_Form(rowItem) {
      this.$swal({
        text: `確定要新增${rowItem.form_name}的版本嗎?`,
        icon: "question",
        showCancelButton: true,
        confirmButtonText: "確定",
        cancelButtonText: "取消",
        preConfirm: () => {
          return this.axios
            .post("/rise_api/public/index.php/design/copyForm", {
              form_id: rowItem.form_id,
              version: rowItem.selectitem,
            })
            .then((response) => {
              // console.log(response);
              if (response.data == "1") {
                this.$swal({
                  icon: "success",
                  title: "新增完成",
                  showConfirmButton: false,
                  timer: 1500,
                });
                this.getFormList();
              }
            });
        },
      });
    },
    copyFormToNew(rowItem) {
      this.$swal({
        text: `輸入新增問卷名稱並複製${rowItem.form_name}`,
        input: "text",
        showCancelButton: true,
        confirmButtonText: "確定",
        cancelButtonText: "取消",
        preConfirm: (name) => {
          return this.axios
            .post("/rise_api/public/index.php/design/copyFormToNew", {
              form_id: rowItem.form_id,
              version: rowItem.selectitem,
              name: name,
              create_name: localStorage.username,
            })
            .then((response) => {
              if (response.data == "1") {
                this.$swal({
                  icon: "success",
                  title: "新增完成",
                  showConfirmButton: false,
                  timer: 1500,
                });
                this.getFormList();
              }
            });
        },
      });
    },
    formExist(name) {
      var self = this;
      var isExist = false;
      $.each(self.forms, function () {
        if (this.form_name == name) isExist = true;
      });
      return isExist;
    },
    //版本改變時
    choose(rowItem) {
      this.axios
        .post("/rise_api/public/index.php/design/publish_check", {
          form_id: rowItem.form_id,
          n_version: rowItem.selectitem,
        })
        .then((response) => {
          let response2 = response.data[0];
          rowItem.created_time = response2.api_data[0].created_time;
          rowItem.last_update_time = response2.api_data[0].last_updated_time;
          if (response2.sta == "1") {
            rowItem.published = true;
          } else {
            rowItem.published = false;
          }
        });
    },
    copy(rowItem) {
      console.log(rowItem.form_name); //問卷名稱
      var txt = `https://rise.iii.org.tw/rise_api/public/index.php/ssoLogin/toForm?username=Guest&password=Guest123&form=${rowItem.form_name}`;
      var oInput = document.createElement("input");
      oInput.value = txt;
      document.body.appendChild(oInput);
      oInput.select(); // 选择对象
      document.execCommand("Copy"); // 执行浏览器复制命令
      oInput.className = "oInput";
      oInput.style.display = "none";
      console.log("success!");
      this.$swal({
        icon: "success",
        title: "複製成功!",
        showConfirmButton: false,
        timer: 1500,
      });
    },
  },
};
</script>

<style>
</style>



