<template>
  <div class="question-info mx-0 my-2">
    <div
      v-if="
        question.number >= 0 &&
        question.type !== 'class' &&
        question.type !== 'page' &&
        type !== 'group'
      "
      class="question-order mr-2"
    >
      {{ question.number }}
    </div>
    <div v-if="type === 'group'" class="question-order mr-2">
      {{ question.order }}
    </div>
    <div class="question-type mr-2 px-2">{{ type_convert[question.type] }}</div>
    <input
      v-if="question.type !== 'page'"
      class="question-title mr-2 px-2"
      type="text"
      placeholder="題目名稱"
      v-model="question.title"
    />
  </div>
</template>

<script>
export default {
  props: ["question", "type"],
  data() {
    return {
      type_convert: {
        class: "分類標題",
        page: "分頁",
        companyName: "公司名稱",
        companyNum: "統一編號",
		career: "業別",
        choice: "單選題",
        selection: "多選題",
        number: "數字",
        percentage: "百分比",
        priority: "排序",
        text: "文字",
        email: "Email",
        phone: "電話",
      },
    };
  },
};
</script>

<style>
/*-------------------------------*/
/*     Question-info styles      */
/*-------------------------------*/
.question-info {
  display: flex;
  align-items: center;
}
.question-order {
  height: 30px;
  width: 40px;
  border: 1px solid #5b9bd5;
  border-radius: 0.5rem;
  text-align: center;
  line-height: 30px;
  font-size: 14px;
  font-weight: 500;
  color: #fff;
  background-color: #ccc;
}
.question-type {
  height: 30px;
  border: 1px solid #3b84b9;
  border-radius: 0.5rem;
  text-align: center;
  line-height: 30px;
  font-size: 14px;
  font-weight: 500;
  white-space: nowrap;
  color: #fff;
  background-color: #5b9bd5;
}
.question-title {
  display: block;
  float: right;
  height: 100%;
  width: 100%;
  line-height: 1.5;
  font-size: 1rem;
  font-weight: 600;
  color: #495057;
  border: none;
  outline: none;
  border-bottom: 1.5px solid #ced4da;
  background-color: transparent;
}
</style>



