<template>
  <div class="container-fluid" style="padding-bottom:5px">
    <div class="row">
      <div class="col-lg-8 col-lg-offset-2">
        <h2 class="title">群組權限設定</h2>
      </div>
    </div>
    <hr />

    <table class="table table-bordered table-striped">
      <thead>
        <tr>
          <th scope="col">群組名稱</th>
          <th scope="col"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(form,index) in forms">
          <td>{{ form.form_group_name }}</td>
          <td>
            <router-link :to="`group_permit/${form.form_group_name}/${form.all_form}`">
              <button class="btn btn-primary" @click="change_authority(form)">編輯</button>
            </router-link>

            <!-- <button class="btn btn btn-danger" @click="delete_data(form)">刪除</button> -->
          </td>
        </tr>
      </tbody>
    </table>
    <!-- <router-link :to="`admin_account/new_account`"> -->
    <div style="margin:0px 0px 15px 15px;">
      <button class="btn btn-success" @click="new_admin()">新增管理群組</button>
    </div>
    <!-- </router-link> -->
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      new_form_name: "",
      forms: []
    };
  },
  created() {
    var self = this;
    self.getFormList();
  },
  mounted() {
    $("#wrapper").addClass("toggled");
  },
  methods: {
    //一進來取表單資料
    getFormList() {
      var self = this;
      this.axios
        .get(
          "/rise_api/public/index.php/permit/get_petmit"
        )
        .then(response => {
          var forms = (self.forms = []);
          $.each(response.data, function(key, value) {
            forms.push({
              form_group_name: value.group_name,
              all_form: value.all_form
            });
          });
        });
    },
    new_admin() {
      this.$swal({
        title: "新增管理群組",
        html:
          '<p align="left">群組名稱:</..p>' +
          '<input id="swal-input1" class="swal2-input">',
        focusConfirm: false,
        confirmButtonText: "新增",
        preConfirm: () => {
          return this.axios
            .post(
              "/rise_api/public/index.php/permit/get_petmit_sidebar",
              {
                group_name: document.getElementById("swal-input1").value
              }
            )
            .then(response => {
              this.$swal({
                icon: "success",
                title: "新增完成",
                showConfirmButton: false,
                timer: 1500
              }).then(response2 => {
                if (response.data == "1") {
                  this.getFormList();
                } else {
                }
              });
            });
        }
      });
    }
  }
};
</script>

<style>
</style>



