<template>
  <dyna-list
    :edit-page="editPage"
    :form-key="formKey"
  />
</template>

<script>
import DynaList from '../../components/DynaList/index.vue';

export default {
  components: {
    DynaList,
  },
  props: {},
  data() {
    return {
      formKey: 'startup',
      editPage: '/dyna/startup/edit',
    };
  },
  created() {},
  computed: {},
  methods: {},
};
</script>

<style scoped></style>



