<!--
	題目選項樣式(有分數版)
-->
<template>
  <div class="question-choice">
    <div class="choice row my-2 mx-0 p-1" v-for="(choice, idx) in choiceList" :key="idx">
      <div class="col-md p-0" v-if="choice.otherFlag !== true">
        <input class="choice-title px-2" type="text" placeholder="選項" v-model="choice.title" />
      </div>
      <div class="col-md p-0" v-else>
        <input
          class="choice-title px-2"
          type="text"
          placeholder="選項"
          v-model="choice.title"
        />
      </div>
      <select v-if="question.type !== 'priority'" class="col-md-1 mx-1" v-model="choice.jump">
        <option value selected>跳題</option>
        <option v-for="idx in questions.length">{{ idx }}</option>
      </select>
      <select v-if="question.type !== 'priority'" class="col-md-3 mx-1" v-model="choice.group">
        <option value selected>題組</option>
        <option v-for="g in groups">{{ g.name }}</option>
      </select>
      <input
        v-if="question.type !== 'priority'"
        class="choice-score col-md-1 mx-1 px-2"
        type="text"
        placeholder="分數"
        v-model="choice.score"
      />
      <div v-if="choice.otherFlag !== true" class="delete-question" @click="deleteChoice(choice,idx)">
        <font-awesome-icon icon="times-circle" />
      </div>
    </div>
    <div class="option-btn my-2">
      <button class="btn" @click="addChoice()">新增選項</button>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {};
  },
  props: ["questions", "idx", "question"],
  methods: {
    addChoice(idx = this.choiceList.length) {
      let result = $.map(this.questions[this.idx].choiceList, function(
        item,
        index
      ) {
        return item.otherFlag;
      }).indexOf(true);
      let choice_obj = {
        title: "",
        jump: "",
        group: "",
        score: "",
        otherFlag:false
      };
      // 代表沒有其他選項可以直接新增
      if (result == -1) {
        this.questions[this.idx].choiceList.push(choice_obj);
      }
      // 沒有其他選項的時候 要把前一個往前推
      else {
        this.choiceList.splice(result, 1, choice_obj);
      }
    },
    deleteChoice(choice, rowItem) {
      this.$swal({
        title: `確定要刪除選項-${choice.title}嗎?`,
        text: "(此刪除僅畫面中刪除，若確定要刪除請點擊儲存)",
        icon: "question",
        showCancelButton: true,
        confirmButtonText: "確定",
        cancelButtonText: "取消"
      }).then(Response => {
        if (Response.value) {
          this.choiceList.splice(rowItem, 1);
        }
      });
    }
  },
  computed: {
    choiceList() {
      var self = this;
      //判斷如果有人自己輸入其他選項 跳出警告訊息
      $.each(this.questions[this.idx].choiceList, function(key, value) {
        //  如果message==undefined (表示不是透過其他選項生成的題目)
        if (value.message == undefined && value.title == "其他選項") {
          self
            .$swal({
              icon: "error",
              title: "不得輸入其他選項",
              text: "若需要其他選項，請於右方點選其他選項"
            })
            .then(() => {
              value.title = "其他選";
            });
        }
      });
      //如果有勾其他選項
      if (this.question.hasOther) {
        let result = $.map(this.questions[this.idx].choiceList, function(
          item,
          index
        ) {
          return item.otherFlag;
        }).indexOf(true);
        // 如果沒有其他選項 就給他push一個
        if (result == -1) {
          this.questions[this.idx].choiceList.push({
            title: "其他選項",
            jump: "",
            group: "",
            score: "",
            message: "",
            otherFlag:true
          });
        } else {
        }
      }
      // 如果沒有勾其他選項 就直接執行
      else {
        var result2 = $.map(this.questions[this.idx].choiceList, function(
          item,
          index
        ) {
          return item.otherFlag;
        }).indexOf(true);
        //如果有找到其他選項  才把其他選項刪除
        if (result2 !== -1) {
          this.questions[this.idx].choiceList.splice(result2, 1);
        }
      }
      return this.questions[this.idx].choiceList;
    },
    groups() {
      var g = this.questions.filter(function(item, index, array) {
        return item.type === "group";
      });
      return g;
    }
  }
};
</script>

<style>
/*-------------------------------*/
/*     Question-choice styles    */
/*-------------------------------*/

.choice {
  display: flex;
  background-color: #bdd7ee;
  border: 1px solid #41719c;
  border-radius: 0.3rem;
}
.choice-title {
  width: 100%;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 0.3rem;
  outline: none;
}

.choice-score {
  width: 50px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 0.3rem;
  outline: none;
}

.choice select {
  padding: 0;
  color: #777;
}

/*-------------------------------*/
/*     Option button styles    */
/*-------------------------------*/
.option-btn button {
  padding: 0.1rem 0.75rem;
  color: #fff;
  font-size: 14px;
  border-color: #5a825a;
  background-color: #a0bea0;
}

.option-btn button:hover {
  color: #fff;
  padding: 0.15rem 0.75rem;
  background-color: #a0bea0;
}
</style>



