<!--
	排序題型樣式
-->
<template>
	<div class="row mx-0 my-2">
		<div class="question-area col-9 p-2">
			<question-info :question="question" :type="type"></question-info>
      <question-choice-score :questions="questions" :idx="idx" :question="question"></question-choice-score>
		</div>
		<basic-option :question="question">
		</basic-option>
	</div>
</template>

<script>
	import QuestionInfo from './QuestionInfo.vue'
  import QuestionChoiceScore from "./QuestionChoiceScore.vue";
	import QuestionChoiceBasic from './QuestionChoiceBasic'
	import BasicOption from './BasicOption.vue'
	export default{
		props:['type',"questions", "idx"],
		components:{
			QuestionInfo,
      QuestionChoiceScore,
			QuestionChoiceBasic,
			BasicOption,
		},
    computed: {
      question() {
        return this.questions[this.idx];
      }
    }
	}
</script>

<style>
.question-area{
	border-right: 2px dashed #bbb;
}
</style>



