import dayjs from 'dayjs';

export function buildPredicator(script) {
  return Function('"use strict";return (' + script + ')')();
}

export function buildTransformer(script) {
  return Function('"use strict";return (' + script + ')')();
}

export function getTrueMan() {
  return () => true;
}

export function getEchoMan() {
  return (value) => value;
}

// ref: https://stackoverflow.com/questions/40710628/how-to-convert-snake-case-to-camelcase-in-my-app
export const snakeToCamel = (str) =>
  str
    .toLowerCase()
    .replace(/([-_][a-z])/g, (group) =>
      group.toUpperCase().replace('-', '').replace('_', '')
    );

// ref: https://dirask.com/posts/JavaScript-check-if-function-is-async-DNkKrp
export function isAsync(func) {
  if (func.constructor && func.constructor.name === 'AsyncFunction') {
    return true;
  }
  return false;
}

// ref: https://medium.com/@trmaphi/lodash-isempty-value-you-might-be-using-it-the-wrong-way-d83210d7decf
export function isEmptyValue(value) {
  return (
    value === undefined ||
    value === null ||
    value === NaN ||
    (typeof value === 'object' && Object.keys(value).length === 0) ||
    (typeof value === 'string' && value.trim().length === 0)
  );
}

export function parseRoc(roc) {
  const rocDate = `${roc}`.trim();

  // if (rocDate.length === 8)

  if (rocDate.length === 7) {
    const AD = 19110000 + parseInt(roc);
    return AD.toString();
  } else {
    return roc;
  }
}

export function parseAD(AD) {
  const date = dayjs(AD, 'YYYYMMDD');
  return date.toDate();
}



