<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-lg-8 col-lg-offset-2">
        <h2 class="title">圖表設定</h2>
      </div>
    </div>
    <hr />

    <div class="row">
      <div class="col-3">
        <!-- 用v-model綁定value給change用 -->
        <select class="browser-default custom-select" @change="get_version()" v-model="chooseValue">
          <option disabled value>請選擇表單名稱</option>
          <option v-for="n in forms" :value="n.form_id">{{n.form_name}}</option>
        </select>
      </div>
      <div class="col-2" style="padding-left:0px">
        <!-- 不透過v-model綁定value 在change用$event -->
        <select class="browser-default custom-select" @change="get_chart()" v-model="chooseVersion">
          <option v-for="n in version_forms" :value="n.form_version">{{n.form_version}}</option>
        </select>
      </div>
      <br />
      <br />
      <br />
    </div>
    <table v-if="show_type===true" class="table table-bordered table-striped">
      <thead>
        <tr>
          <th scope="col" style="vertical-align:text-top;">
            <font>圖表名稱</font>
          </th>
          <th scope="col" style="vertical-align:text-top;">圖表類型</th>

          <th scope="col" style="vertical-align:text-top;">
            <font>顯示</font>
          </th>
          <th>
            <form class="form-inline my-lg-0">
              <input
                class="form-control mr-2"
                type="search"
                placeholder="新增圖表名稱"
                v-model="new_chart_name"
              />
              <button class="btn btn-success" @click="createchart">新增</button>
            </form>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(form,index) in table_forms ">
          <!-- <td align="center">{{ form.chart_form_name }}</td> -->
          <td align="center">
            <input
              class="form-control mr-2"
              type="text"
              v-model="form.chart_form_name"
              :readonly="form.edit_type"
            />
          </td>
          <td align="center">
            <!-- {{ form.chart_form_type }} -->
            <select
              class="browser-default custom-select"
              v-model="form.chart_form_type"
              @change="change_chart(table_forms,index)"
            >
              <option value="bar">長條圖</option>
              <option value="horizontalBar">橫條圖</option>
              <option value="pie">圓環圖</option>
              <option value="line">折線圖</option>
              <option value="radar">雷達圖</option>
            </select>
          </td>
          <td align="center" style="padding-top: 20px;">
            <!-- {{ form.chart_form_show }} -->
            <input
              type="checkbox"
              @click="change_chart_show(table_forms,index)"
              v-model="form.chart_form_show"
              :true-value="1"
              :false-value="0"
            />
          </td>
          <td>
            <button
              v-if="form.chart_form_edit"
              class="btn btn-outline-primary"
              @click="edit_chart(table_forms,index)"
            >編輯</button>
            <button v-else class="btn btn-primary">編輯中</button>
            <button class="btn btn-danger" @click="delete_chart(table_forms,index)">刪除</button>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="row">
      <div class="col-1"></div>
      <div class="col-10" v-show="show_type_2==true">
        <canvas
          id="myChart"
          width="700"
          height="350"
          class="chartjs-render-monitor"
          style="display: block; width: 900px; height: 500px;chartjs-render-animation 0.001s"
        ></canvas>
        <br />
        <br />
        <div class="row">
          <div class="col-1"></div>
          <div class="col-9">
            <form v-show="!pie_type" class="form-inline">
              <div class="form-group">
                <label class="form-label">滿分:&nbsp;</label>
                <input type="text" class="form-control form-control-xxx" v-model="maxscore" />
                <label class="form-label">&nbsp;分數間距:&nbsp;</label>
                <input type="text" class="form-control form-control-xxx" v-model="scoreRange" />
                <label class="form-label">&nbsp;結果頁顯示區域:&nbsp;</label>
                <select v-model="part">
                  <option value="1">第一部分</option>
                  <option value="2">第二部分</option>
                  <option value="3">第三部分</option>
                </select>
              </div>
            </form>
            <br />
          </div>
<!--          <div class="col-2"></div>-->
        </div>
        <div style="padding-bottom: 10px">
          <label class="form-label">圖表說明:&nbsp;</label>
          <textarea  class="form-control form-control-xxx" style="width: 735px" v-model="description" ></textarea>
        </div>
        <div>
          <div class="form-row align-items-center" v-for="(form,index) in chart_forms.dimensions">
            <div v-if="pie_type" class="col-2"></div>

            <div class="col-auto">
              <label>構面:</label>
            </div>
            <div class="col-auto">
              <div class="input-group mb-2">
                <select
                  class="form-control"
                  v-model="form.subjectArr[0]"
                  @change="change_dimensions"
                >
                  <option v-for="(form2,index) in dimensions_forms[0]" :value="form2">{{form2}}</option>
                </select>
              </div>
            </div>
            <div class="col-auto">
              <label v-if="!pie_type">產業平均分數:</label>
              <label v-else>模擬分數:</label>
            </div>
            <div class="col-auto">
              <div class="input-group mb-2">
                <input
                  type="text"
                  class="form-control"
                  v-model="form.historyArr"
                  @input="change_history(chart_forms.dimensions,index)"
                />
              </div>
            </div>
            <div v-if="!pie_type" class="col-auto">
              <label v-if="!pie_type">同業平均分數:</label>
              <label v-else>模擬分數:</label>
            </div>
            <div v-if="!pie_type" class="col-auto">
              <div class="input-group mb-2">
                <input
                  type="text"
                  class="form-control"
                  v-model="form.historyArr2"
                  @input="change_history2(chart_forms.dimensions,index)"
                />
              </div>
            </div>
            <div class="delete-level" @click="delete_dimensions(chart_forms.dimensions,index)">
              <font-awesome-icon icon="times-circle" />
            </div>
          </div>
        </div>
        <button class="btn btn-primary newLevel" @click="new_dimensions">新增構面</button>
        <div align="right">
          <button class="btn btn-success" @click="save_chart">儲存圖表設定</button>
        </div>
      </div>
      <div class="col-1"></div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Chart from "chart.js";

export default {
  data() {
    return {
      before_click: "",
      forms: [], //表單名稱
      chooseValue: "", //表單的選擇值
      version_forms: [], //表單版本
      chooseVersion: "", //版本的選擇值
      table_forms: [], //表格內容
      show_type: false, //表單的顯示
      show_type_2: false, //圖表的顯示
      pie_type: false, //圓環圖
      new_chart_name: "", //新圖表名稱的值
      scoreRange: 1, //圖表分數間距
      maxscore: 5, //圖表滿分
      chart_forms: [], //canvas元件
      dimensions_forms: [], //構面表單
      html_chart: "", //顯示圖表
      chart_id: "", //編輯中的圖表id
      part:1,
      description:""
    };
  },

  mounted() {
    $("#wrapper").addClass("toggled");
  },
  watch: {
    maxscore() {
      //是圓環圖的時候 不能執行這段
      if (this.pie_type != true) {
        this.html_chart.options.scales.yAxes[0].ticks.max = parseInt(
          this.maxscore
        );
        this.html_chart.update();
      }
    },
    scoreRange() {
      //是圓環圖的時候 不能執行這段
      if (this.pie_type != true) {
        this.html_chart.options.scales.yAxes[0].ticks.stepSize = parseInt(
          this.scoreRange
        );
        this.html_chart.update();
      }
    }
  },
  created() {
    this.getFormList();
  },
  methods: {
    //點擊新增購面時
    new_dimensions() {
      //給自己的分數亂數 (區間是1到目前設定的最大值)
      let random_score = Math.floor(
        Math.random() * parseInt(this.maxscore) + 1
      );
      this.chart_forms.dimensions.push({
        subjectArr: ["請選擇構面"],
        historyArr: random_score,
        historyArr2:random_score
      });
      //給一個chart新的labels名稱(請選擇構面) 分數亂數 最後更新圖表 (是圓環圖的時候 不能執行這段)
      if (this.pie_type != true) {
        this.html_chart.data.labels.push(["請選擇構面"]);
        this.html_chart.data.datasets[1].data.push(random_score);
      }
      random_score = Math.floor(Math.random() * parseInt(this.maxscore) + 1);
      this.html_chart.data.datasets[0].data.push(random_score);

      this.html_chart.update();
    },
    //選擇構面更改時
    change_dimensions() {
      this.html_chart.update();
    },
    //歷史分數更改時
    change_history(rowItem, index) {
      this.html_chart.data.datasets[1].data[index] = rowItem[index].historyArr;
      this.html_chart.update();
    },
    //歷史分數更改時
    change_history2(rowItem, index) {
      this.html_chart.data.datasets[2].data[index] = rowItem[index].historyArr2;
      this.html_chart.update();
    },
    //一進來取表單資料
    getFormList() {
      var forms = this.forms;
      this.axios
        .post(
          "/rise_api/public/index.php/review/getForm",
          {
            create_name: localStorage.username,
            group_name: localStorage.authority
          }
        )
        .then((response) => {
          //代表不是admin
          if (response.data[0].sta != 1) {
            $.each(response.data[0].api_data, function (key, value) {
              let edit_user_json = JSON.parse(value.edit_user);
              // 是創建者或在可編輯者中
              if (
                value.create_name == localStorage.username ||
                edit_user_json[0].indexOf(localStorage.username) > -1
              ) {
                forms.push({
                  form_id: value.id,
                  form_name: value.name,
                });
              }
            });
          } else {
            $.each(response.data[0].api_data, function (key, value) {
              forms.push({
                form_id: value.id,
                form_name: value.name,
              });
            });
          }
        });
    },
    //拿版本
    get_version() {
      var self = this;
      this.version_forms = [];
      var version_forms = this.version_forms;
      var chooseValue = this.chooseValue;

      this.axios
        .post(
          "/rise_api/public/index.php/review/getForm_version",
          {
            form_id: chooseValue,
          }
        )
        .then((response) => {
          this.show_type = false;
          this.show_type_2 = false;

          version_forms.push({
            form_version: "請選擇表單版本",
            form_id: "請選擇表單版本",
          }); //加入空白
          $.each(response.data, function (key, value) {
            version_forms.push({
              form_id: chooseValue,
              form_version: `V${value.version}`,
            });
          });
          self.chooseVersion = "請選擇表單版本";
        });
    },
    //新增圖表
    createchart() {
      var self = this;
      this.axios
        .post(
          "/rise_api/public/index.php/chart/newchart",
          {
            form_id: self.chooseValue,
            version: self.chooseVersion.replace("V", ""),
            chart_name: self.new_chart_name,
          }
        )
        .then((response) => {
          this.get_chart();
          this.$swal({
            icon: "success",
            title: "新增完成",
            showConfirmButton: false,
            timer: 1500,
          });
        });
    },
    //拿表單+版本的表格
    get_chart() {
      var self = this;
      self.table_forms = [];
      var form_version = self.chooseVersion;
      form_version = form_version == "All" ? "All" : form_version.substr(1);
      this.axios
        .post(
          "/rise_api/public/index.php/chart/getchart_form",
          {
            form_id: self.chooseValue,
            version: form_version,
          }
        )
        .then((response) => {
          self.show_type_2 = false;
          $.each(response.data, function (key, value) {
            self.table_forms.push({
              chart_form_name: value.title,
              chart_form_type: value.type,
              chart_form_show: value.show_chart,
              chart_form_edit: true,
              chart_form_id: value.id,
              edit_type: true,
            });
          });
          self.show_type = true;

          //把問卷+版本的構面取回
          self.axios
            .get(
              "/rise_api/public/index.php/chart/getForm_dimension/" +
                self.chooseValue +
                "/" +
                self.chooseVersion.replace("V", "")
            )
            .then((response) => {
              self.dimensions_forms = [];
              self.dimensions_forms.push(Object.keys(response.data.dimensions));
              //在第一個新增"請選擇構面"
              self.dimensions_forms[0].unshift("請選擇構面");
            });
        });
    },
    //改變圖表樣式
    change_chart(rowItem, index) {
      var self = this;
      let type = rowItem[index].chart_form_type;
      if (this.before_click !== "") {
        var ctx = document.getElementById("myChart").getContext("2d");
        if (type == "bar") {
          self.html_chart.destroy();
          this.pie_type = false;
          this.bar(ctx, self.chart_forms);
        } else if (type == "line") {
          self.html_chart.destroy();
          this.pie_type = false;
          this.line(ctx, self.chart_forms);
        } else if (type == "radar") {
          self.html_chart.destroy();
          this.pie_type = false;
          this.radar(ctx, self.chart_forms);
        } else if (type == "horizontalBar") {
          self.html_chart.destroy();
          this.pie_type = false;
          this.horizontalBar(ctx, self.chart_forms);
        } else {
          self.html_chart.destroy();
          this.pie_type = true;
          this.pie(ctx, self.chart_forms);
        }
      }
    },
    //改變顯示狀態
    change_chart_show(rowItem, index) {
      var self = this;
      let up_or_down_str =
        rowItem[index].chart_form_show == 0 ? "顯示" : "隱藏";
      let alert_str = `你確定要${up_or_down_str}名稱為${rowItem[index].chart_form_name}的圖表嗎`;
      this.$swal({
        text: alert_str,
        icon: "question",
        showCancelButton: true,
        confirmButtonText: "確定",
        cancelButtonText: "取消",
      }).then((Response) => {
        if (Response.value) {
          this.axios
            .put(
              "/rise_api/public/index.php/chart/change_chart_show",
              {
                chart_id: rowItem[index].chart_form_id,
                show_chart: rowItem[index].chart_form_show,
              }
            )
            .then((response) => {
              if (response.data == "1") {
                this.$swal({
                  icon: "success",
                  title: "修改完成",
                  showConfirmButton: false,
                  timer: 1500,
                });
              } else {
                this.$swal({
                  icon: "error",
                  title: "修改失敗",
                  showConfirmButton: false,
                  timer: 1500,
                });
              }
            });
        } else {
          self.table_forms[index].chart_form_show =
            rowItem[index].chart_form_show == 0 ? "1" : "0";
        }
      });
    },
    //編輯按鈕
    edit_chart(rowItem, index) {
      var self = this;

      //第一次執行if 之後執行else
      if (this.before_click === "") {
        //改變圖表名稱(input) readonly
        rowItem[index].edit_type = false;
        this.before_click = index;
        rowItem[index].chart_form_edit = false;
      } else {
        //改變圖表名稱(input) readonly
        rowItem[index].edit_type = false;
        rowItem[this.before_click].edit_type = true;
        rowItem[this.before_click].chart_form_edit = true;
        rowItem[index].chart_form_edit = false;
        this.before_click = index;
      }
      this.show_type_2 = true;
      this.chart_id = rowItem[index].chart_form_id;
      //拿chart 資料表的json回來
      this.axios
        .post(
          "/rise_api/public/index.php/chart/get_chartjson",
          {
            chart_id: this.chart_id,
          }
        )
        .then((response) => {
          //轉成json後 將圖表Y軸的值設定 最後判斷圖形種類後畫圖。
          self.chart_forms = JSON.parse(response.data[0].chart_json);
          var ctx = document.getElementById("myChart").getContext("2d");
          self.maxscore = self.chart_forms.maxscore;
          self.scoreRange = self.chart_forms.scoreRange;
          self.part = response.data[0].part;
          self.description = response.data[0].description;

          if (this.html_chart != []) this.html_chart.destroy(); //如果不是第一次的話 狀態會是true 執行chart的destroy的function
          //依照圖型畫圖
           if (rowItem[index].chart_form_type == "bar") {
            this.pie_type = false;
            this.bar(ctx, self.chart_forms);
          } else if (rowItem[index].chart_form_type == "line") {
            this.pie_type = false;
            this.line(ctx, self.chart_forms);
          } else if (rowItem[index].chart_form_type == "radar") {
            this.pie_type = false;
            this.radar(ctx, self.chart_forms);
          } else if (rowItem[index].chart_form_type == "horizontalBar") {
            this.pie_type = false;
            this.horizontalBar(ctx, self.chart_forms);
          } else {
            this.pie_type = true;
            this.pie(ctx, self.chart_forms);
          }
        });
    },
    //刪除圖表
    delete_chart(rowItem, index) {
      this.$swal({
        text: `確定要刪除名稱為${rowItem[index].chart_form_name}的圖表嗎?`,
        icon: "question",
        showCancelButton: true,
        confirmButtonText: "確定",
        cancelButtonText: "取消",
        preConfirm: () => {
          return this.axios
            .delete(
              "/rise_api/public/index.php/chart/delete_chart",
              {
                params: {
                  chart_id: rowItem[index].chart_form_id,
                },
              }
            )
            .then((response) => {
              if ((response.data = "1")) {
                this.$swal({
                  icon: "success",
                  title: "刪除完成",
                  showConfirmButton: false,
                  timer: 1500,
                }).then((response) => {
                  this.get_chart();
                });
              }
            });
        },
      });
    },
    //儲存圖表設定按鈕
    save_chart() {
      var self = this;
      //使歷史分數 總分 分數間距都只能是數字 如果構面是請選擇構面(未設定也不行)//
      let have_string = false;
      var re = /^[0-9]+$/;
      $.each(this.chart_forms.dimensions, function (key, value) {
        if (!re.test(value.historyArr) || value.subjectArr == "請選擇構面") {
          have_string = true;
          return false;
        }
      });
      if (!re.test(this.maxscore) || !re.test(this.scoreRange))
        have_string = true;
      //使歷史分數 總分 分數間距都只能是數字 如果構面是請選擇構面(未設定也不行)//
      if (have_string) {
        this.$swal({
          icon: "error",
          title: "分數中不可有非數字的內容或未選的構面且至少要填寫同業平均分數",
          showConfirmButton: false,
          timer: 1500,
        });
      } else {
        // 目前編輯中的資料內容
        console.log(self.maxscore)

        var find_arr = self.table_forms.find(function (item, index, array) {
          return item.chart_form_id == self.chart_id;
        });
        self.chart_forms.maxscore = self.maxscore;
        self.chart_forms.scoreRange = self.scoreRange;
        this.$swal({
          text: `確定要儲存嗎?`,
          icon: "question",
          showCancelButton: true,
          confirmButtonText: "確定",
          cancelButtonText: "取消",
          preConfirm: () => {
            return this.axios
              .put(
                "/rise_api/public/index.php/chart/save_chart",
                {
                  chart_id: this.chart_id,
                  chart_json: JSON.stringify(self.chart_forms),
                  title: find_arr.chart_form_name,
                  type: find_arr.chart_form_type,
                  part:self.part,
                  description:self.description
                }
              )
              .then((response) => {
                if (response.data == "1") {
                  this.$swal({
                    icon: "success",
                    title: "修改完成",
                    showConfirmButton: false,
                    timer: 1500,
                  });
                } else {
                  this.$swal({
                    icon: "error",
                    title: "修改失敗",
                    showConfirmButton: false,
                    timer: 1500,
                  });
                }
              });
          },
        });
      }
    },
    //id, 分數陣列  構面陣列  歷史分數陣列  圖表名稱 分數上限 分數間距
    horizontalBar(ctx, data) {
      let scoreArr = [];
      let subjectArr = [];
      let historyArr = [];
      let historyArr2 = [];
      // let chartTitle = data.chartTitle;
      let maxscore = data.maxscore;
      let scoreRange = data.scoreRange;
      $.each(data.dimensions, function(key, value) {
        subjectArr.push(value.subjectArr);
        historyArr.push(value.historyArr);
        historyArr2.push(value.historyArr2);
      });
      $.each(data.dimensions, () => {
        let random_score = Math.floor(
          Math.random() * parseInt(this.maxscore) + 1
        );
        scoreArr.push(random_score);
      });
      //給自己的分數 給予亂數
      let marksData = {
        labels: subjectArr,
        datasets: [
          {
            label: "我自己的分數",
            backgroundColor: "#ffb81c",
            borderColor: "#ffb81c",
            data: scoreArr
          }
        ]
      };
      if (historyArr.length > 0) {
        let dataset = {
          label: "產業平均分數",
          backgroundColor: "#00816d",
          borderColor: "#00816d",
          data: historyArr
        };
        marksData.datasets.push(dataset);
      }
      if (historyArr2.length > 0) {
        if(historyArr2[0] !== "0" && historyArr2[0] !== ""){
          let dataset = {
            label: "同業平均分數",
            backgroundColor: "#7f7f7f",
            borderColor: "#7f7f7f",
            data: historyArr2
          };
          marksData.datasets.push(dataset);
        }
      }
      let chartOptions = {
        hover: {
          animationDuration: 0
        },
        animation: {
          duration: 1,
          onComplete: function() {
            var chartInstance = this.chart,
              ctx = chartInstance.ctx;

            ctx.font = Chart.helpers.fontString(
              Chart.defaults.global.defaultFontSize,
              Chart.defaults.global.defaultFontStyle,
              Chart.defaults.global.defaultFontFamily
            );
            ctx.textAlign = "center";
            ctx.textBaseline = "bottom";

            this.data.datasets.forEach(function(dataset, i) {
              var meta = chartInstance.controller.getDatasetMeta(i);
              meta.data.forEach(function(bar, index) {
                var data = dataset.data[index];
                ctx.fillText(data, bar._model.x, bar._model.y - 5);
              });
            });
          }
        },
        legend: {
          display: true
        },
        scales: {
          xAxes: [
            {
              ticks: {
                beginAtZero: true,
                min: 0,
                max: parseInt(maxscore),
                fontSize: 10,
                stepSize: parseInt(scoreRange)
              },
              scaleLabel: {
                display: true,
                labelString: "得分數"
              }
            }
          ],
          yAxes: [
            {
              ticks: {
                fontSize: 12
              }
            }
          ]
        },
        title: {
          display: true,
          text: ""
        }
      };
      this.html_chart = new Chart(ctx, {
        type: "horizontalBar",
        data: marksData,
        options: chartOptions,
        responsive: true,
        maintainAspectRatio: false
      });
    },
    //id, 分數陣列  構面陣列  歷史分數陣列  圖表名稱 分數上限 分數間距
    bar(ctx, data) {
      let scoreArr = [];
      let subjectArr = [];
      let historyArr = [];
      let historyArr2 = [];
      // let chartTitle = data.chartTitle;
      let maxscore = data.maxscore;
      let scoreRange = data.scoreRange;

      $.each(data.dimensions, function(key, value) {
        subjectArr.push(value.subjectArr);
        historyArr.push(value.historyArr);
        historyArr2.push(value.historyArr2);
      });

      $.each(data.dimensions, () => {
        let random_score = Math.floor(
          Math.random() * parseInt(this.maxscore) + 1
        );
        scoreArr.push(random_score);
      });
      //給自己的分數 給予亂數
      let marksData = {
        labels: subjectArr,
        datasets: [
          {
            label: "我自己的分數",
            backgroundColor: "#ffb81c",
            borderColor: "#ffb81c",
            data: scoreArr
          }
        ]
      };
      if (historyArr.length > 0) {
        let dataset = {
          label: "產業平均分數",
          backgroundColor: "#00816d",
          borderColor: "#00816d",
          data: historyArr
        };
        marksData.datasets.push(dataset);
      }
      if (historyArr2.length > 0) {
        if(historyArr2[0] !== "0" && historyArr2[0] !== ""){
          let dataset = {
            label: "同業平均分數",
            backgroundColor: "#7f7f7f",
            borderColor: "#7f7f7f",
            data: historyArr2
          };
          marksData.datasets.push(dataset);
        }
      }
      let chartOptions = {
        hover: {
          animationDuration: 0,
        },
        animation: {
          duration: 1,
          onComplete: function () {
            var chartInstance = this.chart,
              ctx = chartInstance.ctx;

            ctx.font = Chart.helpers.fontString(
              Chart.defaults.global.defaultFontSize,
              Chart.defaults.global.defaultFontStyle,
              Chart.defaults.global.defaultFontFamily
            );
            ctx.textAlign = "center";
            ctx.textBaseline = "bottom";

            this.data.datasets.forEach(function (dataset, i) {
              var meta = chartInstance.controller.getDatasetMeta(i);
              meta.data.forEach(function (bar, index) {
                var data = dataset.data[index];
                ctx.fillText(data, bar._model.x, bar._model.y - 5);
              });
            });
          },
        },
        legend: {
          display: true,
        },
        scales: {
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
                min: 0,
                max: parseInt(maxscore),
                fontSize: 10,
                stepSize: parseInt(scoreRange)
              },
              scaleLabel: {
                display: true,
                labelString: "得分數",
              },
            },
          ],
          xAxes: [
            {
              ticks: {
                fontSize: 12,
              },
            },
          ],
        },
        title: {
          display: true,
          text: "",
        },
      };
      this.html_chart = new Chart(ctx, {
        type: "bar",
        data: marksData,
        options: chartOptions,
        responsive: true,
        maintainAspectRatio: false,
      });
    },
    //id ,分數陣列  構面陣列  圖表名稱
    pie(ctx, data) {
      let subjectArr = [];
      let historyArr = []; //用歷史分數當畫面上的模擬分數 (因為只有圓環圖沒有兩個分數的比較)
      // let chartTitle = data.chartTitle;
      $.each(data.dimensions, function (key, value) {
        subjectArr.push(value.subjectArr);
        historyArr.push(value.historyArr);
      });

      var marksData = {
        labels: subjectArr,
        datasets: [
          {
            labels: ["red", "blue", "purple", "pink", "orange"],
            backgroundColor: ["red", "blue", "purple", "pink", "orange"],
            data: historyArr
          }
        ]
      };

       var chartOptions = {
        responsive: true,
        legend: {
          display: false
        },
        animation: {
          animateScale: true
        },
        title: {
          display: true,
          text: ""
        }
      };
      this.html_chart = new Chart(ctx, {
        type: "doughnut",
        data: marksData,
        options: chartOptions,
        responsive: true,
        maintainAspectRatio: false
      });
    },
    //id ,分數陣列  構面陣列  歷史分數陣列  圖表名稱 分數上限 分數間距
    radar(ctx, data) {
      let scoreArr = [];
      let subjectArr = [];
      let historyArr = [];
      let historyArr2 = [];
      // let chartTitle = data.chartTitle;
      let maxscore = data.maxscore;
      let scoreRange = data.scoreRange;
      $.each(data.dimensions, function(key, value) {
        subjectArr.push(value.subjectArr);
        historyArr.push(value.historyArr);
        historyArr2.push(value.historyArr2);
      });
      //給自己的分數 給予亂數
      $.each(data.dimensions, () => {
        let random_score = Math.floor(
          Math.random() * parseInt(this.maxscore) + 1
        );
        scoreArr.push(random_score);
      });
      var marksData = {
        labels: subjectArr,
        datasets: [
          {
            label: "我自己的分數",
            borderColor: "#ffb81c",
            data: scoreArr,
            fill: false
          }
        ]
      };
      if (historyArr.length > 0) {
        let dataset = {
          label: "產業平均分數",
          borderColor: "#00816d",
          data: historyArr,
          fill: false
        };
        marksData.datasets.push(dataset);
      }
      if (historyArr2.length > 0) {
        if(historyArr2[0] !== "0" && historyArr2[0] !== ""){
          let dataset = {
            label: "同業平均分數",
            borderColor: "#7f7f7f",
            data: historyArr2,
            fill: false
          };
          marksData.datasets.push(dataset);
        }
      }
      var chartOptions = {
        legend: {
          display: true
        },
        tooltips: {
          mode: "index",
          intersect: false
        },
        hover: {
          mode: "nearest",
          intersect: true
        },
        title: {
          display: true,
          text: ""
        },
        scales: {
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
                fontSize: 10,
                min: 0,
                max: parseInt(maxscore),
                stepSize: parseInt(scoreRange)
              },
              scaleLabel: {
                display: true,
                labelString: "得分數"
              }
            }
          ],
          xAxes: [
            {
              ticks: {
                fontSize: 12
              }
            }
          ]
        }
      };
      this.html_chart = new Chart(ctx, {
        type: "radar",
        data: marksData,
        options: chartOptions,
        responsive: true,
        maintainAspectRatio: false
      });
    },
    //id ,分數陣列  構面陣列  歷史分數陣列  圖表名稱 分數上限 分數間距
    line(ctx, data) {
      let scoreArr = [];
      let subjectArr = [];
      let historyArr = [];
      let historyArr2 = [];
      // let chartTitle = data.chartTitle;
      let maxscore = data.maxscore;
      let scoreRange = data.scoreRange;
      $.each(data.dimensions, function(key, value) {
        subjectArr.push(value.subjectArr);
        historyArr.push(value.historyArr);
        historyArr2.push(value.historyArr2);
      });
      //給自己的分數 給予亂數
      $.each(data.dimensions, () => {
        let random_score = Math.floor(
          Math.random() * parseInt(this.maxscore) + 1
        );
        scoreArr.push(random_score);
      });
      var marksData = {
        labels: subjectArr,
        datasets: [
          {
            label: "我自己的分數",
            borderColor: "#ffb81c",
            data: scoreArr,
            fill: false
          }
        ]
      };
      if (historyArr.length > 0) {
        let dataset = {
          label: "產業平均分數",
          backgroundColor: "#00816d",
          borderColor: "#00816d",
          data: historyArr
        };
        marksData.datasets.push(dataset);
      }
      if (historyArr2.length > 0) {
        if(historyArr2[0] !== "0" && historyArr2[0] !== ""){
          let dataset = {
            label: "同業平均分數",
            backgroundColor: "#7f7f7f",
            borderColor: "#7f7f7f",
            data: historyArr2
          };
          marksData.datasets.push(dataset);
        }
      }
      var chartOptions = {
        legend: {
          display: true
        },
        tooltips: {
          mode: "index",
          intersect: false
        },
        hover: {
          mode: "nearest",
          intersect: true
        },
        title: {
          display: true,
          text: ""
        },
        scales: {
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
                fontSize: 10,
                min: 0,
                max: parseInt(maxscore),
                stepSize: parseInt(scoreRange)
              },
              scaleLabel: {
                display: true,
                labelString: "得分數"
              }
            }
          ],
          xAxes: [
            {
              ticks: {
                fontSize: 12
              }
            }
          ]
        }
      };
      this.html_chart = new Chart(ctx, {
        type: "line",
        data: marksData,
        options: chartOptions,
        responsive: true,
        maintainAspectRatio: false
      });
    },
    //刪除構面
    delete_dimensions(rowItem, index) {
      this.$swal({
        title: `確定要刪除級此構面嗎?`,
        text: "(此刪除僅畫面中刪除，若確定要刪除請點擊右下角的儲存圖表設定)",
        icon: "question",
        showCancelButton: true,
        confirmButtonText: "確定",
        cancelButtonText: "取消"
      }).then(Response => {
        if (Response.value) {
          this.chart_forms.dimensions.splice(index, 1);
          this.html_chart.data.labels.splice(index, 1);
          this.html_chart.data.datasets[0].data.splice(index, 1);
          // 是圓環圖的時候 不能執行這段(沒有歷史分數)
          if (!this.pie_type) {
            this.html_chart.data.datasets[1].data.splice(index, 1);
          }
          this.html_chart.update();
        } else {
        }
      });
    }
  }
};
</script>


<style>
thead:first-child {
  text-align: center;
  font-size: 1.2vw;
}
th:first-of-type {
  border-top-left-radius: 0.5rem;
}
th:last-of-type {
  border-top-right-radius: 0.5rem;
}
.form-control-xxx {
  width: 60px;
  display: inline;
}
.delete-level {
  color: #dc3545;
}
</style>



