<template>
  <div class="col-lg-2">
    <label
      class="col-form-label"
      :for="columnName"
    >
      {{ columnDisplay }}
      <span
        v-if="!columnNullable"
        class="necessary"
      >
        *
      </span>
    </label>
  </div>
</template>

<script>
export default {
  props: {
    columnName: String,
    columnDisplay: String,
    columnNullable: Boolean,
  },
  created() {
  },
};
</script>

<style>
.necessary {
  color: red;
}
</style>



