import _ from 'lodash';
import { getCompanyInfo } from '../utils/business';
import { parseAD, parseRoc } from '../utils/lang';

const COMPANY_INFO_MAPPING = {
  company_cname: 'Company_Name',
  company_head: 'Responsible_Name',
  established: 'Company_Setup_Date',
  capital: 'Capital_Stock_Amount',
  // address: 'Company_Location',
};

/**
 * state 命名規則 follow，故可直接使用 `set_${columnName}`、`fetch_${column_name}`、`get_${column_name}` 來操作
 */
export default {
  namespaced: true,
  state: {
    company_num: '',
    company_cname: '',
    company_head: '',
    established: new Date(0),
    capital: 0,
    // address: '',
    industry_type_id: 0,
  },
  mutations: {
    set_company_num(state, payload) {
      const { company_num } = payload;
      console.log('set company num: ', company_num);
      state.company_num = company_num;
    },
    set_company_cname(state, payload) {
      const { company_cname } = payload;
      state.company_cname = company_cname;
    },
    set_company_head(state, payload) {
      const { company_head } = payload;
      state.company_head = company_head;
    },
    set_capital(state, payload) {
      const { capital } = payload;
      state.capital = capital;
    },
    /*
    set_address(state, payload) {
      const { address } = payload;
      state.address = address;
    },
     */
    set_industry_type_id(state, payload) {
      const { industry_type_id } = payload;
      state.industry_type_id = industry_type_id;
    },
    set_established(state, payload) {
      const { established } = payload;
      state.established = established;
    },
  },
  actions: {
    async fetch_company_info({ commit, state }, payload) {
      const { company_num } = state;
      const { modelUpdater } = payload;

      if (!!state) {
        const info = await getCompanyInfo(company_num);
        if (!!info) {
          _.forOwn(COMPANY_INFO_MAPPING, (fetchKey, stateKey) => {
            let fetchValue = info[fetchKey];

            if (!!fetchValue) {
              console.log(`set: ${stateKey} to ${fetchValue}`);

              if (stateKey === 'established') {
                fetchValue = parseAD(parseRoc(fetchValue));
              }

              commit(`set_${stateKey}`, {
                [stateKey]: fetchValue,
              });

              modelUpdater(stateKey, fetchValue);
            }
          });
        }
      }
    },
  },
  getters: {
    get_company_num: (state) => state.company_num,
    get_company_cname: (state) => state.company_cname,
    get_company_head: (state) => state.company_head,
    get_capital: (state) => state.capital,
    // get_address: (state) => state.address,
    get_industry_type_id: (state) => state.industry_type_id,
    get_established: (state) => state.established,
  },
};



