<template>
  <div class="form-group row">
    <dyna-title
      :columnDisplay="columnDisplay"
      :columnName="columnName"
      :columnNullable="columnNullable"
    />
    <div class="form-inline col-lg-10">
      <input
        type="number"
        class="number"
        :id="columnName"
        :name="columnName"
        :size="columnLength"
        :placeholder="columnPlaceholder"
        v-model="columnValue"
      />
      &nbsp;
      <dyna-verifier
        :columnDisplay="columnDisplay"
        :verifier="verifier"
        :needVerify="needVerify"
        :valueGetter="getValue"
      />
      &nbsp;
      <dyna-helper
        :helper="helper"
        :callback="callback"
      />
    </div>
  </div>
</template>

<script>
import DynaVerifier from "../DynaVerifier/index.vue";
import DynaTitle from "../DynaTitle/index.vue";
import DynaHelper from "../DynaHelper/index.vue";

import { getEchoMan, getTrueMan } from "../../utils/lang";
import { getDefaultHelper } from "../../utils/form";

export default {
  props: {
    columnSpec: Object,
    formName: String,
  },
  components: { DynaVerifier, DynaTitle, DynaHelper },
  data() {
    return {
      columnName: '',
      columnDisplay: '',
      columnComponent: '',
      columnLength: '',
      columnNullable: '',
      columnDataType: 'int',
      columnPlaceholder: '',
      columnValue_: null,

      needVerify: false,
      needTransform: false,

      verifier: getTrueMan(),
      transformer: getEchoMan(),
      updater: getEchoMan(),
      helper: getDefaultHelper(),
    };
  },
  computed: {
    columnValue: {
      get() {
        return !!this.columnValue_
          ? this.columnValue_
          : this.columnSpec.columnValue;
      },
      set(val) {
        this.columnValue_ = val;
        this.updater(val);
        this.$emit('updated', val);
      },
    },
  },
  created() {
    const columnSpec = this.columnSpec;

    this.columnName = columnSpec.columnName;
    this.columnDisplay = columnSpec.columnDisplay;
    this.columnComponent = columnSpec.columnComponent;
    this.columnDataType = columnSpec.columnDataType;
    this.columnLength = columnSpec.columnLength;
    this.columnNullable = columnSpec.columnNullable;
    this.columnPlaceholder = columnSpec.columnPlaceholder;
    this.columnValue_ = columnSpec.columnValue;

    this.needVerify = columnSpec.needVerify;
    this.needTransform = columnSpec.needTransform;

    this.verifier = columnSpec.verifier;
    this.transformer = columnSpec.transformer;
    this.updater = columnSpec.updater;
    this.helper = columnSpec.helper;
  },
  methods: {
    getValue() {
      return this.columnValue;
    },
    callback(value) {
      this.columnValue = this.parseValue(value);
    },
    parseValue(value) {
      if (this.columnDataType === 'int' || this.columnDataType === 'money') {
        return parseInt(value);
      } else {
        return parseFloat(value);
      }
    },
  },
};
</script>

<style>
.necessary {
  color: red;
}

.number {
  width: 300px;
}
</style>



