<template>
  <div class="container-fluid pb-3">
    <div class="row">
      <div class="col-lg-8 col-lg-offset-2">
        <h2 class="title">{{ displayName }}</h2>
      </div>
    </div>
    <hr />
    <div class="row">
      <div class="col-12 form-inline">
        <input
          class="form-control query"
          id="query"
          name="query"
          v-model="formContentQuery"
        />
        &nbsp;
        <a href="#" @click="search">
          <button class="btn btn-success">查詢</button>
        </a>
        &nbsp;
        <router-link :to="`${formRoute}?id=-1`" replace>
          <button class="btn btn-primary">新增</button>
        </router-link>
      </div>
    </div>
    <div class="row">
      <table class="table table-bordered">
        <thead>
        <tr>
          <th scope="col" v-for="spec in listSpec" :key="spec.key">
            {{ spec.display }}
          </th>
          <th scope="col">&nbsp;</th>
        </tr>
        </thead>
        <tbody>
        <tr
          v-for="item in formContentShow"
          :key="item.id"
        >
          <th :scope="getRowScope(spec.key)" v-for="spec in listSpec" :key="spec.key">{{ item[spec.key] }}</th>
          <td>
            <router-link :to="`${formRoute}?id=${item.id}`" replace>
              <button class="btn btn-primary">編輯</button>
            </router-link>
            <button class="btn btn-success">瀏覽</button>
            <a @click="deleteItem(item.id)">
              <button class="btn btn-danger">刪除</button>
            </a>
          </td>
        </tr>
        </tbody>
      </table>
    </div>
    <div class="row">
      <nav aria-label="Page navigation example">
        <ul class="pagination justify-content-center">
          <li class="page-item">
            <a
              class="page-link"
              href="#"
              aria-label="Previous"
              v-if="listPage > 1"
              @click="showPreviousPage()"
            >
              <span aria-hidden="true">&laquo;</span>
            </a>
            <span
              class="page-link"
              aria-label="Previous"
              aria-hidden="true"
              v-else
            >
              &laquo;
            </span>
          </li>
          <li
            class="page-item"
            v-for="index in totalPages"
            :key="index"
          >
            <a
              class="page-link"
              href="#"
              v-if="listPage !== index"
              @click="showPage(index)"
            >
              {{ index }}
            </a>
            <span
              class="page-link"
              aria-hidden="true"
              v-else
            >
              {{ index }}
            </span>
          </li>
          <li class="page-item">
            <a
              class="page-link"
              href="#"
              aria-label="Next"
              v-if="listPage < totalPages"
              @click="showNextPage()"
            >
              <span aria-hidden="true">&raquo;</span>
            </a>
            <span
              class="page-link"
              aria-label="Next"
              aria-hidden="true"
              v-else
            >
              &raquo;
            </span>
          </li>
        </ul>
      </nav>
    </div>
  </div>
</template>

<script>
import _ from "lodash";
import $ from "jquery";

import {
  deleteContentById,
  errorHandler,
  fetchFormContentCountPromise,
  fetchFormContentListPromise,
  fetchListSpecPromise,
  getFormNameByKey
} from "../../utils/api";

import { PAGE_SIZE } from "../../utils/form";

const VM = {
  props: {
    editPage: String,
    formKey: String,
  },
  data() {
    return {
      componentKey: 1,
      formRoute: '',
      formName: "",
      formContentCount: 0,
      formContentList: [],
      formContentShow: [],
      listSpec: [],
      displayName: "",
      totalPages: 1,
      listPage: 1,
      formContentQuery: ""
    };
  },
  mounted() {
    $("#wrapper").addClass("toggled");
  },
  created() {
    this.formName = getFormNameByKey(this.formKey);
    this.formRoute = this.editPage;
    this.fetchListSpec();
  },
  methods: {
    fetchListSpec() {
      const self = this;

      fetchListSpecPromise(this.formName, (resp) => {
        const { data } = resp;
        const { display, columns } = data;

        self.listSpec = columns;
        self.displayName = display;

        self.fetchFormContents("");
      }, errorHandler);
    },
    fetchFormContents(query) {
      const self = this;

      const listCallback = (list) => {
        self.listPage = 1;
        const start = 0;
        self.formContentShow = list.slice(start, PAGE_SIZE);
      };

      const countCallback = (count) => {
        self.calculatePages(count);

        fetchFormContentListPromise(
          self.formName,
          query,
          _.partial(handleList, _, self.updateFormContentList, listCallback),
          errorHandler
        );
      };

      fetchFormContentCountPromise(
        self.formName,
        query,
        _.partial(handleCount, _, self.updateFormContentCount, countCallback),
        errorHandler
      );
    },
    getRowScope(key) {
      return (key === "id") ? "row" : "";
    },
    search() {
      this.fetchFormContents(this.formContentQuery);
    },
    updateFormContentCount(count) {
      this.formContentCount = count;
    },
    updateFormContentList(list) {
      this.formContentList = list;
    },
    updateTotalPages(pageCount) {
      this.totalPages = pageCount;
    },
    calculatePages(formContentCount) {
      const pageCount =
        formContentCount % PAGE_SIZE === 0
          ? formContentCount / PAGE_SIZE
          : parseInt((formContentCount / PAGE_SIZE).toFixed(0)) + 1;

      this.updateTotalPages(pageCount);
    },
    showPage(page) {
      // console.log("show page: ", page);
      const start = (page - 1) * PAGE_SIZE;
      const end = page * PAGE_SIZE;

      if (this.formContentList.length >= start) {
        this.formContentShow = this.formContentList.slice(start, end);
        this.listPage = page;
      }
    },
    showPreviousPage() {
      if (this.listPage > 1) {
        const page = this.listPage - 1;
        this.showPage(page);
      }
    },
    showNextPage() {
      if (this.listPage < this.totalPages) {
        const page = this.listPage + 1;
        this.showPage(page);
      }
    },
    deleteItem(id) {
      const self = this;
      self
        .$swal({
          icon: "warning",
          title: "確認刪除本筆？",
          showDenyButton: true,
          showCancelButton: true,
          confirmButtonText: "Yes",
          denyButtonText: "No"
        })
        .then((result) => {
          if (result.isConfirmed) {
            deleteContentById(
              self.formName,
              id,
              (resp) => {
                const { data } = resp;
                const { result, count, message } = data;

                if (result && count > 0) {
                  self.$swal({
                    icon: "success",
                    title: "OK!"
                  });

                  self.fetchFormContents();
                } else {
                  self.$swal({
                    icon: "error",
                    title: "Oops...",
                    text: message
                  });
                }
              },
              errorHandler
            );
          }
        });
    }
  }
};

const handleCount = (resp, updater, callback) => {
  const { data } = resp;

  if (_.isEmpty(data)) {
    throw "系統性錯誤，請確認後端查詢參數";
  }

  const { count } = data;

  console.log("fetched count: ", count);

  updater(parseInt(count));
  callback(parseInt(count));
};

const handleList = (resp, updater, callback) => {
  const { data } = resp;

  console.log("fetched list: ", data);

  if (_.isEmpty(data)) {
    console.log("empty data");
    updater([]);
    callback([]);
  } else {
    console.log("data fetched");
    updater(data);
    callback(data);
  }
};

export default VM;
</script>

<style scoped>
.dyna {
  font-size: 1.5em;
}

.query {
  width: 240px !important;
}
</style>



