<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-lg-8 col-lg-offset-2">
        <h2 class="title">{{ group_name }}-權限設定</h2>
      </div>
    </div>
    <hr />

    <table class="table table-bordered table-striped">
      <thead></thead>
      <tbody>
        <div class="form-group col-sm-6">
          <label>名稱</label>
          <input name="title" type="text" v-model="new_group_name" required class="form-control" />
        </div>
        <div class="form-group col-sm-6"></div>
        <div class="col-sm-12">
          <div class="form-inline">
            <label id="dyna_list" class="form-control margin_label">
              <input type="checkbox" value="DynaFormList,list" true-value="1" false-value="0" v-model="dyna" />
              DynaFormList
            </label>

            <label class="form-control margin_label">
              <input type="checkbox" value="回答紀錄,review" v-model="form" />
              回答紀錄
            </label>
            <label class="form-control margin_label">
              <input type="checkbox" value="製作問卷,design" v-model="form" />
              製作問卷
            </label>
            <label class="form-control margin_label">
              <input type="checkbox" value="圖表設定,chart" v-model="form" />
              圖表設定
            </label>

            <label class="form-control margin_label">
              <input type="checkbox" value="管理員帳號管理,admin_account" v-model="sys" />
              管理員帳號管理
            </label>
            <label class="form-control margin_label">
              <input type="checkbox" value="使用者帳號管理,user_account" v-model="sys" />
              使用者帳號管理
            </label>
            <label class="form-control margin_label">
              <input type="checkbox" value="管理員操作紀錄,admin_log" v-model="sys" />
              管理員操作紀錄
            </label>
            <label class="form-control margin_label">
              <input type="checkbox" value="使用者操作紀錄,user_log" v-model="sys" />
              使用者操作紀錄
            </label>
            <label id="group_permit" class="form-control margin_label">
              <input type="checkbox" value="群組權限設定,group_permit" v-model="sys" />
              群組權限設定
            </label>
            <!--
            <label id="group_permit" class="form-control margin_label">
              <input type="checkbox" value="apptracker查詢,apptracker" v-model="app" />
              apptracker查詢
            </label>
            -->
            <label id="all_form" class="form-control margin_label">
              <input type="checkbox" true-value="1" false-value="0" v-model="all_form" />
              可看到全部問卷
            </label>

          </div>
        </div>
        <br />
        <div style="margin: 0px 0px 15px 15px">
          <button type="submit" class="btn btn-primary" @click="update_permit()">
            儲存
          </button>
        </div>
      </tbody>
    </table>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      new_group_name: this.$route.params.group_name,
      group_name: this.$route.params.group_name,
      form: [],
      sys: [],
      // user: [],
      // log: [],
      // permit: [],
      // app: [],
      dyna: [],
      all_form: this.$route.params.all_form,
      booleanValue: true,
    };
  },
  mounted() {
    $("#wrapper").addClass("toggled");
  },
  created() {
    this.get_data();
  },
  methods: {
    //一進來取表單資料
    get_data() {
      this.booleanValue = true;
      this.axios
        .post(
          "/rise_api/public/index.php/permit/get_petmit_sidebar",
          {
            authority: this.new_group_name,
          }
        )
        .then((response) => {
          const data = response.data;
          //問卷設計
          data.form.name.forEach((name, index) => {
            var url = data.form.url[index];
            // 塞回到 vue 的 form
            this.form.push(`${name},${url}`);
            // this.form.push(name + "," + url);
          });
          //系統管理
          data.sys.name.forEach((name, index) => {
            var url = data.sys.url[index];
            // 塞回到 vue 的 sys
            this.sys.push(`${name},${url}`);
          });

          /*
          //帳號管理
          data.user.name.forEach((name, index) => {
            var url = data.user.url[index];
            this.user.push(`${name},${url}`);
          });
          //系統紀錄
          data.log.name.forEach((name, index) => {
            var url = data.log.url[index];
            this.log.push(`${name},${url}`);
          });
          //權限管理
          data.permit.name.forEach((name, index) => {
            var url = data.permit.url[index];
            this.permit.push(`${name},${url}`);
          });
          //apptracker查詢
          data.app.name.forEach((name, index) => {
            var url = data.app.url[index];
            this.app.push(`${name},${url}`);
          });
          */

          //dyna
          data.dyna.name.forEach((name, index) => {
            var url = data.dyna.url[index];
            this.dyna.push(`${name},${url}`);
          });
        });
      //不給修改admin_permit的權限設定功能(避免被關掉 就沒人可以使用了)
      let group_name = location.href.split("group_permit/");
      this.axios
        .post(
          "/rise_api/public/index.php/design/check_permit",
          {
            group_name: group_name[1],
          }
        )
        .then((response) => {
          if (response.data == 1) {
            document.getElementById("group_permit").hidden = true;
          }
        });
    },
    // 修改權限設定
    update_permit() {
      this.$swal({
        text: "確定要儲存嗎?",
        icon: "question",
        showCancelButton: true,
        confirmButtonText: "確定",
        cancelButtonText: "取消",
        preConfirm: () => {
          //問卷設計
          var form_data_url = [];
          var form_data_name = [];
          $.each(this.form, function (key, value) {
            if (value) {
              let name_url = value.split(",");
              form_data_name.push(name_url[0]);
              form_data_url.push(name_url[1]);
            }
          });

          //系統管理
          var sys_data_url = [];
          var sys_data_name = [];
          $.each(this.sys, function (key, value) {
            let name_url = value.split(",");
            sys_data_name.push(name_url[0]);
            sys_data_url.push(name_url[1]);
          });

          /*
          //帳號管理
          var user_data_url = [];
          var user_data_name = [];
          $.each(this.user, function (key, value) {
            let name_url = value.split(",");
            user_data_name.push(name_url[0]);
            user_data_url.push(name_url[1]);
          });

          //系統紀錄
          var log_data_url = [];
          var log_data_name = [];
          $.each(this.log, function (key, value) {
            let name_url = value.split(",");
            log_data_name.push(name_url[0]);
            log_data_url.push(name_url[1]);
          });

          //權限管理
          var permit_data_url = [];
          var permit_data_name = [];
          $.each(this.permit, function (key, value) {
            let name_url = value.split(",");
            permit_data_name.push(name_url[0]);
            permit_data_url.push(name_url[1]);
          });

          //apptracker
          var app_data_url = [];
          var app_data_name = [];
          $.each(this.app, function (key, value) {
            let name_url = value.split(",");
            app_data_name.push(name_url[0]);
            app_data_url.push(name_url[1]);
          });
          */

          //dyna
          var dyna_data_url = [];
          var dyna_data_name = [];
          $.each(this.dyna, function (key, value) {
            if (value) {
              let name_url = value.split(",");
              dyna_data_name.push(name_url[0]);
              dyna_data_url.push(name_url[1]);
            }
          });

          var test_data = {
            form: {
              name: form_data_name,
              url: form_data_url,
            },
            sys: {
              name: sys_data_name,
              url: sys_data_url,
            },
            /*
            user: {
              name: user_data_name,
              url: user_data_url,
            },
            log: {
              name: log_data_name,
              url: log_data_url,
            },
            permit: {
              name: permit_data_name,
              url: permit_data_url,
            },
            app: {
              name: app_data_name,
              url: app_data_url,
            },
            */
            dyna: {
              name: dyna_data_name,
              url: dyna_data_url,
            }
          };
          this.axios
            .put(
              "/rise_api/public/index.php/permit/update_permit",
              {
                group_name: this.group_name,
                permit: test_data,
                update_username: localStorage.username,
                new_group_name: this.new_group_name,
                all_form: this.all_form,
              }
            )
            .then((response) => {
              //應該是改網址才會遇到
              if (response.data == "") {
                this.$swal
                  .fire({
                    icon: "error",
                    title: "非法修改",
                    text: "非法修改，請確認是否有此頁面的權限",
                    confirmButtonText: "返回製作問卷頁面",
                  })
                  .then(() => {
                    this.$router.push("/management/admin/design");
                  });
              }
              //改現在登入的權限名稱
              else if (
                localStorage.authority != this.new_group_name &&
                localStorage.authority == this.group_name
              ) {
                this.$swal
                  .fire({
                    icon: "success",
                    title: "權限修改完成",
                    text: "權限修改完成，請重新登入",
                    confirmButtonText: "返回登入頁面",
                  })
                  .then(() => {
                    //先轉址
                    this.$router.push("/management/login");
                  });
              } else {
                this.$swal({
                  icon: "success",
                  title: "修改完成",
                  showConfirmButton: false,
                  timer: 1500,
                }).then(() => {
                  // localStorage.setItem("authority", this.new_group_name);
                  //先轉址
                  //this.$router.push({ path: this.new_group_name });
                  this.$router.push("/management/admin/group_permit");
                  //再重整
                  //this.$router.go();
                });
              }
            });
        },
      });
    },
  },
};
</script>

<style>
.margin_label {
  margin-right: 8px;
  margin-top: 7px;
}
</style>






