<template>
  <div class="container-fluid answer-container">
    <div class="row">
      <div class="col-lg-8 col-lg-offset-2">
        <h2 class="title">回答紀錄</h2>
        <h7 class="title">若沒選時間則匯出當月資料，MAX:700 </h7>
      </div>
    </div>
    <hr />
    <div class="row">
      <div class="col-3">
        <!-- 用v-model綁定value給change用 -->
        <select
          class="browser-default custom-select"
          @change="get_version()"
          v-model="chooseValue"
        >
          <option
            disabled
            value
          >
            請選擇表單名稱
          </option>
          <option
            v-for="n in forms"
            :value="n.form_id"
          >
            {{ n.form_name }}
          </option>
        </select>
      </div>
      <div
        class="col-1"
        style="padding-left: 0px"
      >
        <!-- 不透過v-model綁定value 在change用$event -->
        <select
          class="browser-default custom-select"
          @change="get_answer($event)"
        >
          <option
            v-for="n in version_forms"
            :value="n.form_id"
          >
            {{ n.form_version }}
          </option>
        </select>
      </div>
      <div class="col-1">
        <select
          class="browser-default custom-select"
          v-model="chooseValueYear"
          @change="get_month($event)"
        >
          <template v-if="chooseValue != ''">
            <option
              v-for="n in year"
              :value="n.year"
            >
              {{ n.year }}
            </option>
          </template>
        </select>
      </div>
      <div class="col-1">
        <select
          class="browser-default custom-select"
          @change="get_answer_month($event)"
          v-model="chooseValueMonth"
        >
          <option
            v-for="m in month"
            :value="m.month"
          >
            {{ m.month }}
          </option>
        </select>
      </div>
      <div
        class="col-2"
        v-if="show_type === true"
      >
        <input
          type="text"
          class="form-control"
          v-model="username"
          placeholder="請輸入填寫人名稱"
        />
      </div>
      <div
        class="col-2"
        v-if="show_type === true"
      >
        <input
          type="text"
          class="form-control"
          v-model="companyname"
          placeholder="請輸入帳號公司名稱"
        />
      </div>
      <div
        class="col-2"
        v-if="show_type === true"
      >
        <input
          type="text"
          class="form-control"
          v-model="companynum"
          placeholder="請輸入帳號統一編號"
        />
      </div>
    </div>

    <!-- 版本選全部的時候無法匯出(因為題目跟答案會不一樣 沒有辦法對應) -->
    <div class="row">
      <div
        class="col-3"
        v-if="show_type === true"
      >
        <img
          class="myMOUSE2"
          src="../../assets/1595466944315.jpg"
          style="width: 160px"
          @click="export_excel()"
        />
      </div>
    </div>

    <div
      class="row"
      id="answer-table"
    >
      <div class="col-12">
        <table
          id="tableId"
          v-if="show_type === true"
          class="table table-bordered table-striped over_css"
        >
          <thead>
            <tr>
              <th>
                <font size="2.5">問卷公司名稱</font>
              </th>
              <th>
                <font size="2.5">問卷統一編號</font>
              </th>
              <th>
                <font size="2.5">填寫人</font>
              </th>
              <th>
                <font size="2.5">時間</font>
              </th>
              <th>
                <font size="2.5">帳號公司名稱</font>
              </th>
              <th>
                <font size="2.5">總評</font>
              </th>
              <th>
                <font size="2.5">總分</font>
              </th>
              <th>
                <font size="2.5">帳號統一編號</font>
              </th>
              <th>
                <font size="2.5">員工人數</font>
              </th>
              <th>
                <font size="2.5">產業類別</font>
              </th>
              <th v-for="form in dimensions[0]">
                <font size="2.5">{{ form }}</font>
              </th>
              <th>
                <font size="2.5">問卷結果</font>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="form in filterSearch">
              <td>{{ form.form_companyName }}</td>
              <td>{{ form.form_companyNum }}</td>
              <td>{{ form.form_name }}</td>
              <td>{{ form.form_time }}</td>
              <td>{{ form.form_cname }}</td>
              <td>{{ form.form_final_score }}</td>
              <td>{{ form.form_final_score_2 }}</td>

              <td>{{ form.form_cnum }}</td>
              <td>{{ form.form_workers }}</td>
              <td>{{ form.form_industry }}</td>
              <td v-for="form2 in dimensions[0]">
                <p>{{ form.form_score[form2].score }}</p>
              </td>
              <td>
                <a
                  :href="get_answer_page(form.form_eid)"
                  target="_blank"
                  >{{ get_answer_page(form.form_eid) }}</a
                >
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import _ from 'lodash';
import $ from 'jquery';
import xlsx from 'xlsx';
import fileSaver from 'file-saver';

export default {
  data() {
    return {
      forms: [],
      version_forms: [],
      version: '',
      chooseValue: '',
      answer_form: [],
      dimensions: [],
      show_type: false,
      username: '',
      companyname: '',
      companynum: '',
      export_data: [],
      version_data: '',
      month: [],
      year: [],
      chooseValueYear: '',
      chooseValueMonth: '',
    };
  },
  mounted() {
    $('#wrapper').addClass('toggled');
  },
  created() {
    this.getFormList();
  },
  computed: {
    filterSearch() {
      var username = _.trim(this.username);
      var companyName = _.trim(this.companyname);
      var companyNum = _.trim(this.companynum);
      // var answer_form = this.answer_form;
      var result_form = this.answer_form;

      // 如果要用match的搜尋 可以參考這邊 型別為ture 或 false
      // console.log(!!answer_form[0].form_name.match("aa"));
      // console.log(!!answer_form[1].form_name.match("aa"));

      // indexOf用在陣列裡面
      // return username.trim() !== ""
      //   ? answer_form.filter(function(d) {
      //       return d.form_name.indexOf("test") > -1;
      //     })
      //   : answer_form;

      if (!!username) {
        result_form = result_form.filter(
          (d) => d.form_name.indexOf(username) > -1
        );
      }

      if (!!companyName) {
        result_form = result_form.filter(
          (d) => d.form_cname.indexOf(companyName) > -1
        );
      }

      if (!!companyNum) {
        result_form = result_form.filter(
          (d) => d.form_cnum.indexOf(companyNum) > -1
        );
      }

      /*
      return username.trim() !== ''
        ? answer_form.filter(function (d) {
            if (d.form_name.indexOf(username) === 0) {
              return username;
            }
          })
        : answer_form;
      */

      return result_form;
    },
  },
  methods: {
    //一進來取表單資料
    getFormList() {
      var forms = this.forms;

      this.axios
        .post('/rise_api/public/index.php/review/getForm', {
          create_name: localStorage.username,
          group_name: localStorage.authority,
        })
        .then((response) => {
          //代表不是admin
          if (response.data[0].sta != 1) {
            $.each(response.data[0].api_data, function (key, value) {
              let edit_user_json = JSON.parse(value.edit_user);
              // 是創建者或在可編輯者中
              if (
                value.create_name == localStorage.username ||
                edit_user_json[0].indexOf(localStorage.username) > -1
              ) {
                forms.push({
                  form_id: value.id,
                  form_name: value.name,
                  report_type: value.report_type,
                });
              }
            });
          } else {
            $.each(response.data[0].api_data, function (key, value) {
              forms.push({
                form_id: value.id,
                form_name: value.name,
                report_type: value.report_type,
              });
            });
          }
        });
    },
    sortBy: function (sortKey) {
      this.reverse = this.sortKey == sortKey ? !this.reverse : false;
      this.sortKey = sortKey;
    },
    get_version() {
      this.version_forms = [];
      this.year = [];
      var version_forms = this.version_forms;
      var chooseValue = this.chooseValue;
      var year = this.year;

      this.axios
        .post('/rise_api/public/index.php/review/getForm_version', {
          form_id: chooseValue,
        })
        .then((response) => {
          version_forms.push({
            form_version: '請選擇表單版本',
            form_id: '請選擇表單版本',
          });

          this.show_type = false;
          // version_forms.push({ form_version: "All", form_id: chooseValue }); //因為構面可能不同 無法做出表格
          $.each(response.data, function (key, value) {
            version_forms.push({
              form_id: chooseValue,
              form_version: `V${value.version}`,
            });
          });

          year.push({
            year: '請選擇年份',
          });
          var y = 2020;
          for (var i = 1; i <= 6; i++) {
            year.push({
              year: y.toString(),
            });
            y = y + 1;
          }
        });
    },
    get_answer(rowItem) {
      // console.log(rowItem);
      this.answer_form = [];
      var answer_form = this.answer_form;
      this.dimensions = [];
      var dimensions = this.dimensions;
      var form_version = rowItem.target[rowItem.target.selectedIndex].text;

      //因為構面可能不同 無法做出表格 this.version_data =
      //因為構面可能不同 無法做出表格   form_version == "All" ? "All" : form_version.substr(1);
      this.version_data = form_version.substr(1);
      var vm = this;
      this.axios
        .post('/rise_api/public/index.php/review/getForm_answer', {
          form_id: rowItem.target.value,
          form_version: this.version_data,
        })
        .then((response) => {
          $.each(response.data['0'].result, function (key, value) {
            if (
              response.data['0'].result[key].score.length !== 0 &&
              key === 0
            ) {
              dimensions.push(
                Object.keys(response.data['0'].result[key].score)
              );
            }
            answer_form.push({
              form_id: value.id,
              form_eid: value.eid,
              form_name: value.name,
              form_time: value.time,
              form_cname: value.cname,
              form_companyName: value.companyName,
              form_final_score: '', //總評
              form_final_score_2: 0,
              form_cnum: value.cnum,
              form_companyNum: value.companyNum,
              form_workers: value.workers,
              form_industry: value.industry,
              form_score: value.score, //各個構面分數
              form_answer: value.answer, //回答內容
            });
          });

          let final_score = JSON.parse(response.data['0'].result2.final_score);
          let test_str = '';
          //並非所有分數都會被加入總分
          //拿有納入總分的執行each 比對後把分數加總 再看落在哪一個區間內
          $.each(answer_form, (key, value) => {
            let score = 0;
            $.each(final_score.dimensions, (key2, value2) => {
              if (
                value.form_score[value2] != null &&
                value.form_score[value2] !== undefined
              ) {
                score += value.form_score[value2].score;
              } else {
                value.form_score[value2] = { des: '', score: 0 };
                score = 0;
              }
            });
            $.each(dimensions[0], (key2, value2) => {
              if (
                value.form_score[value2] != null &&
                value.form_score[value2] !== undefined
              ) {
              } else {
                value.form_score[value2] = { des: '', score: 0 };
                score = 0;
              }
            });
            value.form_final_score_2 = score;
            $.each(final_score.level, (key3, val) => {
              if (score >= val.min && score <= val.max) {
                answer_form[key].form_final_score = val.des;
              } else {
              }
            });
          });
          vm.show_type = true;
        });
      console.log(dimensions);
      console.log(answer_form);
    },
    get_answer_page(id) {
      if (!!this.forms) {
        const selected = this.forms.find(
          (form) => form.form_id === this.chooseValue
        );

        if (!!selected) {

          const form_report_type = selected.report_type;
          // const answer_view_page = form_report_type === '1' ? 'answer_view_1' : 'answer_view_2';
          const answer_view_page = form_report_type === '1' ? 'answer_report_1' : 'answer_report_2';
          
          // const answer_page = `http://localhost:8080/#/view/${answer_view_page}/${id}`;

          const answer_page = `https://rise.iii.org.tw/rise_front/#/view/${answer_view_page}/${id}`;

          return answer_page;
        }
      }

      return '#';
    },
    export_excel() {
      var self = this;
      var form_name = '';
      $.each(this.forms, (key, val) => {
        if (val['form_id'] == this.chooseValue) {
          form_name = val['form_name'];
        }
      });

      var date_now = new Date();
      var month = date_now.getMonth() + 1;
      var da = date_now.getFullYear() + '-' + month + '-' + date_now.getDate();

      var version = this.version_data;
      var month_start = '';
      var month_end = '';

      if (!!this.chooseValueYear && !!this.chooseValueMonth) {
        month_start =
          this.chooseValueYear + '-' + this.chooseValueMonth + '-01';
        month_end = this.chooseValueYear + '-' + this.chooseValueMonth + '-31';
        console.log('2');
      } else if (!!this.chooseValueYear) {
        month_start = this.chooseValueYear + '-01-01';
        month_end = this.chooseValueYear + '-12-31';
        console.log('3');
      } else {
        // month_start=date_now.getFullYear() +"-" +month + "-01";
        // month_end=date_now.getFullYear() +"-" +month + "-31";
        month_start = 'N';
        month_end = 'N';

        console.log('1');
      }

      console.log(month_start);
      console.log(month_end);

      // 欄位說明
      var column_0 = [];
      // 回答紀錄
      var answer_0 = [];

      //取欄位說明 (題目內容)this.version_data
      this.axios
        .get(
          `/rise_api/public/index.php/form_question/getForm/${this.chooseValue}/${version}`
        )
        .then((response) => {
          let a = response.data.questions;
          $.each(a, function (key, value) {
            //判斷title有沒有值 (有的話表示是題組)
            let is_group = value.title == undefined ? value.name : value.title;
            column_0.push({
              題號: `Q${key + 1}`,
              題目名稱: is_group,
            });
            // 是選擇題或多選題的話 將內容選項內容解析
            if (value.type == 'choice' || value.type == 'selection') {
              $.each(value.choiceList, function (key_2, value_2) {
                column_0.push({
                  選項: value.choiceList[key_2].title,
                });
              });
            }
            // 換行用
            column_0.push({
              題號: '',
            });
          });

          const username = _.trim(this.username);
          const companyName = _.trim(this.companyname);
          const companyNum = _.trim(this.companynum);
          const resultFilter = (result) => {
            let resultForm = result;

            if (!!username) {
              resultForm = resultForm.filter(
                (d) => d.name.indexOf(username) > -1
              );
            }

            if (!!companyName) {
              resultForm = resultForm.filter(
                (d) => d.cname.indexOf(companyName) > -1
              );
            }

            if (!!companyNum) {
              resultForm = resultForm.filter(
                (d) => d.cnum.indexOf(companyNum) > -1
              );
            }

            return resultForm;
          };

          // 取回答紀錄
          this.axios
            .post('/rise_api/public/index.php/review/getForm_answer2', {
              form_id: this.chooseValue,
              form_version: version,
              month_start: month_start,
              month_end: month_end,
            })
            .then((response) => {
              console.log(response);

              const filteredResult = resultFilter(response.data[0].result);

              $.each(filteredResult, function (nu, val) {
                // console.log(val.time);
                answer_0.push({
                  帳號公司名稱: val.cname,
                  帳號統一編號: val.cnum,
                  公司名稱: val.companyName,
                  統一編號: val.companyNum,
                  填寫時間: val.time,
                });
                $.each(val.answer, function (nu2, val2) {
                  // 取結構裡面的key
                  let num = Object.keys(val2)[0];
                  // 把key帶入取結構內容
                  let val3 = val2[num];
                  // 百分比題目 or  優先序
                  if (val3.type == 'percentage' || val3.type == 'priority') {
                    let type_name =
                      val3.type == 'percentage' ? '_百分比_' : '_優先_';
                    for (let i = 0; i < val3.count; i++) {
                      answer_0[nu][num + type_name + (i + 1)] = val3.ans[i];
                    }
                  }
                  // 多選題
                  else if (val3.type == 'selection') {
                    // 先給值 不然非必填的話 可能會跳號
                    answer_0[nu][num + '_多選'] = '';
                    // 有內容的話就會填入內容
                    $.each(val3.ans, function (pronum, proans) {
                      answer_0[nu][num + '_多選'] += `${proans},`;
                    });
                    let answer_0_str = answer_0[nu][num + '_多選'];
                    // 移除掉最後的逗點
                    answer_0[nu][num + '_多選'] = answer_0_str.substring(
                      0,
                      answer_0_str.length - 1
                    );
                  }
                  // 其他題目
                  else {
                    answer_0[nu][num + '_答案'] = val3.ans[0];
                  }

                  // 多選跟單選的其他值 (前台如果有其他 就不會是undefined  )
                  if (val3.message != undefined) {
                    answer_0[nu][num + '_其他'] =
                      val3.message != '' ? val3.message : '';
                  }
                });
              });

              var column = xlsx.utils.json_to_sheet(column_0);
              var answer = xlsx.utils.json_to_sheet(answer_0);
              // 把h5的表包進xlsx的function
              var dimension = xlsx.utils.table_to_sheet($('#tableId')[0]);

              const wb2 = xlsx.utils.book_new();
              // 新增sheet
              xlsx.utils.book_append_sheet(wb2, answer, '問卷答案');
              xlsx.utils.book_append_sheet(wb2, column, '問卷題目');
              xlsx.utils.book_append_sheet(wb2, dimension, '總評');
              // 存檔
              xlsx.writeFile(wb2, da + '_' + form_name + '.xlsx');
            });
        });
    },
    get_month(rowItem) {
      this.month = [];
      var month = this.month;
      if (this.chooseValueYear != '') {
        month.push({
          month: '請選擇月份',
        });
        for (var i = 1; i <= 12; i++) {
          // console.log(i.toString());
          month.push({
            month: i.toString(),
          });
        }
      }
      this.get_answer_month(rowItem);
    },
    get_answer_month(rowItem) {
      var month = rowItem.target[rowItem.target.selectedIndex].text;
      var version = this.version_data;
      var month_start = '';
      var month_end = '';
      if (this.chooseValueYear != '' && this.chooseValueMonth == '') {
        month_start = this.chooseValueYear + '-01-01';
        month_end = this.chooseValueYear + '-12-31';
      } else {
        month_start = this.chooseValueYear + '-' + month + '-01';
        month_end = this.chooseValueYear + '-' + month + '-31';
      }

      this.answer_form = [];
      var answer_form = this.answer_form;
      this.dimensions = [];
      var dimensions = this.dimensions;
      var form_version = rowItem.target[rowItem.target.selectedIndex].text;
      //因為構面可能不同 無法做出表格 this.version_data =
      //因為構面可能不同 無法做出表格   form_version == "All" ? "All" : form_version.substr(1);

      var vm = this;
      this.axios
        .post('/rise_api/public/index.php/review/getForm_answer_month', {
          form_id: this.chooseValue,
          form_version: version,
          month_start: month_start,
          month_end: month_end,
        })
        .then((response) => {
          // console.log(response);
          if (response.data['0'].result == '' || response == undefined) {
            this.$swal({
              title: `目前尚無資料`,
              icon: 'info',

              confirmButtonText: '確定',
            });
          }
          $.each(response.data['0'].result, function (key, value) {
            if (
              response.data['0'].result[key].score.length !== 0 &&
              key === 0
            ) {
              dimensions.push(
                Object.keys(response.data['0'].result[key].score)
              );
            }
            answer_form.push({
              form_id: value.id,
              form_eid: value.eid,
              form_name: value.name,
              form_time: value.time,
              form_cname: value.cname,
              form_companyName: value.companyName,
              form_final_score: '', //總評
              form_final_score_2: 0,
              form_cnum: value.cnum,
              form_companyNum: value.companyNum,
              form_workers: value.workers,
              form_industry: value.industry,
              form_score: value.score, //各個構面分數
              form_answer: value.answer, //回答內容
            });
          });
          let final_score = JSON.parse(response.data['0'].result2.final_score);
          let test_str = '';
          //並非所有分數都會被加入總分
          //拿有納入總分的執行each 比對後把分數加總 再看落在哪一個區間內
          $.each(answer_form, (key, value) => {
            let score = 0;
            $.each(final_score.dimensions, (key2, value2) => {
              if (
                value.form_score[value2] != null &&
                value.form_score[value2] !== undefined
              ) {
                score += value.form_score[value2].score;
              } else {
                value.form_score[value2] = { des: '', score: 0 };
                score = 0;
              }
            });
            $.each(dimensions[0], (key2, value2) => {
              if (
                value.form_score[value2] != null &&
                value.form_score[value2] !== undefined
              ) {
              } else {
                value.form_score[value2] = { des: '', score: 0 };
                score = 0;
              }
            });
            value.form_final_score_2 = score;
            $.each(final_score.level, (key3, val) => {
              if (score >= val.min && score <= val.max) {
                answer_form[key].form_final_score = val.des;
              } else {
              }
            });
          });
          vm.show_type = true;
        });
      console.log(dimensions);
      console.log(answer_form);
    },
  },
};
</script>

