.over_css {
  display: block;
  overflow-x: auto;
  white-space: nowrap;
  margin-left: 15px;
  text-align: center;
}
thead:first-child {
  text-align: center;
  font-size: 1.2vw;
}

th:first-of-type {
  border-top-left-radius: 0.5rem;
}
th:last-of-type {
  border-top-right-radius: 0.5rem;
}

.myMOUSE2:hover {
  cursor: pointer;
  /* color: #0056c2;
  text-decoration: underline; */
}

.answer-container > .row {
  overflow-x: auto;
  white-space: nowrap;
}

.answer-container > .row > .col-12 {
  display: inline-block;
  float: none;
}
</style>



