<!--
  左側菜單樣式
-->
<template>
  <!-- Sidebar -->
  <nav class="nav navbar-inverse fixed-top" id="sidebar-wrapper" role="navigation">
    <ul class="nav sidebar-nav">
      <div class="sidebar-header">
        <div class="sidebar-brand">RISE管理系統</div>
      </div>

      <!--
      <li class="brand" v-if="dyna_data">廠商管理</li>
      <li v-else></li>
      <li :class="{ active: isActive(item.url) }" v-for="item in dyna_ls">
        <router-link :to="`/dyna/form/${item.url}?type=company`" replace>{{ item.name }}</router-link>
      </li>

      <li :class="{ active: isActive('/dyna/company/list')}">
        <router-link :to="`/dyna/company/list`" replace>廠商管理</router-link>
      </li>

      <li :class="{ active: isActive('/dyna/form/list/startup')}">
        <router-link :to="`/dyna/startup/list`" replace>新創機構資源</router-link>
      </li>

      <li :class="{ active: isActive('/dyna/form/list/test')}">
        <router-link :to="`/dyna/form/list/test`" replace>Test Table</router-link>
      </li>
      -->

      <li class="brand" v-if="form_data">問卷設計</li>
      <li v-else></li>
      <li :class="{ active: isActive(item.url) }" v-for="item in form_ls">
        <router-link :to="`/management/admin/${item.url}`" replace>{{ item.name }}</router-link>
      </li>

      <li class="brand" v-if="sys_data">系統管理</li>
      <li v-else></li>
      <li :class="{ active: isActive(item.url) }" v-for="item in sys_ls">
        <router-link :to="`/management/admin/${item.url}`" replace>{{ item.name }}</router-link>
      </li>

      <!--
      <li class="brand" v-if="user_data">帳號管理</li>
      <li v-else></li>
      <li :class="{ active: isActive(item.url) }" v-for="item in user_ls">
        <router-link :to="`/management/admin/${item.url}`" replace>{{ item.name }}</router-link>
      </li>

      <li class="brand" v-if="log_data">系統紀錄</li>
      <li v-else></li>
      <li :class="{ active: isActive(item.url) }" v-for="item in log_ls">
        <router-link :to="`/management/admin/${item.url}`" replace>{{ item.name }}</router-link>
      </li>

      <li class="brand" v-if="permit_data">權限管理</li>
      <li v-else></li>
      <li :class="{ active: isActive(item.url) }" v-for="item in permit_ls">
        <router-link :to="`/management/admin/${item.url}`" replace>{{ item.name }}</router-link>
      </li>

      <li class="brand" v-if="app_data">apptracker查詢</li>
      <li v-else></li>
      <li :class="{ active: isActive(item.url) }" v-for="item in app_ls">
        <router-link :to="`/management/admin/${item.url}`" replace>{{ item.name }}</router-link>
      </li>
      -->


    </ul>
  </nav>
  <!-- Sidebar -->
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      //問卷設計
      form_ls: [],
      //系統管理
      sys_ls: [],
      //帳號管理
      user_ls: [],
      //系統紀錄
      log_ls: [],
      //權限管理
      permit_ls: [],
      //apptracker
      app_ls: [],
      //dyna form
      dyna_ls: [],
      form_data: false, //問卷設計是否顯示
      sys_data: false, //系統管理是否顯示
      user_data: false, //帳號管理是否顯示
      log_data: false, //系統紀錄是否顯示
      permit_data: false, //權限管理是否顯示
      app_data: false, //權限管理是否顯示
      dyna_data: false // dyna form 顯示
    };
  },
  created() {
    this.get_permit();
  },
  methods: {
    isActive(url) {
      return this.$route.name.toLowerCase() == url.toLowerCase();
    },
    get_permit() {

      console.log('get_permit');
      this.axios
        .post(
          "/rise_api/public/index.php/permit/get_petmit_sidebar",
          {
            authority: localStorage.authority
          }
        )
        .then(response => {
          var form_ls = this.form_ls;
          var response2 = response.data;

          console.log(response2);
          for (let i = 0; i < response2.form.name.length; i++) {
            this.form_data = true;
            form_ls.push({
              name: response2.form.name[i],
              url: response2.form.url[i]
            });
          }

          var sys_ls = this.sys_ls;
          if (typeof response2.sys != "undefined") {
            for (let i = 0; i < response2.sys.name.length; i++) {
              this.sys_data = true;
              sys_ls.push({
                name: response2.sys.name[i],
                url: response2.sys.url[i]
              });
            }
          }

          /*
          var user_ls = this.user_ls;
          if (typeof response2.user != "undefined") {
            for (let i = 0; i < response2.user.name.length; i++) {
              this.user_data = true;
              user_ls.push({
                name: response2.user.name[i],
                url: response2.user.url[i]
              });
            }
          }
          var log_ls = this.log_ls;
          if (typeof response2.log != "undefined") {
            for (let i = 0; i < response2.log.name.length; i++) {
              this.log_data = true;
              log_ls.push({
                name: response2.log.name[i],
                url: response2.log.url[i]
              });
            }
          }
          var permit_ls = this.permit_ls;
          if (typeof response2.permit != "undefined") {
            for (let i = 0; i < response2.permit.name.length; i++) {
              this.permit_data = true;
              permit_ls.push({
                name: response2.permit.name[i],
                url: response2.permit.url[i]
              });
            }
          }
          var app_ls = this.app_ls;
          if (typeof response2.app != "undefined") {
            for (let i = 0; i < response2.app.name.length; i++) {
              this.app_data = true;
              app_ls.push({
                name: response2.app.name[i],
                url: response2.app.url[i]
              });
            }
          }
          */

          var dyna_ls = this.dyna_ls;
          if (typeof response2.dyna != "undefined") {
            for (let i = 0; i < response2.dyna.name.length; i++) {
              this.dyna_data = true;
              dyna_ls.push({
                name: response2.dyna.name[i],
                url: response2.dyna.url[i]
              });
            }
          }
        });
    }
  }
};
</script>

<style >
/*-------------------------------*/
/*     Container styles          */
/*-------------------------------*/
.nav {
  padding: 0;
}

#sidebar-wrapper {
  z-index: 1000;
  width: 0;
  height: 100%;
  font-size: 12pt;
  overflow-y: auto;
  overflow-x: hidden;
  background: #1a1a1a;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

#wrapper.toggled #sidebar-wrapper {
  width: 220px;
}

/*-------------------------------*/
/*     Sidebar nav styles        */
/*-------------------------------*/
.sidebar-nav {
  position: absolute;
  top: 0;
  width: 220px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.sidebar-nav li {
  position: relative;
  line-height: 30px;
  display: inline-block;
  width: 100%;
}

.sidebar-nav li:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  height: 100%;
  width: 3px;
  background-color: #2574a9;
}

.sidebar-nav li:hover:before {
  width: 100%;
}

.sidebar-nav li a {
  display: block;
  color: #fff;
  text-decoration: none;
  padding: 10px 15px 10px 30px;
}

.sidebar-nav li a:hover,
.sidebar-nav li a:active,
.sidebar-nav li a:focus {
  color: #fff;
  text-decoration: none;
  padding-left: 35px;
  background-color: #2574a9;
}

.sidebar-nav li.brand {
  background-color: #000;
  color: #ddd;
  padding: 5px;
  font-weight: 600;
  background: linear-gradient(#0a0a0a, #000);
}

.sidebar-nav li.active {
  background: #2574a9;
}

.sidebar-header {
  font-size: 20px;
  position: relative;
  width: 100%;
  display: inline-block;
}

.sidebar-brand {
  color: #ddd;
  height: 4rem;
  position: relative;
  background-color: #2574a9;
  font-weight: 500;
  padding: 1rem 1rem;
}
</style>



