import _ from 'lodash';
import R from 'ramda';
import A from 'axios';

import { errorHandler } from './api';

export const INDUSTRY_ID_1_LENGTH = 1;
export const INDUSTRY_ID_2_LENGTH = 2;
export const INDUSTRY_ID_3_LENGTH = 4;

async function fetchCompanyQuery(apiUrl, companyNum) {
  try {
    const response = await A.get(apiUrl, {
      params: {
        'company-num': companyNum,
      },
    });

    const { data } = response;
    const { result, body } = data;

    if (!result) {
      return {};
    } else {
      return body;
    }
  } catch (error) {
    errorHandler(error);
    return false;
  }
}

export async function verifyCompanyNum(companyNum) {
  /* 呼叫後端服務 */
  const verifyUrl = `/dyna_form_api/public/index.php/form-content/verify-company-num`;

  try {
    const getResponse = await A.get(verifyUrl, {
      params: {
        'company-num': companyNum,
      },
    });

    const { data } = getResponse;
    const { result } = data;

    return result;
  } catch (error) {
    errorHandler(error);
    return false;
  }
}

export async function getCompanyInfo(companyNum) {
  const apiUrl = `/dyna_form_api/public/index.php/form-content/get-company-info`;
  return fetchCompanyQuery(apiUrl, companyNum);
}

export async function getCompanyType(companyNum) {
  const apiUrl = `/dyna_form_api/public/index.php/form-content/get-company-type`;
  return fetchCompanyQuery(apiUrl, companyNum);
}



