<template>
  <div>
    <div class="limiter">
      <div class="login-wrapper p-t-30 p-b-50">
        <span class="form-title my-3">RISE管理系統</span>
        <div class="form-content py-2">
          <form>
            <div class="form-group input-wrapper">
              <input
                class="form-control form-input"
                type="text"
                v-model="username"
                placeholder="User name"
              />
            </div>
            <div class="form-group input-wrapper">
              <input
                class="form-control form-input"
                type="password"
                v-model="passwd"
                placeholder="Password"
                @keyup.enter="login"
              />
            </div>
          </form>
          <div class="btn-wrapper m-t-32">
            <button class="btn btn-secondary login-btn" @click.prevent="login">登入</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>




<script>
import $ from "jquery";
export default {
  data() {
    return {
      username: "",
      passwd: ""
    };
  },
  created: function() {
    var _this = this;
    // document.onkeydown = function(e) {
    //   let key = window.event.keyCode;
    //   if (key == 13) {
    //     _this.login();
    //   }
    // };
  },
  methods: {
    login() {
      let email = this.username;
      let password = this.passwd;
      this.$store.dispatch("auth/login", { email, password }).then(Response => {
        if (Response.data[0].suc == "T")
          this.$router.push("/management/admin/design");
        else {
          console.log(Response);
          this.$swal({
            icon: "error",
            title: "帳號或密碼錯誤",
            showConfirmButton: false,
            timer: 1500
          });
        }
      });
    }
  }
};
</script>

<style>
body,
html {
  height: 100%;
}

.limiter {
  width: 100%;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ecf2f9;
}
.limiter::before {
  content: "";
  display: block;
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.65);
}

.login-wrapper {
  width: 390px;
  border-radius: 10px;
  overflow: hidden;

  background: transparent;
}

/*-------------------------------*/
/*     form styles          */
/*-------------------------------*/

.form-content {
  width: 100%;
  border-radius: 10px;
  background-color: #fff;
}
.form-title {
  font-size: 28px;
  color: #555555;
  line-height: 1.2;
  text-align: center;
  text-transform: uppercase;

  display: block;
}

/*-------------------------------*/
/*     Input styles          */
/*-------------------------------*/

.input-wrapper {
  width: 100%;
  position: relative;
  border-bottom: 1px solid #e6e6e6;
  padding: 10px 20px;
}
.form-input {
  font-size: 1.2rem;
  background: transparent;
  border: none;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s;
  transition: all 0.4s;
}
.form-input:focus,
.form-input:hover {
  padding-left: 10px;
}
.form-input:focus::-webkit-input-placeholder {
  color: transparent;
}
.form-input:focus:-moz-placeholder {
  color: transparent;
}
.form-input:focus::-moz-placeholder {
  color: transparent;
}
.form-input:focus:-ms-input-placeholder {
  color: transparent;
}

.form-input::-webkit-input-placeholder {
  color: #555555;
}
.form-input:-moz-placeholder {
  color: #555555;
}
.form-input::-moz-placeholder {
  color: #555555;
}
.form-input:-ms-input-placeholder {
  color: #555555;
}

/*-------------------------------*/
/*     Button styles          */
/*-------------------------------*/
.btn-wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.login-btn {
  font-size: 18px;
  color: #fff;
  line-height: 1.2;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
  min-width: 160px;
  height: 42px;
  border-radius: 21px;
  position: relative;
  z-index: 1;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s;
  transition: all 0.4s;
}
.login-btn:hover {
  background-color: #555555;
}
.login-btn a {
  color: #fff;
}
</style>



