<!--
	各種題目選項設定
	必選、其他選項等等
-->
<template>
  <div class="option-area col-3 p-2">
    <label class="checkbox">
      <input type="checkbox" v-model="question.required" />
      <font-awesome-icon v-if="question.required" icon="check-square" />
      <font-awesome-icon v-else :icon="['far', 'square']" />
      <span class="text-nowrap">必填</span>
    </label>

    <label class="checkbox" v-if="question.hasOwnProperty('manualInput')">
      <input type="checkbox" v-model="question.manualInput" />
      <font-awesome-icon v-if="question.manualInput" icon="check-square" />
      <font-awesome-icon v-else :icon="['far', 'square']" />
      <span class="text-nowrap">手動輸入</span>
    </label>

    <label class="checkbox" v-if="question.hasOwnProperty('hasOther')">
      <input type="checkbox" v-model="question.hasOther" />
      <font-awesome-icon v-if="question.hasOther" icon="check-square" />
      <font-awesome-icon v-else :icon="['far', 'square']" />
      <span class="text-nowrap">其他選項</span>
    </label>
    <label class="checkbox" v-if="question.hasOwnProperty('arrangement')">
      <input type="checkbox" v-model="question.arrangement" />
      <font-awesome-icon v-if="question.arrangement" icon="check-square" />
      <font-awesome-icon v-else :icon="['far', 'square']" />
      <span class="text-nowrap">雙排</span>
    </label>
    <div class="form-row" v-if="question.hasOwnProperty('maxSelect')">
      <label class="col-lg-4 col-form-label text-nowrap">多選上限:</label>
      <div class="col-lg-3">
        <input class="form-control" type="text" v-model="question.maxSelect" />
      </div>
    </div>
    <div class="form-row" v-if="question.hasOwnProperty('min')">
    <label class="col-lg-3 col-form-label text-nowrap">範圍:</label>
    <div class="col-lg-3">
      <input class="form-control" type="text" v-model="question.min" />
    </div>
    <label class="col-lg-1 col-form-label text-nowrap">-</label>
    <div class="col-lg-3">
      <input class="form-control" type="text" v-model="question.max" />
    </div>
  </div>

    <div class="form-row" v-if="question.manualInput">
        <label class="col-lg-3 col-form-label text-nowrap">小數點</label>
      <div class="col-lg-3">
        <input class="form-control" type="text" v-model="question.decimalPlace" />
      </div>
      <label class="col-lg-1 col-form-label text-nowrap">位</label>
    </div>
<!--    <div class="form-row" v-if="question.manualInput">-->
<!--      <label class="col-lg-3 col-form-label text-nowrap">單位:</label>-->
<!--      <div class="col-lg-3_L">-->
<!--        <input class="form-control" type="text" v-model="question.unit" />-->
<!--      </div>-->
<!--    </div>-->

    <div class="form-row" v-if="question.hasOwnProperty('maxScore')">
      <label class="col-lg-3 col-form-label text-nowrap">滿分:</label>
      <div class="col-lg-3">
        <input class="form-control" type="text" v-model="question.maxScore" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["question"],
};
</script>

<style scoped>
.option-area {
  color: #666;
}
.col-lg-3{
  -ms-flex:0 0 26%;
  flex:0 0 26%;
  max-width:26%
}

.col-lg-3_L{
  -ms-flex:0 0 40%;
  flex:0 0 40%;
  max-width:40%
}

/*-------------------------------*/
/*     Checkbox styles           */
/*-------------------------------*/
.checkbox {
  display: block;
  position: relative;
  cursor: pointer;
  color: #666;
  font-size: 1rem;
}
input[type="checkbox"] {
  position: absolute;
  right: 9000px;
}
</style>



