<!--
  標頭樣式
-->
<template>
  <nav class="nav navbar justify-content-end fixed-top" id="header">
    <button class="btn btn btn-danger my-2 my-sm-0" type="submit" @click="sign_out()">登出</button>
  </nav>
</template>

<style>
#header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  height: 4rem;
  padding: 0.5rem 1rem;
  /*border: 1px solid #e7e7e7;*/
  background-color: #1a1a1a;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
#wrapper.toggled #header {
  margin-left: 220px;
}
</style>

<script>
export default {
  methods: {
    //一進來取表單資料
    sign_out() {
      let email = localStorage.username;
      this.$swal({
        text: "確定要登出嗎?",
        icon: "question",
        showCancelButton: true,
        confirmButtonText: "確定",
        cancelButtonText: "取消",
        preConfirm: () => {
          this.$store.dispatch("auth/logout", { email }).then(Response => {
            this.$router.push("/management/login");
          });
        }
      });
    }
  }
};
</script>



