<template>
  <dyna-form
    :list-page="listPage"
    :form-key="formKey"
    :form-id="formId"
  />
</template>

<script>
import DynaForm from '../../components/DynaForm/index.vue';

export default {
  components: { DynaForm },
  props: {},
  data() {
    return {
      formKey: 'startup',
      formId: -1,
      listPage: '/dyna/startup/list',
    };
  },
  created() {
    this.formId = this.$route.query.id;
  },
  methods: {},
  computed: {},
};
</script>



