<template>
  <div class="form-group row">
    <dyna-title
      :columnDisplay="columnDisplay"
      :columnName="columnName"
      :columnNullable="columnNullable"
    />
    <div class="form-inline col-lg-10">
      <div v-if="isText || isEmail || isPhone">
        <input
          :type="inputType"
          :class="columnWidth"
          :id="columnName"
          :name="columnName"
          :size="columnLength"
          :placeholder="columnPlaceholder"
          v-model="columnValue"
        />
      </div>
      <div v-else-if="isMultiline || isRich">
        <textarea
          class="form-control text-500"
          rows="3"
          :id="columnName"
          :name="columnName"
          :placeholder="columnPlaceholder"
          v-model="columnValue"
        ></textarea>
      </div>
      &nbsp;
      <dyna-verifier
        :columnDisplay="columnDisplay"
        :needVerify="needVerify"
        :verifier="verifier"
        :valueGetter="getValue"
      />
      &nbsp;
      <dyna-helper
        :callback="callback"
        :helper="helper"
      />
      <!--
      &nbsp;
      <button
        type="button"
        class="btn btn-warning"
        @click="checkValue"
      >
        CheckValue
      </button>
      -->
    </div>
  </div>
</template>

<script>
import DynaVerifier from '../DynaVerifier/index.vue';
import DynaTitle from '../DynaTitle/index.vue';
import DynaHelper from '../DynaHelper/index.vue';

import { getEchoMan, getTrueMan } from '../../utils/lang';
import { getDefaultHelper, TYPE_CREATE, TYPE_UPDATE } from '../../utils/form';

export default {
  props: {
    columnSpec: Object,
    formName: String,
  },
  components: { DynaVerifier, DynaTitle, DynaHelper },
  data() {
    return {
      columnName: '',
      columnDisplay: '',
      columnComponent: '',
      columnLength: '',
      columnNullable: '',
      columnPlaceholder: '',
      columnValue_: '',
      needVerify: false,
      needTransform: false,
      verifier: getTrueMan(),
      transformer: getEchoMan(),
      updater: getEchoMan(),
      helper: getDefaultHelper(),
      inputType: 'text',
    };
  },
  created() {
    const columnSpec = this.columnSpec;

    // console.log('column spec in dyna text: ', columnSpec);

    this.columnName = columnSpec.columnName;
    this.columnDisplay = columnSpec.columnDisplay;
    this.columnComponent = columnSpec.columnComponent;
    this.columnLength = columnSpec.columnLength;
    this.columnNullable = columnSpec.columnNullable;
    this.columnPlaceholder = columnSpec.columnPlaceholder;
    this.columnValue_ = columnSpec.columnValue;

    this.needVerify = columnSpec.needVerify;
    this.needTransform = columnSpec.needTransform;

    this.verifier = columnSpec.verifier;
    this.transformer = columnSpec.transformer;
    this.updater = columnSpec.updater;
    this.helper = columnSpec.helper;

    if (this.isEmail) {
      this.inputType = 'email';
    } else if (this.isPhone) {
      this.inputType = 'tel';
    }
  },
  methods: {
    getValue() {
      return this.columnValue;
    },
    callback(value) {
      this.columnValue = value;
      this.updater(value);
    },
    updateModel(event) {
      const value = event.target.value;
      this.updateValue(value);
    },
    updateValue(value) {
      this.updater(value);
    },
    checkValue() {
      const value = this.getValue();
      console.log('check value: ', value);
    },
  },
  computed: {
    columnValue: {
      get() {
        return !!this.columnValue_
          ? this.columnValue_
          : this.columnSpec.columnValue;
      },
      set(val) {
        this.columnValue_ = val;
        this.updater(val);
        this.$emit('updated', val);
      },
    },
    columnWidth: function () {
      console.log('compute width by length: ', this.columnLength);

      if (this.columnLength) {
        return `form-control text-${this.columnLength}`;
      } else {
        return 'form-control';
      }
    },
    isText() {
      return this.columnComponent === 'text';
    },
    isEmail() {
      return this.columnComponent === 'email';
    },
    isRich() {
      return this.columnComponent === 'rich';
    },
    isMultiline() {
      return this.columnComponent === 'multiline';
    },
    isCode() {
      return this.columnComponent === 'code';
    },
    isPhone() {
      return this.columnComponent === 'phone';
    },
  },
};
</script>

<style>
.necessary {
  color: red;
}

.alert {
  color: red;
  font-size: 0.8em;
}

.valid {
  color: green;
  font-size: 0.8em;
}

.text-10 {
  width: 120px !important;
}

.text-25 {
  width: 180px !important;
}

.text-50 {
  width: 240px !important;
}

.text-100 {
  width: 360px !important;
}

.text-250 {
  width: 480px !important;
}

.text-500 {
  width: 720px !important;
}
</style>



