<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-lg-8 col-lg-offset-2">
        <h2 class="title">使用者帳號管理</h2>
        <p>(狀態1為待審核統一編號是否正確，如確定無誤請修改為2)</p>
      </div>
    </div>
    <hr />

    <table class="table table-bordered table-striped">
      <thead>
        <tr>
          <th scope="col">電子郵件</th>
          <th scope="col">姓名</th>
          <th scope="col">公司名稱</th>
          <th scope="col">統一編號</th>
          <th scope="col">聯絡電話</th>
          <th scope="col">驗證碼</th>
          <th scope="col">驗證狀態</th>
          <th scope="col"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(form, index) in forms">
          <td>{{ form.form_username }}</td>
          <td>{{ form.form_name }}</td>
          <td>{{ form.form_cname }}</td>
          <td>{{ form.form_cnum }}</td>
          <td>{{ form.form_phone }}</td>
          <td>{{ form.form_registered_token }}</td>
          <td>{{ form.form_status }}</td>
          <td>
            <button
              class="btn btn-primary"
              @click="change_status(form)"
            >
              編輯
            </button>
            <button
              class="btn btn btn-danger"
              @click="delete_data(form)"
            >
              刪除
            </button>
            <button
              class="btn btn btn-success"
              @click="short_path(form)"
            >
              短網址
            </button>
          </td>
        </tr>
      </tbody>
    </table>

    <div
      id="myModal"
      class="modal fade"
      ref="myModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="myModalLabel"
      aria-hidden="true"
    >
      <div
        class="modal-dialog"
        role="document"
      >
        <div class="modal-content">
          <div class="modal-header">
            <h4 class="modal-title">短網址設定</h4>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div>
              <h5>新增短網址</h5>
              <select v-model="shortFormId">
                <option value="">請選擇問卷</option>
                <option
                  :value="item.id"
                  v-for="item in origForms"
                  :key="item.id"
                  :disabled="isDisabled(item.id)"
                >
                  {{ item.name }}
                </option>
              </select>
              <div
                class="text-right"
                style="padding-top: 10px; padding-right: 45px"
              >
                <button
                  type="button"
                  class="btn btn-primary"
                  @click="makeShortPath()"
                >
                  建立短網址
                </button>
              </div>
            </div>
            <hr />
            <div>
              <div v-if="has_short_paths()">
                <ul>
                  <li
                    v-for="short in shortPaths"
                    :key="short.short_url"
                  >
                    {{ short.name }} :
                    <a :href="short.short_url">{{ short.short_url }}</a>
                    &nbsp;
                    <a
                      class="tooltiplink"
                      data-title="刪除此筆短網址"
                      @click="deleteShortPath(short.short_url)"
                    >
                      <font-awesome-icon icon="trash" />
                    </a>
                    &nbsp;
                    <a
                      class="tooltiplink"
                      data-title="發送短網址通知"
                      @click="showMail(short.short_url)"
                    >
                      <font-awesome-icon icon="envelope" />
                    </a>
                  </li>
                </ul>
                <div
                  id="mail-short"
                  ref="mailer"
                  :class="mailerClass"
                >
                  <form>
                    <div class="form-group">
                      <label
                        for="notify_title"
                        style="font-weight: 500"
                        >通知標題</label
                      >
                      <br />
                      <input
                        name="notify_title"
                        type="text"
                        placeholder="通知標題"
                        class="form-control"
                        v-model="sendingTitle"
                      />
                    </div>
                    <div class="form-group">
                      <label
                        for="notify_content"
                        style="font-weight: 500"
                      >
                        通知內容
                      </label>
                      <br />
                      <!--  -->
                      <vue-ckeditor
                        id="content_editor"
                        name="notify_content"
                        ref="contentEditor"
                        v-model="sendingContent"
                        :config="mailConfig"
                      />

                      <!--
                      <textarea
                        name="notify_content"
                        v-model="sendingContent"
                        placeholder="通知內容"
                        class="form-control"
                        rows="5"
                      ></textarea>
                      -->
                    </div>
                    <div
                      class="text-right"
                      style="margin-top: 10px"
                    >
                      <button
                        type="button"
                        class="btn btn-primary"
                        @click="sendMail()"
                      >
                        發送
                      </button>
                      <button
                        type="button"
                        class="btn btn-secondary"
                        @click="cancelSending()"
                      >
                        取消
                      </button>
                    </div>
                  </form>
                </div>
              </div>

              <p v-else>目前尚未建立短網址</p>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              Close
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import _ from 'lodash';

import VueCkeditor from 'vue-ckeditor2';

export default {
  components: { VueCkeditor },

  data() {
    return {
      new_form_name: '',
      forms: [],
      selected: {},
      shortPaths: [],
      origForms: [],
      myForms: [],
      shortFormId: '',
      sendingShortPath: {},
      sendingTitle: '',
      sendingContent: '',
      mailConfig: {
        toolbarGroups: [
          { name: 'clipboard', groups: ['clipboard', 'undo'] },
          {
            name: 'editing',
            groups: ['find', 'selection', 'spellchecker', 'editing'],
          },
          { name: 'forms', groups: ['forms'] },
          '/',
          { name: 'basicstyles', groups: ['basicstyles', 'cleanup'] },
          {
            name: 'paragraph',
            groups: ['list', 'indent', 'blocks', 'align', 'bidi', 'paragraph'],
          },
          { name: 'links', groups: ['links'] },
          { name: 'insert', groups: ['insert'] },
          '/',
          { name: 'styles', groups: ['styles'] },
          { name: 'colors', groups: ['colors'] },
          { name: 'tools', groups: ['tools'] },
          { name: 'document', groups: ['mode', 'document', 'doctools'] },
          { name: 'others', groups: ['others'] },
          { name: 'about', groups: ['about'] },
        ],
        removeButtons:
          'Save,NewPage,Preview,Print,Templates,Cut,Copy,Paste,PasteText,PasteFromWord,Undo,Redo,Find,Replace,SelectAll,Scayt,Form,Checkbox,Radio,TextField,Select,Button,ImageButton,Textarea,HiddenField,About,Maximize,ShowBlocks,BGColor,TextColor,Flash,Table,PageBreak,Iframe,SpecialChar,Smiley,Anchor,Unlink,BidiLtr,BidiRtl,Language,JustifyBlock,JustifyRight,JustifyCenter,JustifyLeft,Indent,Outdent,Strike,Subscript,Superscript,RemoveFormat,CopyFormatting,Blockquote,Styles,Font,FontSize',
        height: 300,
        allowedContent: true,
      },
    };
  },
  created() {
    var self = this;
    self.getFormList();
  },
  mounted() {
    $('#wrapper').addClass('toggled');
    CKEDITOR.disableAutoInline = true;
  },
  computed: {
    excludedFormIds() {
      return this.myForms.map((item) => item.id);
    },
    mailerClass() {
      if (!_.isEmpty(this.sendingShortPath)) {
        return 'show';
      } else {
        return '';
      }
    },
  },
  /*
  updated() {
    if (this.$refs.contentEditor) {
      CKEDITOR.disableAutoInline = false;
      CKEDITOR.inline(this.$refs.contentEditor);
    }
  },
  beforeDestroy() {
    if (this.$refs.contentEditor) {
      CKEDITOR.instances[this.$refs.contentEditor.getAttribute('id')].destroy();
    }
  },
  */
  methods: {
    //一進來取表單資料
    getFormList() {
      var self = this;
      this.axios
        .get('/rise_api/public/index.php/user_account/get_member')
        .then((response) => {
          var forms = (self.forms = []);
          $.each(response.data, function (key, value) {
            forms.push({
              form_username: value.username,
              form_email: value.email,
              form_name: value.name,
              form_cname: value.cname,
              form_cnum: value.cnum,
              form_phone: value.phone,
              form_auth: value.auth,
              form_registered_token: `${value.registered_token.substr(0, 6)}**`,
              form_status: value.status,
            });
          });
        });
    },
    change_status(rowItem) {
      console.log(rowItem);
      this.$swal({
        title: `目前修正資料為${rowItem.form_username}`,
        html:
          `<table><tr><td>聯絡郵件 <td/> <td><input type='text' id='swal-input-email' class='form-control' value="${rowItem.form_email}"><td/><tr/>` +
          `<tr><td> 姓名     <td/> <td> <input type='text' id='swal-input-name' class='form-control' value="${rowItem.form_name}"><td/><tr/>` +
          `<tr><td> 公司名稱  <td/> <td> <input type='text' id='swal-input-company' class='form-control' value="${rowItem.form_cname}"><td/><tr/>` +
          `<tr><td> 統一編號  <td/> <td> <input type='text' id='swal-input-number' class='form-control' value="${rowItem.form_cnum}"><td/><tr/>` +
          `<tr><td> 連絡電話  <td/> <td> <input type='text' id='swal-input-phone' class='form-control' value="${rowItem.form_phone}"><td/><tr/>` +
          `<tr><td> 驗證狀態  <td/> <td> <input type='text' id='swal-input-status' class='form-control' value="${rowItem.form_status}"><td/><tr/>` +
          "<tr><td colspan ='3' align='right'>(驗證統一編號：0為待審核，2為正確)<td/><tr/><table/>",

        inputAttributes: {
          autocapitalize: 'off',
        },
        showCancelButton: true,
        confirmButtonText: '修改',
        cancelButtonText: '取消',
        showLoaderOnConfirm: true,
        preConfirm: (value) => {
          return this.axios
            .put(
              '/rise_api/public/index.php/user_account/member_status_update',
              {
                form_username: rowItem.form_username,
                mamber_email: document.getElementById('swal-input-email').value,
                mamber_name: document.getElementById('swal-input-name').value,
                mamber_company:
                  document.getElementById('swal-input-company').value,
                mamber_number:
                  document.getElementById('swal-input-number').value,
                mamber_phone: document.getElementById('swal-input-phone').value,
                member_status:
                  document.getElementById('swal-input-status').value,
              }
            )
            .then((response) => {
              if (response.data == '1') {
                this.$swal({
                  icon: 'success',
                  title: '修改完成',
                  showConfirmButton: false,
                  timer: 1500,
                }).then((response) => {
                  this.getFormList();
                });
              }
            });
        },
      });
    },
    delete_data(rowItem) {
      this.$swal({
        text: `確定要刪除電子郵件為${rowItem.form_username}使用者嗎?`,
        icon: 'question',
        showCancelButton: true,
        confirmButtonText: '確定',
        cancelButtonText: '取消',
        preConfirm: () => {
          return this.axios
            .delete('/rise_api/public/index.php/user_account/delete_member', {
              params: {
                form_username: rowItem.form_username,
              },
            })
            .then((response) => {
              if ((response.data = '1')) {
                this.$swal({
                  icon: 'success',
                  title: '刪除完成',
                  showConfirmButton: false,
                  timer: 1500,
                }).then((response) => {
                  this.getFormList();
                });
              }
            });
        },
      });
    },
    isDisabled(id) {
      return this.excludedFormIds.includes(id);
    },
    short_path(rowItem) {
      this.selected = rowItem;

      this.shortPaths = [];
      this.myForms = [];
      this.origForms = [];

      this.fetch_short_paths(true);
    },
    fetch_short_paths(show_modal) {
      const self = this;

      this.axios
        .get('/rise_api/public/index.php/design/getFormList')
        .then((response) => {
          const { data } = response;
          self.origForms = data.map((item) => ({
            id: item.id,
            name: item.name,
          }));

          self.axios
            .get('/rise_api/public/index.php/user_account/get_short', {
              params: {
                username: self.selected['form_username'],
              },
            })
            .then((response) => {
              self.shortPaths = response.data;

              if (self.has_short_paths()) {
                self.myForms = self.shortPaths.map((short) => ({
                  id: short['form_id'],
                  name: short['name'],
                }));
              }

              console.log('my forms: ', self.myForms);

              if (show_modal) {
                $(self.$refs.myModal).modal('show');
              }
            });
        });
    },
    has_short_paths() {
      return !_.isNil(this.shortPaths) && !_.isEmpty(this.shortPaths);
    },
    makeShortPath() {
      const username = this.selected.form_username;
      const form_id = this.shortFormId;
      const adminname = localStorage.getItem('username');

      console.log(
        'make short path: ',
        username,
        ' form: ',
        form_id,
        ' by ',
        adminname
      );

      const self = this;
      // https://rise.iii.org.tw/rise_api/public/index.php/user_account/create_short?username=ashley@mail.fmsc.com.tw&form_id=12mmhxj5co302f4y&adminname=admin
      this.axios
        .post('/rise_api/public/index.php/user_account/create_short', {
          username,
          form_id,
          adminname,
        })
        .then((response) => {
          self.shortPaths = [];
          self.myForms = [];
          self.origForms = [];

          self.fetch_short_paths(false);
        });
    },
    showMail(shortUrl) {
      console.log('send mail');

      // short.short_url
      this.sendingShortPath = this.shortPaths.find(
        (item) => item['short_url'] === shortUrl
      );

      console.log('sending short path: ', this.sendingShortPath);

      if (!!this.sendingShortPath) {
        this.sendingTitle = `「${this.sendingShortPath['name']}」問卷短網址通知`;
        this.sendingContent = `<div><h5>您好：</h5>

<p>
以下是您的問卷短網址，請妥善保管。<br/>
<a href="${this.sendingShortPath['short_url']}">${this.sendingShortPath['short_url']}</a><br/>
</p>

<i>
此為系統自動發送，請勿直接回覆此信件。
</i></div>`;

        $.fn.modal.Constructor.prototype.enforceFocus = function () {
          modal_this = this;
          $(document).on('focusin.modal', function (e) {
            if (
              modal_this.$element[0] !== e.target &&
              !modal_this.$element.has(e.target).length &&
              !$(e.target.parentNode).hasClass('cke_dialog_ui_input_select') &&
              !$(e.target.parentNode).hasClass('cke_dialog_ui_input_text')
            ) {
              modal_this.$element.focus();
            }
          });
        };

        $(this.$refs.myModal).removeAttr('tabindex');

        // this.$refs.myModal.removeAttr('tabindex');
        // document.querySelector('.cke_dialog_body').style.zIndex = '9999';
        // $('#myModal').removeAttr('tabindex');
      }
    },
    sendMail() {
      console.log('send mail');
      const self = this;
      const adminname = localStorage.getItem('username');

      this.axios
        .post('/rise_api/public/index.php/user_account/send_notify', {
          username: this.sendingShortPath['username'],
          form_id: this.sendingShortPath['form_id'],
          adminname: adminname,
          notify_type: 'SHORT_PATH',
          notify_title: this.sendingTitle,
          notify_content: this.sendingContent,
        })
        .then((response) => {
          const data = response.data;
          if (data['result'] === 'success') {
            self
              .$swal({
                icon: 'success',
                title: '寄送完成',
                showConfirmButton: false,
                timer: 1500,
              })
              .then((response) => {
                self.cancelSending();
              });
          } else {
            self.$swal({
              icon: 'error',
              title: '寄送失敗',
              text: data['error']['text'],
              showConfirmButton: false,
              timer: 1500,
            });
          }
        });
    },
    cancelSending() {
      this.sendingShortPath = null;
      this.sendingTitle = '';
      this.sendingContent = '';
    },
    deleteShortPath(shortUrl) {
      console.log('delete short path: ', shortUrl);
      const adminname = localStorage.getItem('username');
      const shortPath = this.shortPaths.find(
        (item) => item['short_url'] === shortUrl
      );

      if (!!shortPath) {
        const self = this;

        //show swal to confirm delete
        this.$swal({
          title: '確定要刪除此短網址嗎？',
          text: '刪除後將無法復原',
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#d33',
          cancelButtonColor: '#3085d6',
          confirmButtonText: '刪除',
          cancelButtonText: '取消',
        }).then((result) => {
          if (result.value) {
            self.axios
              .post('/rise_api/public/index.php/user_account/delete_short', {
                username: shortPath['username'],
                form_id: shortPath['form_id'],
                adminname: adminname,
              })
              .then((response) => {
                self.shortPaths = [];
                self.myForms = [];
                self.origForms = [];

                self.fetch_short_paths(false);
              });
          }
        });
      }
    },
  },
};
</script>

<style scoped>
a.tooltiplink {
  position: relative;
}

a.tooltiplink:hover::after {
  content: attr(data-title);
  background-color: #8fbc8f;
  color: #fff;
  padding: 8px;
  border-radius: 4px;
  font-size: 12px;
  line-height: 14px;
  display: block;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
  z-index: 1;
}

#mail-short {
  display: none;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

#mail-short.show {
  display: block;
  opacity: 1;
}

.cke_dialog_body {
  z-index: 99999;
}
</style>



