
<template>
  <!-- <div v-if="curr_dimension">
    <final-score-block :form="form" :name="curr_dimension" :score="curr_finalscore">
  <div>-->
  <!-- <textarea class="form-control" rows="5" placeholder="閱讀前需知~~" v-model="form.first_read"></textarea> -->
  <!-- </div>
    </final-score-block>
  </div>-->
  <div>
    <div class="final-score-block-wrapper">
      <div class="final-score-block">
        <form id="form1">
          <h2 class="card-header">可編輯使用者_ {{form_namee[0]}}</h2>
          <!--h2 class="card-header">可編輯使用者</h2-->
          <div class="card-body">
            <div class="questions-modify row mx-0 py-4">
              <div class="col mx-2">
                <h5>無法編輯使用者</h5>
                <select multiple class="question-select form-control" v-model="left_data">
                  <option v-for="form in forms" :value="form" :key="form">{{ form }}</option>}
                </select>
              </div>
              <div>
                <button class="btn btn-warning" @click="toLeft"><</button>
                <button class="btn btn-warning" @click="toRight">></button>
              </div>
              <div class="col mx-2">
                <h5>可編輯使用者</h5>
                <select multiple class="question-select form-control" v-model="right_data">
                  <option v-for="form in forms_2[0]" :value="form" :key="form">{{ form }}</option>}
                </select>
              </div>
            </div>
            <br />
          </div>
          <div align="right">
            <button class="btn btn-success" @click="save" style="margin-right: 20px;">儲存</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import $ from "jquery";
export default {
  props: ["form"],
  components: {},
  data() {
    return {
      forms: [],
      forms_2: [],
      left_data: [], //左邊的值(無法編輯使用者)
      right_data: [], //右邊的值(可編輯使用者)
      form_namee:[]
    };
  },
  mounted() {
    //固定新增級距按鈕在右下角，使用scrollTop獲取滾輪位置
    $("#wrapper").addClass("toggled");
  },
  created() {
    this.read();
  },
  methods: {
    read() {
      var self = this;
      var forms = self.forms;
      var forms_2 = self.forms_2;
      //可編輯使用者
      let form_need = location.href.split("authority/");
      let form_id=form_need[1].split(".");
      var form_name=decodeURIComponent(form_id[1]);
      self.form_namee.push(form_name);

      this.axios
        .post(
          "/rise_api/public/index.php/design/get_edit_user",
          {
            form_id: form_id[0]
          }
        )
        .then(response => {
          var edit_user = JSON.parse(response.data[0].edit_user);
          //表示是空的
          if (edit_user.length === 0) {
            console.log("no_data");
          } else {
            $.each(edit_user, function(key, value) {
              forms_2.push(value);
            });

          }
          //無法編輯使用者
          this.axios
              .get(
              "/rise_api/public/index.php/admin_account/get_admin"
            )
            .then(response => {
              $.each(response.data, function(key, value) {
                forms.push(value.username);
              });
            })
            .then(() => {
              self.$nextTick(function() {
                //將可編輯的使用者從無法編輯刪除
                forms_2[0].forEach(function(key, value) {
                  let delete_index = forms.indexOf(key);
                  forms.splice(delete_index, 1);
                });
              });
            });
        });
    },
    // 左邊待選題目移至右邊已選題目
    toRight() {
      //先新增右邊的  再把左邊的移除
      this.forms_2[0].push(this.left_data[0]);
      let delete_index = this.forms.indexOf(this.left_data[0]);
      this.forms.splice(delete_index, 1);
    },
    toLeft() {
      this.forms.push(this.right_data[0]);
      let delete_index = this.forms_2[0].indexOf(this.right_data[0]);
      this.forms_2[0].splice(delete_index, 1);
      this.forms.sort();
    },
    save(){
      let form_need = location.href.split("authority/");
      let form_id=form_need[1].split(".");
      var edit_user=this.forms_2[0];
      var ed=[];//資料庫被多包一層，拿掉的話就變成整支程式都要改
      ed.push(edit_user);
      console.log(ed);
      this.axios
        .put(
          "/rise_api/public/index.php/design/save_edit_user",
          {
            form_id: form_id[0],edit_user: JSON.stringify(ed)
          }
        )
        .then(response => {
          //console.log(response);
          //console.log(this.forms_2[0]);//右邊可編輯的值
          this.$swal({
          icon: "success",
          title: "儲存成功!",
        });
        })
    }


    /*save() {
      let form_need = location.href.split("authority/");
      let form_id=form_need[1].split(".");
      console.log("ssssss");
      console.log(form_id);
      console.log(this.forms_2[0]);
      this.axios
        .post(
          "/rise_api/public/index.php/admin_account/get_admin",
          { form_id: form_id[0] }//, edit_user: b
        )
        .then(response => {
          if (response.data == 1) {
          } else {
          }
        });
    }*/
  }
};
</script>

<style>
.final-score-block-wrapper {
  width: 100%;
  height: 100%;
  float: right;
  overflow-y: auto;
  overflow-x: auto;
}
.final-score-block {
  width: 100%;
  float: right;
}
.final-score-block {
  float: right;
  padding: 5px;
}
.final-score-level {
  position: relative;
  width: 100%;
  height: 200px;
  padding: 20px;
  overflow-y: auto;
  background-color: #eee;
  border-top: 2px dashed #bbb;
}
.final-score-level .newLevel {
  position: absolute;
  bottom: 10px;
  right: 10px;
}
.delete-level {
  color: #dc3545;
}
</style>



