<template>
  <div class="form-group row">
    <dyna-title
      :columnDisplay="addressColumn.columnDisplay"
      :columnName="addressColumn.columnName"
      :columnNullable="addressColumn.columnNullable"
    />
    <!-- city column -->
    <div class="form-inline col-lg-10">
      <select
        class="form-control address-select"
        :name="cityColumn.columnName"
        :id="cityColumn.columnName"
        :key="cityColumn.columnName"
        v-model="cityId"
      >
        <option
          value="0"
          :selected="getCitySelected(0)"
        >
          請選擇
        </option>
        <option
          v-for="cityItem in cityCode"
          :key="cityItem.id"
          :selected="getCitySelected(cityItem.id)"
          :value="cityItem.id"
        >
          {{ cityItem.name }}
        </option>
      </select>
      &nbsp;
      <select
        class="form-control address-select"
        :name="districtColumn.columnName"
        :id="districtColumn.columnName"
        :key="districtColumn.columnName"
        v-model="districtId"
      >
        <option
          value="0"
          :selected="getDistrictSelected(0)"
        >
          請選擇
        </option>
        <option
          v-for="districtItem in districtItems"
          :value="districtItem.id"
          :key="districtItem.id"
          :selected="getDistrictSelected(districtItem.id)"
        >
          {{ districtItem.name }}
        </option>
      </select>
      &nbsp;
      <input
        type="text"
        class="form-control address-content"
        :id="addressColumn.columnName"
        :name="addressColumn.columnName"
        :size="addressColumn.columnLength"
        :placeholder="addressColumn.columnPlaceholder"
        v-model="addressContent"
      />
      &nbsp;
      <dyna-helper
        :helper="helper"
        :callback="callback"
      />
    </div>
  </div>
</template>

<script>
import _ from 'lodash';

import DynaTitle from '../DynaTitle/index.vue';
import DynaHelper from '../DynaHelper/index.vue';

import { errorHandler, fetchColumnRefPromise } from '../../utils/api';
import { getDefaultHelper } from '../../utils/form';

const REF_CITY_CODE = 'city_code';
const REF_DISTRICT_CODE = 'district_code';

export default {
  props: {
    formName: String,
    cityColumn: Object,
    districtColumn: Object,
    addressColumn: Object,
    cityUpdater: Function,
    districtUpdater: Function,
    addressUpdater: Function,
  },
  components: { DynaTitle, DynaHelper },
  data() {
    return {
      cityCode: [],
      districtCode: [],
      helper: getDefaultHelper(),
      addressContent_: '',
      cityId_: null,
      districtId_: null,
    };
  },
  created() {
    const self = this;

    fetchColumnRefPromise(
      REF_CITY_CODE,
      (response) => {
        const { data } = response;
        self.cityCode = data;
      },
      errorHandler
    );
    fetchColumnRefPromise(
      REF_DISTRICT_CODE,
      (response) => {
        const { data } = response;
        self.districtCode = data;
      },
      errorHandler
    );

    this.addressContent_ = this.addressColumn.columnValue;
    this.cityId_ = this.cityColumn.columnValue;
    this.districtId_ = this.districtColumn.columnValue;

    this.helper = this.addressColumn.helper;
  },
  computed: {
    addressContent: {
      get() {
        return !!this.addressContent_
          ? this.addressContent_
          : this.addressColumn.columnValue;
      },
      set(val) {
        this.addressContent_ = val;
        this.addressUpdater(val);
        this.$emit('updated', val);
      },
    },
    cityId: {
      get() {
        return !!this.cityId_ ? this.cityId_ : this.cityColumn.columnValue;
      },
      set(val) {
        this.cityId_ = val;
        this.cityUpdater(val);
        this.postUpdateCityId(val);
        this.$emit('updated', val);
      },
    },
    districtId: {
      get() {
        return !!this.districtId_
          ? this.districtId_
          : this.districtColumn.columnValue;
      },
      set(val) {
        this.districtId_ = val;
        this.districtUpdater(val);
        this.$emit('updated', val);
      },
    },
    districtItems: {
      get() {
        return this.districtCode.filter(
          (district) => district.city_id === this.cityId
        );
      },
      set(val) {
        this.$emit('updated', val);
      },
    },
  },
  methods: {
    postUpdateCityId(cityId) {
      this.districtItems = this.getDistrictItems(cityId);
    },
    getDistrictItems(cityId) {
      return this.districtCode.filter(
        (district) => district.city_id === cityId
      );
    },
    callback(addressText) {
      const sAddressText = addressText.trim().replace('台', '臺');

      const cityCode = this.parseCity(sAddressText);

      if (_.isNil(cityCode)) {
        this.$swal({
          icon: 'error',
          title: 'Oops...',
          text: `工商查詢的地址格式有誤：${addressText}，請直接輸入`,
        });
      } else {
        const districtCode = this.parseDistrict(cityCode, addressText);

        if (_.isNil(districtCode)) {
          this.$swal({
            icon: 'error',
            title: 'Oops...',
            text: `工商查詢的地址格式有誤：${addressText}，請直接輸入`,
          });
        } else {
          const addressContent = this.parseAddressContent(
            cityCode,
            districtCode,
            sAddressText
          );

          console.log(
            'parsed: city: ',
            cityCode.name,
            ', district: ',
            districtCode.name,
            ', address: ',
            addressContent
          );

          this.cityId = cityCode.id;
          this.districtId = districtCode.id;
          this.addressContent = addressContent;
        }
      }
    },
    parseCity(addressText) {
      const addressCityName = addressText.substring(0, 3);

      return this.cityCode.find((city) => city.name === addressCityName);
    },
    parseDistrict(cityCode, addressText) {
      const cityDistricts = this.districtCode.filter(
        (district) => district.city_id === cityCode.id
      );

      return cityDistricts.find((district) => {
        const districtLength = district.name.length;
        const districtStart = 3;
        const addressDistrict = addressText.substring(
          districtStart,
          districtStart + districtLength
        );

        return district.name === addressDistrict;
      });
    },
    parseAddressContent(cityCode, districtCode, addressText) {
      const cityDistLength = cityCode.name.length + districtCode.name.length;
      const addressLength = addressText.length;

      return addressText.substring(cityDistLength, addressLength);
    },
    getCitySelected(cityId) {
      return this.cityId === cityId;
    },
    getDistrictSelected(districtId) {
      return this.districtId === districtId;
    },
  },
};
</script>

<style>
.address-select {
  width: 120px !important;
}

.address-content {
  width: 480px !important;
}
</style>



