import _ from 'lodash';

import { getEchoMan, getTrueMan } from './lang';

const INPUT_LENGTH_MAPPING = {
  10: 50,
  50: 100,
  100: 200,
  250: 500,
  500: 1000,
};

export const PAGE_SIZE = 10;

export function getInputLength(columnType, columnLength) {
  if (columnType === 'string' && !_.isNil(columnLength)) {
    return parseInt(columnLength);
  } else {
    return 500;
  }
}

export function getDefaultHelper() {
  return {
    title: '',
    func: null,
  };
}

export function buildFormColumn(specColumn, value) {
  return {
    columnName: specColumn.column_name,
    columnDisplay: specColumn.column_display,
    columnComponent: specColumn.column_component,
    columnDataType: specColumn.column_data_type,
    columnLength: getInputLength(
      specColumn.column_data_type,
      specColumn.column_length
    ),
    columnNullable: specColumn.column_nullable,
    columnEnabled: specColumn.column_enabled,
    columnQueriable: specColumn.column_queriable,
    columnRef: specColumn.column_ref,
    columnValue: value,
    columnPlaceholder: specColumn.column_placeholder,
    needVerify: !!specColumn.column_verify,
    needTransform: !!specColumn.column_script,
    isAddressGroup: false,
    isCompanyGroup: false,
    verifier: getTrueMan(),
    transformer: getEchoMan(),
    updater: getEchoMan(),
    helper: getDefaultHelper(),
  };
}

export function getDefaultValue(columnSpec) {
  const { columnDataType } = columnSpec;
  switch (columnDataType) {
    case 'number':
    case 'int':
    case 'double':
      return 0;
    case 'date':
    case 'datetime':
      return new Date();
    default:
      return '';
  }
}

export const TYPE_CREATE = '新增';
export const TYPE_UPDATE = '編輯';



