<template>
  <span v-if="needVerify">
    <button
      type="button"
      class="btn btn-success"
      @click="runVerifier"
    >
      驗證
    </button>
    <span class="valid">{{ validMsg }}</span>
    <span class="alert">{{ alertMsg }}</span>
  </span>
</template>

<script>
export default {
  props: {
    valueGetter: Function,
    needVerify: Boolean,
    verifier: Function,
    columnDisplay: String,
  },
  data() {
    return {
      alertMsg: '',
      validMsg: '',
    };
  },
  created() {},
  methods: {
    runVerifier() {
      const value = this.valueGetter.call(null);
      console.log('run verifier for value: ', value);

      new Promise((resolve) => {
        const verifyResult = this.verifier(value);
        resolve(verifyResult);
      }).then((verifyResult) => {
        if (!verifyResult) {
          this.alertMsg = `${this.columnDisplay} 驗證失敗`;
          this.validMsg = '';
        } else {
          this.validMsg = `${this.columnDisplay} 驗證成功`;
          this.alertMsg = '';
        }
      });
    },
  },
};
</script>

<style>
.alert {
  color: red;
  font-size: 0.8em;
}

.valid {
  color: green;
  font-size: 0.8em;
}
</style>



