<template>
  <div class="form-group row">
    <dyna-title
      :columnDisplay="columnDisplay"
      :columnName="columnName"
      :columnNullable="columnNullable"
    />

    <div class="form-inline col-lg-10">
      <input
        class="form-control"
        accept="application/pdf,image/*"
        type="file"
        :id="columnName"
        :name="columnName"
        @change="uploadFile"
      />
    </div>
  </div>
</template>

<script>
import DynaTitle from '../DynaTitle/index.vue';

import { getEchoMan } from '../../utils/lang';

export default {
  props: {
    columnSpec: Object,
    formName: String,
  },
  components: { DynaTitle },
  data() {
    return {
      columnName: '',
      columnDisplay: '',
      columnComponent: '',
      columnNullable: '',
      columnValue: '',

      needTransform: false,

      transformer: getEchoMan(),
      updater: getEchoMan(),

      files: [],
    };
  },
  created() {
    const columnSpec = this.columnSpec;

    this.columnName = columnSpec.columnName;
    this.columnDisplay = columnSpec.columnDisplay;
    this.columnComponent = columnSpec.columnComponent;
    this.columnNullable = columnSpec.columnNullable;
    this.columnValue = columnSpec.columnValue;

    this.needTransform = columnSpec.needTransform;

    this.transformer = columnSpec.transformer;
    this.updater = columnSpec.updater;
  },
  computed: {},
  methods: {
    uploadFile(event) {
      console.log('column name: ', event.target.name);
      console.log('files: ', event.target.files);

      const [first, ...rest] = event.target.files;
      this.updater(first);
    },
  },
};
</script>

<style></style>



