import A from 'axios';

export const TYPE_RISE_API = 'rise_api';
export const TYPE_DYNA_FORM_API = 'dyna_form_api';

const FORM_NAME_MAPPING = {
  company: 'company_basic',
  test: 'test_table',
  startup: 'startup_basic',
};

export function buildApiPrefix(apiType, apiRoute) {
  return `/${apiType}/public/index.php${apiRoute}`;
}

export function getFormNameByKey(formKey) {
  return FORM_NAME_MAPPING[formKey];
}

export function errorHandler(error) {
  console.error(error);
}

export function fetchColumnRefPromise(tableName, respHandler, errorHandler) {
  A(buildApiPrefix(TYPE_DYNA_FORM_API, `/form-content/${tableName}/all`), {
    method: 'GET',
  })
    .then(respHandler)
    .catch(errorHandler);
}

export function fetchFormSpecPromise(formName, respHandler, errorHandler) {
  A(buildApiPrefix(TYPE_DYNA_FORM_API, '/dyna-form/spec'), {
    method: 'GET',
    params: {
      form: formName,
    },
  })
    .then(respHandler)
    .catch(errorHandler);
}

export function fetchFormValuePromise(
  formName,
  formId,
  respHandler,
  errorHandler
) {
  A(
    buildApiPrefix(TYPE_DYNA_FORM_API, `/form-content/${formName}/${formId}`, {
      method: 'GET',
    })
  )
    .then(respHandler)
    .catch(errorHandler);
}

export function fetchColumnVerifierPromise(
  formName,
  columnName,
  respHandler,
  errorHandler
) {
  A(buildApiPrefix(TYPE_DYNA_FORM_API, '/dyna-form/verify'), {
    method: 'GET',
    params: {
      form: formName,
      column: columnName,
    },
  })
    .then(respHandler)
    .catch(errorHandler);
}

export function fetchColumnTransformerPromise(
  formName,
  columnName,
  respHandler,
  errorHandler
) {
  A(buildApiPrefix(TYPE_DYNA_FORM_API, '/dyna-form/script'), {
    method: 'GET',
    params: {
      form: formName,
      column: columnName,
    },
  })
    .then(respHandler)
    .catch(errorHandler);
}

export function saveFormPromise(formName, formData, respHandler, errorHandler) {
  A(buildApiPrefix(TYPE_DYNA_FORM_API, `/form-content/save/${formName}`), {
    method: 'POST',
    data: formData,
  })
    .then(respHandler)
    .catch(errorHandler);
}

export function updateFormPromise(
  formName,
  formId,
  formData,
  respHandler,
  errorHandler
) {
  A(
    buildApiPrefix(
      TYPE_DYNA_FORM_API,
      `/form-content/update/${formName}/${formId}`
    ),
    {
      method: 'POST',
      data: formData,
    }
  )
    .then(respHandler)
    .catch(errorHandler);
}

export function fetchFormContentCountPromise(
  formName,
  query,
  respHandler,
  errorHandler
) {
  A(buildApiPrefix(TYPE_DYNA_FORM_API, `/form-content/${formName}/count`), {
    method: 'GET',
    params: {
      query,
    },
  })
    .then(respHandler)
    .catch(errorHandler);
}

export function fetchListSpecPromise(formName, respHandler, errorHandler) {
  A(buildApiPrefix(TYPE_DYNA_FORM_API, `/form-content/list-spec/${formName}`), {
    method: 'GET',
  })
    .then(respHandler)
    .catch(errorHandler);
}

export function fetchFormContentListPromise(
  formName,
  query,
  respHandler,
  errorHandler
) {
  A(buildApiPrefix(TYPE_DYNA_FORM_API, `/form-content/${formName}/list`), {
    method: 'GET',
    params: {
      query,
    },
  })
    .then(respHandler)
    .catch(errorHandler);
}

export async function checkCompanyExists(companyNum) {
  /* 呼叫後端服務 */
  const verifyUrl = `/dyna_form_api/public/index.php/form-content/check-company-exists`;

  try {
    const getResponse = await A.get(verifyUrl, {
      params: {
        'company-num': companyNum,
      },
    });

    const { data } = getResponse;
    const { result } = data;

    return result;
  } catch (error) {
    errorHandler(error);
    return false;
  }
}

export async function deleteContentById(
  formName,
  itemId,
  respHandler,
  errorHandler
) {
  A(buildApiPrefix(TYPE_DYNA_FORM_API, `/form-content/${formName}/${itemId}`), {
    method: 'DELETE',
  })
    .then(respHandler)
    .catch(errorHandler);
}



