<template>
  <div
    class="form-group row"
    v-if="isIndustryType"
  >
    <dyna-title
      :columnDisplay="columnDisplay"
      :columnName="columnName"
      :columnNullable="columnNullable"
    />
    <div class="form-inline col-lg-10">
      <select
        class="form-control industry-level-1"
        id="industryLevel1"
        key="industryLevel1"
        name="industryLevel1"
        v-model="industryId1"
      >
        <option
          value="0"
          :key="level1Key(0)"
          :selected="level1Selected(0)"
        >
          請選擇
        </option>
        <option
          v-for="selectItem in industryLevel1"
          :value="selectItem.id"
          :key="level1Key(selectItem.id)"
          :selected="level1Selected(selectItem.id)"
        >
          {{ selectItem.code }} - {{ selectItem.name }}
        </option>
      </select>
      &nbsp;
      <select
        class="form-control industry-level-1"
        id="industryLevel2"
        name="industryLevel2"
        key="industryLevel2"
        v-model="industryId2"
      >
        <option
          value="0"
          :key="level2Key(0)"
          :selected="level2Selected(0)"
        >
          請選擇
        </option>
        <option
          v-for="selectItem in industryLevel2"
          :value="selectItem.id"
          :key="level2Key(selectItem.id)"
          :selected="level2Selected(selectItem.id)"
        >
          {{ selectItem.code }} - {{ selectItem.name }}
        </option>
      </select>
      &nbsp;
      <select
        class="form-control industry-level-1"
        id="industryLevel3"
        name="industryLevel3"
        key="industryLevel3"
        v-model="industryId3"
      >
        <option
          value="0"
          :key="level3Key(0)"
          :selected="level3Selected(0)"
        >
          請選擇
        </option>
        <option
          v-for="selectItem in industryLevel3"
          :value="selectItem.id"
          :key="level3Key(selectItem.id)"
          :selected="level3Selected(selectItem.id)"
        >
          {{ selectItem.code }} - {{ selectItem.name }}
        </option>
      </select>
      &nbsp;
      <dyna-helper
        :helper="helper"
        :callback="industryCallback"
      />
    </div>
  </div>
  <div
    class="form-group row"
    v-else
  >
    <dyna-title
      :columnDisplay="columnDisplay"
      :columnName="columnName"
      :columnNullable="columnNullable"
    />

    <div class="form-inline col-lg-10">
      <select
        class="form-control normal-select"
        :name="columnName"
        :id="columnName"
        :key="columnName"
        v-model="columnValue"
      >
        <option
          value="0"
          key="0"
          :selected="getSelected(0)"
        >
          請選擇
        </option>
        <option
          v-for="selectItem in selectItems"
          :value="selectItem.id"
          :key="selectItem.id"
          :selected="getSelected(selectItem.id)"
        >
          {{ selectItem.name }}
        </option>
      </select>
      &nbsp;
      <dyna-helper
        :helper="helper"
        :callback="normalCallback"
      />
    </div>
  </div>
</template>

<script>
import DynaTitle from "../DynaTitle/index.vue";
import DynaHelper from "../DynaHelper/index.vue";

import { errorHandler, fetchColumnRefPromise } from "../../utils/api";
import { getEchoMan } from "../../utils/lang";
import { getDefaultHelper } from "../../utils/form";
import { INDUSTRY_ID_1_LENGTH, INDUSTRY_ID_2_LENGTH, INDUSTRY_ID_3_LENGTH } from "../../utils/business";

export default {
  props: {
    columnSpec: Object,
    formName: String,
  },
  components: { DynaTitle, DynaHelper },
  data() {
    return {
      columnName: '',
      columnDisplay: '',
      columnComponent: '',
      columnLength: '',
      columnNullable: '',
      columnRef: '',
      columnValue_: 0,

      updater: getEchoMan(),
      helper: getDefaultHelper(),

      selectItems: [],

      industryId1_: 0,
      industryId2_: 0,
      industryId3_: 0,

      industryLevel1_: [],
      industryLevel2_: [],
      industryLevel3_: [],
    };
  },
  computed: {
    industryLevel1: {
      get() {
        return !!this.selectItems
          ? this.selectItems.filter((item) => !item.parent_id)
          : [];
      },
      set(arr) {
        this.$emit('updated', arr);
      },
    },
    industryLevel2: {
      get() {
        return !!this.industryId1
          ? this.selectItems.filter(
              (item) => item.parent_id === this.industryId1
            )
          : [];
      },
      set(arr) {
        this.$emit('updated', arr);
      },
    },
    industryLevel3: {
      get() {
        return !!this.industryId2
          ? this.selectItems.filter(
              (item) => item.parent_id === this.industryId2
            )
          : [];
      },
      set(arr) {
        this.$emit('updated', arr);
      },
    },
    industryId1: {
      get() {
        if (!!this.industryId1_) {
          return this.industryId1_;
        } else if (!!this.industryId3) {
          return this.getIndustryId1From3(this.industryId3);
        } else {
          return 0;
        }
      },
      set(val) {
        this.industryId1_ = val;
        this.postUpdateIndustryLevel1(val);
        this.$emit('updated', val);
      },
    },
    industryId2: {
      get() {
        if (!!this.industryId2_) {
          return this.industryId2_;
        } else if (!!this.industryId3) {
          return this.getIndustryId2From3(this.industryId3);
        } else {
          return 0;
        }
      },
      set(val) {
        this.industryId2_ = val;
        this.postUpdateIndustryLevel2(val);
        this.$emit('updated', val);
      },
    },
    industryId3: {
      get() {
        return !!this.industryId3_
          ? this.industryId3_
          : this.columnSpec.columnValue;
      },
      set(val) {
        this.industryId3_ = val;
        this.updater(val);
        this.$emit('updated', val);
      },
    },
    isIndustryType() {
      return this.columnRef === 'industry_code';
    },
    isSelect() {
      return this.columnComponent === 'select';
    },
    isSingle() {
      return this.columnComponent === 'single';
    },
    isCheck() {
      return this.columnComponent === 'check';
    },
    columnValue: {
      get() {
        return !!this.columnValue_
          ? this.columnValue_
          : this.columnSpec.columnValue;
      },
      set(val) {
        this.updater(val);
        this.$emit('updated', val);
      },
    },
  },
  created() {
    const self = this;
    const columnSpec = this.columnSpec;

    this.columnName = columnSpec.columnName;
    this.columnDisplay = columnSpec.columnDisplay;
    this.columnComponent = columnSpec.columnComponent;
    this.columnLength = columnSpec.columnLength;
    this.columnNullable = columnSpec.columnNullable;
    this.columnRef = columnSpec.columnRef;

    this.industryId3_ = columnSpec.columnValue;
    this.columnValue_ = columnSpec.columnValue;

    this.updater = columnSpec.updater;
    this.helper = columnSpec.helper;

    fetchColumnRefPromise(
      this.columnRef,
      (response) => {
        const { data } = response;
        self.selectItems = data;
        console.log('select items: ', self.selectItems);
      },
      errorHandler
    );
  },
  methods: {
    getIndustryId1From3(industryId3) {
      const industryLevel3 = this.selectItems.find(
        (item) => item.id === industryId3
      );
      console.log('industry level 3: ', industryLevel3);

      if (!!industryLevel3) {
        const lv3Code = industryLevel3.code;
        const lv1Code = lv3Code.substring(0, INDUSTRY_ID_1_LENGTH);
        const industryLevel1 = this.selectItems.find(
          (item) => item.code === lv1Code
        );

        if (!!industryLevel1) {
          return industryLevel1.id;
        }
      }
    },
    getIndustryId2From3(industryId3) {
      const industryLevel3 = this.selectItems.find(
        (item) => item.id === industryId3
      );

      if (!!industryLevel3) {
        const lv3Code = industryLevel3.code;
        const lv2Code = lv3Code.substring(0, INDUSTRY_ID_2_LENGTH);
        const industryLevel2 = this.selectItems.find(
          (item) => item.code === lv2Code
        );

        if (!!industryLevel2) {
          return industryLevel2.id;
        }
      }
    },
    getValue() {
      return this.columnValue;
    },
    normalCallback(value) {
      this.columnValue = value;
    },
    industryCallback(value) {
      const codePart3 = value.substring(0, INDUSTRY_ID_3_LENGTH);
      const level3 = this.selectItems.find((item) => item.code === codePart3);

      const codePart2 = value.substring(0, INDUSTRY_ID_2_LENGTH);
      const level2 = this.selectItems.find((item) => item.code === codePart2);

      const codePart1 = value.substring(0, INDUSTRY_ID_1_LENGTH);
      const level1 = this.selectItems.find((item) => item.code === codePart1);

      console.log(
        `code part 1 ${codePart1}, part 2 ${codePart2}, part 3 ${codePart3}`
      );

      if (!!level3 && !!level2 && !!level1) {
        this.industryId1 = level1.id;
        this.industryId2 = level2.id;
        this.industryId3 = level3.id;
      } else {
        this.$swal({
          icon: 'warning',
          title: 'Hey!',
          text: `確認回傳值: ${codePart3}`,
        });
      }
    },
    postUpdateIndustryLevel1(value) {
      this.industryLevel2 = this.selectItems.filter(
        (item) => item.parent_id === parseInt(value)
      );
    },
    postUpdateIndustryLevel2(value) {
      this.industryLevel3 = this.selectItems.filter(
        (item) => item.parent_id === parseInt(value)
      );
    },
    getSelected(id) {
      if (parseInt(this.columnValue) === parseInt(id)) {
        return 'selected';
      } else {
        return '';
      }
    },
    level1Key(id) {
      return `1-${id}`;
    },
    level1Selected(id) {
      if (this.industryId1 === id) {
        return 'selected';
      } else {
        return '';
      }
    },
    level2Key(id) {
      return `2-${id}`;
    },
    level2Selected(id) {
      if (this.industryId2 === id) {
        return 'selected';
      } else {
        return '';
      }
    },
    level3Key(id) {
      return `3-${id}`;
    },
    level3Selected(id) {
      if (this.industryId3 === id) {
        return 'selected';
      } else {
        return '';
      }
    },
  },
};
</script>

<style>
.industry-level-1 {
  width: 240px !important;
}

.normal-select {
  width: 240px !important;
}
</style>



