<template>
  <div class="container-fluid pb-3">
    <form
      enctype="multipart/form-data"
      novalidate
    >
      <div class="row">
        <div class="col-lg-10">
          <h2 class="title">{{ formSpec.table_display }} - {{ editType }}</h2>
        </div>
        <div class="col-lg-2 d-flex justify-content-center align-items-end">
          <button
            type="button"
            class="btn btn-primary"
            @click="saveModel"
          >
            儲存
          </button>
          &nbsp;
          <router-link
            :to="`/dyna/company/list`"
            replace
          >
            <button
              type="button"
              class="btn btn-secondary"
            >
              取消
            </button>
          </router-link>
        </div>
      </div>
      <hr />
      <div :key="componentKey">
        <div
          v-for="columnSpec in formColumns"
          :key="columnSpec.columnName"
        >
          <!-- 地址欄位 -->
          <div
            v-if="
              hasAddress() &&
              columnSpec.isAddressGroup &&
              !isCity(columnSpec) &&
              !isDistrict(columnSpec)
            "
          >
            <dyna-address
              :address-column="addressGroup.address"
              :address-updater="addressGroup.address.updater"
              :city-column="addressGroup.city"
              :city-updater="addressGroup.city.updater"
              :district-column="addressGroup.district"
              :district-updater="addressGroup.district.updater"
            />
          </div>
          <!-- 非地址欄位 -->
          <div v-else-if="!columnSpec.isAddressGroup">
            <div
              v-if="
                isText(columnSpec) ||
                isMultiline(columnSpec) ||
                isRich(columnSpec) ||
                isPhone(columnSpec) ||
                isEmail(columnSpec) ||
                isCode(columnSpec)
              "
            >
              <div v-if="isCapital(columnSpec)">
                <dyna-text
                  :column-spec="columnSpec"
                  :form-name="formName"
                />
              </div>
              <div v-else>
                <dyna-text
                  :column-spec="columnSpec"
                  :form-name="formName"
                />
              </div>
            </div>
            <div v-else-if="isNumber(columnSpec) || isMoney(columnSpec)">
              <dyna-number
                :column-spec="columnSpec"
                :form-name="formName"
              />
            </div>
            <div v-else-if="isDate(columnSpec) || isDateTime(columnSpec)">
              <dyna-date
                :column-spec="columnSpec"
                :form-name="formName"
              />
            </div>
            <div
              v-else-if="
                isSelect(columnSpec) ||
                isSingle(columnSpec) ||
                isCheck(columnSpec)
              "
            >
              <dyna-select
                :column-spec="columnSpec"
                :form-name="formName"
              />
            </div>
            <div v-else-if="isFile(columnSpec)">
              <dyna-file
                :column-spec="columnSpec"
                :form-name="formName"
              />
            </div>
            <div v-else>
              <span>
                {{ columnSpec.columnDisplay }}:
                {{ columnSpec.columnComponent }}
              </span>
            </div>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import _ from 'lodash';
import $ from 'jquery';
import dayjs from 'dayjs';

import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';

import DynaFile from '../../components/DynaFile/index.vue';
import DynaSelect from '../../components/DynaSelect/index.vue';
import DynaDate from '../../components/DynaDate/index.vue';
import DynaText from '../../components/DynaText/index.vue';
import DynaNumber from '../../components/DynaNumber/index.vue';
import DynaAddress from '../../components/DynaAddress/index.vue';

import {
  checkCompanyExists,
  errorHandler,
  fetchColumnTransformerPromise,
  fetchColumnVerifierPromise,
  fetchFormSpecPromise,
  fetchFormValuePromise,
  getFormNameByKey,
  saveFormPromise,
  updateFormPromise,
} from '../../utils/api';

import {
  buildPredicator,
  buildTransformer,
  isEmptyValue,
  snakeToCamel,
} from '../../utils/lang';

import {
  buildFormColumn,
  getDefaultValue,
  TYPE_CREATE,
  TYPE_UPDATE,
} from '../../utils/form';

import {
  getCompanyInfo,
  getCompanyType,
  verifyCompanyNum,
} from '../../utils/business';

const VM = {
  props: [],
  components: {
    DatePicker,
    DynaText,
    DynaNumber,
    DynaAddress,
    DynaDate,
    DynaSelect,
    DynaFile,
  },
  data() {
    return {
      componentKey: 1,
      formKey: 'company',
      formName: '',
      formId: -1,
      formSpec: {},
      formColumns: [],
      formModel: {},
      dateGroup: {},
      companyTypeCode: [],
      cityCode: [],
      districtCode: [],
      industryCode: [],
      districtItems: [],
      editType: TYPE_CREATE,
      addressGroup: {},
      selectItemGroups: {},
    };
  },
  mounted() {
    $('#wrapper').addClass('toggled');
  },
  computed: {},
  created() {
    const self = this;

    self.formId = self.$route.query.id;
    self.editType = parseInt(self.formId) === -1 ? TYPE_CREATE : TYPE_UPDATE;
    self.formName = getFormNameByKey(self.formKey);

    fetchFormSpecPromise(
      self.formName,
      (response) => {
        const spec = response.data;
        // console.log('form spec: ', spec);

        self.formSpec = spec;
        self.formColumns = buildColumns(spec.columns);
        self.initFormColumns();

        if (self.editType === TYPE_UPDATE) {
          self.initFormValues();
        }
      },
      errorHandler
    );
  },
  methods: {
    fetchCompanyInfo() {
      this.$store.dispatch('dynaCompany/fetch_company_info');
    },
    // 重繪元件
    rerender() {
      this.componentKey += 1;
      this.$forceUpdate();
    },
    // 初始化 column spec
    initFormColumns() {
      const self = this;

      _.forEach(this.formColumns, (columnSpec) => {
        // init verifier
        initVerifier(self.formName, columnSpec);
        initCompanyNumVerifier(self, columnSpec);

        // init transformer
        initTransformer(self.formName, columnSpec);

        // init special items
        initDateItems(self, columnSpec);
        initAddressItem(self, columnSpec);

        // init value
        initModelUpdater(self, columnSpec);
        initModelValue(self, columnSpec);

        // init helper
        initCompanyNumHelper(self, columnSpec);
        // initCompanyHeadHelper(self, columnSpec);
        // initCapitalHelper(self, columnSpec);
        // initCompanyNameHelper(self, columnSpec);
        // initEstablishedHelper(self, columnSpec);
        initAddressHelper(self, columnSpec);
        initIndustryTypeHelper(self, columnSpec);
      });

      // init city and district items
      initCityDistItems(self);
    },
    initFormValues() {
      const self = this;

      fetchFormValuePromise(
        this.formName,
        this.formId,
        (response) => {
          const { data } = response;
          const [formValue, ..._] = data;
          // console.log(formValue);

          self.formColumns.forEach((columnSpec) => {
            const { columnName } = columnSpec;
            const columnValue = formValue[columnName];

            columnSpec.columnValue = columnValue;

            self.formModel[columnName] = columnValue;
          });

          console.log('update column: ', self.formColumns);
          console.log('update form model: ', self.formModel);
        },
        errorHandler
      );
    },
    hasAddress() {
      return !_.isEmpty(this.addressGroup);
    },
    isCity(columnSpec) {
      const is_city =
        columnSpec.isAddressGroup &&
        columnSpec.columnName === this.addressGroup['city'].columnName;
      console.log(
        'is city: ',
        is_city,
        ' with column: ',
        columnSpec.columnName
      );
      return is_city;
    },
    isDistrict(columnSpec) {
      return (
        columnSpec.isAddressGroup &&
        columnSpec.columnName === this.addressGroup['district'].columnName
      );
    },
    isText(columnSpec) {
      return columnSpec.columnComponent === 'text';
    },
    isEmail(columnSpec) {
      return columnSpec.columnComponent === 'email';
    },
    isRich(columnSpec) {
      return columnSpec.columnComponent === 'rich';
    },
    isMultiline(columnSpec) {
      return columnSpec.columnComponent === 'multiline';
    },
    isCode(columnSpec) {
      return columnSpec.columnComponent === 'code';
    },
    isAddress(columnSpec) {
      return columnSpec.columnComponent === 'address';
    },
    isNumber(columnSpec) {
      return columnSpec.columnComponent === 'number';
    },
    isMoney(columnSpec) {
      return columnSpec.columnComponent === 'money';
    },
    isPhone(columnSpec) {
      return columnSpec.columnComponent === 'phone';
    },
    isDate(columnSpec) {
      return columnSpec.columnComponent === 'date';
    },
    isDateTime(columnSpec) {
      return columnSpec.columnComponent === 'datetime';
    },
    isSelect(columnSpec) {
      return columnSpec.columnComponent === 'select';
    },
    isSingle(columnSpec) {
      return columnSpec.columnComponent === 'single';
    },
    isCheck(columnSpec) {
      return columnSpec.columnComponent === 'check';
    },
    isFile(columnSpec) {
      return columnSpec.columnComponent === 'file';
    },
    isCapital(columnSpec) {
      return columnSpec.columnName === 'capital';
    },
    updateModel(event) {
      const columnName = event.target.id;
      const value = event.target.value;
      // console.log(`update column: ${columnName} with value ${value}`);

      this._updateModel(columnName, value);
    },
    _updateColumnValue(columnName, value) {
      console.log(`update ${columnName} with ${value}`);

      this.formModel[columnName] = value;
      const columnSpec = this.formColumns.find(
        (columnSpec) => columnSpec.columnName === columnName
      );
      columnSpec.columnValue = value;
    },
    _updateModel(columnName, value) {
      console.log(`update ${columnName} with ${value}`);
      this.formModel[columnName] = value;
      // console.log('updated model: ', this.formModel);
    },
    verifyModel() {
      const self = this;

      console.log('verify model: ', self.formModel);

      const nonNullableColumns = self.formColumns.filter(
        (columnSpec) => !columnSpec.columnNullable
      );

      console.log('non nullable columns: ', nonNullableColumns);

      let result = true;

      nonNullableColumns.forEach((columnSpec) => {
        const formValue = self.formModel[columnSpec.columnName];

        console.log('verify non nullable column: ', columnSpec.columnName);
        console.log('verify form value: ', formValue);

        if (isEmptyValue(formValue)) {
          result = false;
        }
      });

      return result;
    },
    initFormData(formData) {
      const self = this;

      _.forOwn(this.formModel, (value, key) => {
        const columnSpec = self.formColumns.find(
          (col) => col.columnName === key
        );

        if (!!columnSpec) {
          const { columnComponent } = columnSpec;

          switch (columnComponent) {
            case 'date':
              formData.append(key, dayjs(value).format('YYYY-MM-DD'));
              break;
            default:
              formData.append(key, value);
          }
        }
      });
    },
    isCreate() {
      return this.editType === TYPE_CREATE;
    },
    isUpdate() {
      return this.editType === TYPE_UPDATE;
    },
    createOrUpdate(formData) {
      if (this.isCreate()) {
        saveFormPromise(
          this.formName,
          formData,
          this.handleSaveResponse,
          errorHandler
        );
      } else if (this.isUpdate()) {
        updateFormPromise(
          this.formName,
          this.formId,
          formData,
          this.handleSaveResponse,
          errorHandler
        );
      }
    },
    saveModel() {
      console.log('save model: ', this.formModel);
      if (!this.verifyModel()) {
        this.$swal({
          icon: 'error',
          title: 'Oops!',
          text: '須輸入所有的必填欄位',
        });
      } else {
        const formData = new FormData();

        this.initFormData(formData);
        console.log('form data: ', formData);

        this.createOrUpdate(formData);
      }
    },
    handleSaveResponse(resp) {
      console.log('response: ', resp);

      const { data } = resp;
      console.log('data: ', data);

      if (data.result) {
        this.$swal({
          icon: 'success',
          title: 'OK!',
          text: '儲存成功',
        });
      } else {
        this.$swal({
          icon: 'error',
          title: 'Oops...',
          text: data.message,
        });
      }
    },
    cleanModel(event) {
      if (this.editType === TYPE_CREATE) {
        this.cleanAll();
      } else {
        this.resetAll();
      }
    },
    cleanAll() {
      const self = this;

      self.formColumns.forEach((column) => {
        self.updateModel(column.columnName, '');
      });
    },
    resetAll() {
      // 開發新增功能時，先做 clean all.
      this.cleanAll();
    },
    _snakeToCamel(snake) {
      return snakeToCamel(snake);
    },
    getSelectItems(key) {
      return this[key];
    },
  },
};

// 將 API 的 form spec 轉為 UI 的 column spec
function buildColumns(specColumns) {
  if (!specColumns) {
    return;
  }

  const formColumns = specColumns
    .sort((aCol, bCol) => aCol.column_order - bCol.column_order)
    .map((specColumn) => buildFormColumn(specColumn, null));

  return formColumns;
}

// 初始化 form model 的值
function initModelValue(self, columnSpec) {
  self.formModel[columnSpec.columnName] =
    columnSpec.columnValue || getDefaultValue(columnSpec);
}

// 初始化 form model 更新程式
function initModelUpdater(self, columnSpec) {
  columnSpec.updater = _.partial(self._updateModel, columnSpec.columnName);
}

function initHelperFromCompanyInfo(self, columnSpec, checkFunc, apiKey) {
  if (checkFunc(columnSpec)) {
    columnSpec.helper = {
      title: '工商查詢',
      func: async function (callback) {
        const { company_num } = self.formModel;

        console.log('check company info of ', company_num);

        if (!_.isEmpty(company_num)) {
          const info = await getCompanyInfo(company_num);

          if (!_.isEmpty(info)) {
            const value = info[apiKey];

            callback.call(null, value);
          } else {
            self.$swal({
              icon: 'error',
              title: 'Oops...',
              text: '工商查詢失敗，請確認API回傳情形',
            });
          }
        } else {
          self.$swal({
            icon: 'warning',
            title: 'Hey!',
            text: '請先輸入統編',
          });
        }
      },
    };
  }
}

function isCompanyNumColumn(columnSpec) {
  return columnSpec.columnName === 'company_num';
}

function initCompanyNumVerifier(self, columnSpec) {
  if (isCompanyNumColumn(columnSpec)) {
    const origVerifier = columnSpec.verifier;

    columnSpec.verifier = async (companyNum) => {
      console.log('verify company num: ', companyNum);

      const origResult = origVerifier(companyNum); // 驗證格式是否正確

      self.$store.commit(`dynaCompany/set_company_num`, {
        company_num: companyNum,
      });

      if (origResult) {
        const checkResult = await checkCompanyExists(companyNum); // 驗證是否已存在

        if (!checkResult) {
          const verifyResult = await verifyCompanyNum(companyNum); // 驗證工商資訊

          return verifyResult;
        } else {
          return false;
        }
      } else {
        return false;
      }
    };
  }
}

function isCompanyHeadColumn(columnSpec) {
  return columnSpec.columnName === 'company_head';
}

function initCompanyNumHelper(self, columnSpec) {
  if (isCompanyNumColumn(columnSpec)) {
    columnSpec.helper = {
      title: '工商查詢',
      func: function (callback) {
        const { company_num } = self.formModel;

        console.log('check company info of ', company_num);
        if (!_.isEmpty(company_num)) {
          self.$store.dispatch('dynaCompany/fetch_company_info', {
            modelUpdater: self._updateColumnValue,
          });
        } else {
          self.$swal({
            icon: 'warning',
            title: 'Hey!',
            text: '請先輸入統編',
          });
        }
      },
    };
  }
}

function initCompanyHeadHelper(self, columnSpec) {
  initHelperFromCompanyInfo(
    self,
    columnSpec,
    isCompanyHeadColumn,
    'Responsible_Name'
  );
}

function initCompanyHeadHelperOrig(self, columnSpec) {
  if (isCompanyHeadColumn(columnSpec)) {
    columnSpec.helper = {
      title: '工商查詢',
      func: async function (callback) {
        const { company_num } = self.formModel;

        console.log('check company info of ', company_num);

        if (!_.isEmpty(company_num)) {
          const info = await getCompanyInfo(company_num);

          if (!_.isEmpty(info)) {
            const { Responsible_Name } = info;

            callback.call(null, Responsible_Name);
          } else {
            self.$swal({
              icon: 'error',
              title: 'Oops...',
              text: '工商查詢失敗，請確認API回傳情形',
            });
          }
        } else {
          self.$swal({
            icon: 'warning',
            title: 'Hey!',
            text: '請先輸入統編',
          });
        }
      },
    };
  }
}

function isCompanyNameColumn(columnSpec) {
  return columnSpec.columnName === 'company_cname';
}

function initCompanyNameHelper(self, columnSpec) {
  initHelperFromCompanyInfo(
    self,
    columnSpec,
    isCompanyNameColumn,
    'Company_Name'
  );
}

function initCompanyNameHelperOrig(self, columnSpec) {
  if (isCompanyNameColumn(columnSpec)) {
    columnSpec.helper = {
      title: '工商查詢',
      func: async function (callback) {
        const { company_num } = self.formModel;

        console.log('check company info of ', company_num);

        if (!_.isEmpty(company_num)) {
          const info = await getCompanyInfo(company_num);

          if (!_.isEmpty(info)) {
            const { Company_Name } = info;

            callback.call(null, Company_Name);
          } else {
            self.$swal({
              icon: 'error',
              title: 'Oops...',
              text: '工商查詢失敗，請確認API回傳情形',
            });
          }
        } else {
          self.$swal({
            icon: 'warning',
            title: 'Hey!',
            text: '請先輸入統編',
          });
        }
      },
    };
  }
}

function isEstablishedColumn(columnSpec) {
  return columnSpec.columnName === 'established';
}

function initEstablishedHelper(self, columnSpec) {
  initHelperFromCompanyInfo(
    self,
    columnSpec,
    isEstablishedColumn,
    'Company_Setup_Date'
  );
}

function isCapitalColumn(columnSpec) {
  return columnSpec.columnName === 'capital';
}

function initCapitalHelper(self, columnSpec) {
  initHelperFromCompanyInfo(
    self,
    columnSpec,
    isCapitalColumn,
    'Capital_Stock_Amount'
  );
}

function initCapitalHelperOrig(self, columnSpec) {
  if (isCapitalColumn(columnSpec)) {
    columnSpec.helper = {
      title: '工商查詢',
      func: async function (callback) {
        const { company_num } = self.formModel;

        console.log('check company info of ', company_num);

        if (!_.isEmpty(company_num)) {
          const info = await getCompanyInfo(company_num);

          if (!_.isEmpty(info)) {
            const { Capital_Stock_Amount } = info;

            callback.call(null, Capital_Stock_Amount);
          } else {
            self.$swal({
              icon: 'error',
              title: 'Oops...',
              text: '工商查詢失敗，請確認API回傳情形',
            });
          }
        } else {
          self.$swal({
            icon: 'warning',
            title: 'Hey!',
            text: '請先輸入統編',
          });
        }
      },
    };
  }
}

function isAddressColumn(columnSpec) {
  return columnSpec.columnName === 'address';
}

function initAddressHelper(self, columnSpec) {
  initHelperFromCompanyInfo(
    self,
    columnSpec,
    isAddressColumn,
    'Company_Location'
  );
}

function isIndustryTypeColumn(columnSpec) {
  return columnSpec.columnName === 'industry_type_id';
}

function initIndustryTypeHelper(self, columnSpec) {
  if (isIndustryTypeColumn(columnSpec)) {
    columnSpec.helper = {
      title: '工商查詢',
      func: async function (callback) {
        const { company_num } = self.formModel;

        console.log('check company type of ', company_num);

        if (!_.isEmpty(company_num)) {
          const type = await getCompanyType(company_num);

          if (!_.isEmpty(type)) {
            const { Cmp_Business } = type;
            const industryTypes = Cmp_Business.map(
              (business) => business['Business_Item']
            );

            callback.call(null, industryTypes[0]);
          } else {
            self.$swal({
              icon: 'error',
              title: 'Oops...',
              text: '工商查詢失敗，請確認API回傳情形',
            });
          }
        } else {
          self.$swal({
            icon: 'warning',
            title: 'Hey!',
            text: '請先輸入統編',
          });
        }
      },
    };
  }
}

// 抓取所有要驗證格式的 code
function initVerifier(formName, columnSpec) {
  if (columnSpec.needVerify) {
    fetchColumnVerifierPromise(
      formName,
      columnSpec.columnName,
      (response) => {
        const script = response.data;
        columnSpec.verifier = buildPredicator(script);
      },
      errorHandler
    );
  }
}

// 抓取所有要轉換內容的 code
function initTransformer(formName, columnSpec) {
  if (columnSpec.needTransform) {
    fetchColumnTransformerPromise(
      formName,
      columnSpec.columnName,
      (response) => {
        const script = response.data;
        columnSpec.transformer = buildTransformer(script);
      },
      errorHandler
    );
  }
}

// 初始化日期欄位
function initDateItems(self, columnSpec) {
  if (self.isDate(columnSpec.columnComponent)) {
    self.dateGroup[columnSpec.columnName] = new Date();
  }
}

// 初始化地址欄位
function initAddressItem(self, columnSpec) {
  // 先將 address 納入 addressGroup 裡
  if (self.isAddress(columnSpec)) {
    console.log('is address of: ', columnSpec.columnName);
    self.addressGroup['address'] = columnSpec;
    columnSpec.isAddressGroup = true;
  }
}

// 初始化縣市及行政區欄位
function initCityDistItems(self) {
  // 將 city_id 及 district_id 納入 addressGroup 裡
  if (!_.isEmpty(self.addressGroup)) {
    self.formColumns.forEach((columnSpec) => {
      if (columnSpec.columnName === 'city_id') {
        self.addressGroup['city'] = columnSpec;
        columnSpec.isAddressGroup = true;
      } else if (columnSpec.columnName === 'district_id') {
        self.addressGroup['district'] = columnSpec;
        columnSpec.isAddressGroup = true;
      }
    });

    console.log('address group: ', self.addressGroup);
  }
}

export default VM;
</script>

<style>
.necessary {
  color: red;
}

.city {
  width: 100%;
}

.district {
  width: 100%;
}

address {
  width: 600px;
}

.text-10 {
  width: 150px;
}

.text-50 {
  width: 300px;
}

.text-100 {
  width: 600px;
}

.text-250 {
  width: 800px;
}

.text-500 {
  width: 1000px;
}
</style>



