<!--
  問卷外層樣式
  一般題目、題組、構面頁籤
-->
<template>
  <div class="container-fluid pb-3">
    <!-- Info header -->
    <nav class="navbar navbar-expand-md">
      <input class="navbar-brand" type="text" placeholder="題目名稱" v-model="form.name" />

      <div class="collapse navbar-collapse">
        <ul class="navbar-nav mr-auto">
          <span class="navbar-text">v{{ form.version }}</span>
        </ul>
        <div class="form-inline my-2 my-lg-0">
          <button class="btn btn-primary" style="margin-right: 10px" type="submit" @click="preview">
            預覽
          </button>
          <button class="btn btn-primary" type="submit" @click="updateForm">
            儲存
          </button>
        </div>
      </div>
    </nav>

    <hr class="m-1" />

    <!-- Tab content  -->
    <ul class="nav nav-tabs">
      <li class="nav-item">
        <a class="nav-link active mx-1" data-toggle="tab" href="#questions">一般題目</a>
      </li>
      <li class="nav-item">
        <a class="nav-link mx-1" data-toggle="tab" href="#groups">題組</a>
      </li>
      <li class="nav-item">
        <a class="nav-link mx-1" data-toggle="tab" href="#dimensions">構面</a>
      </li>
      <li class="nav-item">
        <a class="nav-link mx-1" data-toggle="tab" href="#finalscores">總評</a>
      </li>
      <li class="nav-item">
        <a class="nav-link mx-1" data-toggle="tab" href="#appreciation">感謝頁</a>
      </li>
      <li class="nav-item">
        <a class="nav-link mx-1" data-toggle="tab" href="#wording">字樣</a>
      </li>
    </ul>
    <br />
    <div class="tab-content">
      <question-content class="tab-pane fade show active" id="questions" :form="form"></question-content>
      <group-content class="tab-pane fade" id="groups" :form="form"></group-content>
      <dimension-content class="tab-pane fade" id="dimensions" :form="form"></dimension-content>
      <final-score-content class="tab-pane fade" v-if="form.appreciation_checkbox == 0" id="finalscores" :form="form">
      </final-score-content>
      <appreciation-content class="tab-pane fade" id="appreciation" :form="form"></appreciation-content>
      <wording-content class="tab-pane fade" id="wording" :formWording="form.wording"
        @updateWording="updateFormWording"></wording-content>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import _ from "lodash";

import QuestionContent from "./form _content/QuestionContent.vue";
import GroupContent from "./form _content/GroupContent.vue";
import DimensionContent from "./form _content/DimensionContent.vue";
import FinalScoreContent from "./form _content/FinalScoreContent.vue";
import AppreciationContent from "./form _content/AppreciationContent.vue";
import WordingContent from "./form _content/WordingContent.vue";

const processIndustryScores = (industryScores) => {
  if (_.isString(industryScores)) {
    return industryScores;
  } else {
    return JSON.stringify(industryScores);
  }
};

export default {
  components: {
    QuestionContent,
    GroupContent,
    DimensionContent,
    FinalScoreContent,
    AppreciationContent,
    WordingContent,
  },
  data() {
    return {
      form: {},
    };
  },
  created() {
    this.getForm();
  },
  mounted() {
    $("#wrapper").addClass("toggled");
  },
  methods: {
    //取得選擇的問卷
    getForm() {
      var self = this;
      this.axios
        .get(
          "/rise_api/public/index.php/form_question/getForm/" +
          self.$route.params.form +
          "/" +
          self.$route.params.version
        )
        .then((response) => {
          self.form = response.data;

          console.log("wording: ", self.form.wording);

          if (_.isNil(self.form.wording)) {
            self.form.wording = {
              industry: '',
              higher: '',
              equal: '',
              lower: ''
            };
          } else {
            self.form.wording = JSON.parse(self.form.wording);
          }
        });
    },
    preview() {
      var self = this;
      var version = self.$route.params.version;

      var url =
        // "https://rise.iii.org.tw/rise_front/#/view/form/" +
        "/rise_front/#/view/form/" + self.form.id + "/" + version;
      window.open(url);
    },
    updateFormWording(val) {
      console.log('update form wording: ', val);
      this.form.wording = val;
    },
    //儲存、更新問卷
    updateForm(form) {
      var self = this;
      // 判斷有沒有多個其他(避免有人點了其他選項 又自己新增一個其他)
      let more_other = false;
      // 避免題目沒問題
      let no_title = [];

      $.each(self.form.questions, function (key, val) {
        if (val.title == "") no_title.push(key + 1);

        if (val.type == "selection") {
          if (val.maxSelect <= 0) {
            no_title.push(key + 1);
          }
        }
        // if (val.hasOther) {
        //   $.each(val.choiceList, function (key2, val2) {
        //     if(val2.find) {

        //     }
        //   });
        // }
      });
      if (no_title.length > 0) {
        this.$swal({
          icon: "error",
          title: "題目不得為空",
          text: `第${no_title}題的題目不得為空或多選上限需大於0`,
        });
      } else if (self.form.name.trim() === "" || self.form.name === null) {
        this.$swal({
          icon: "error",
          title: "問卷題目不得為空",
          text: `問卷題目不得為空`,
        });
      } else if (
        self.form.first_read === null ||
        self.form.first_read.trim() === ""
      ) {
        this.$swal({
          icon: "error",
          title: "閱讀前需知不得為空",
          text: `閱讀前需知不得為空`,
        });
      } else {
        var maxScore = 0;
        self.form.questions.forEach(function (v) {
          if (v.maxScore !== null && v.maxScore !== undefined) {
            console.log(v.maxScore);
            maxScore += parseFloat(v.maxScore);
          }
        });
        if (
          self.form.questionNumber_checkbox === null ||
          self.form.questionNumber_checkbox === undefined
        ) {
          self.form.questionNumber_checkbox = 1;
        }
        if (
          self.form.blue_checkbox === null ||
          self.form.blue_checkbox === undefined
        ) {
          self.form.blue_checkbox = 0;
        }
        this.$swal({
          text: "確定要更新問卷內容嗎?",
          icon: "question",
          showCancelButton: true,
          confirmButtonText: "確定",
          cancelButtonText: "取消",
          preConfirm: () => {
            console.log('wording: ', self.form.wording);

            return this.axios
              .put("/rise_api/public/index.php/form_question/updateForm", {
                id: self.form.id,
                version: self.form.version,
                first_read: self.form.first_read,
                questions: JSON.stringify(self.form.questions),
                groups: JSON.stringify(self.form.groups),
                dimensions: JSON.stringify(self.form.dimensions),
                final_score: JSON.stringify(self.form.final_score),
                wording: JSON.stringify(self.form.wording),
                industryScores: processIndustryScores(self.form.industryScores),
                finalScoreContent_checkbox:
                  self.form.finalScoreContent_checkbox,
                final_content: self.form.final_content,
                title_content: self.form.title_content,
                titleNote_content: self.form.titleNote_content,
                titleNoteContent_checkbox: self.form.titleNoteContent_checkbox,
                appreciation_checkbox: self.form.appreciation_checkbox,
                appreciation_content: self.form.appreciation_content,
                appreciation_banner: self.form.appreciation_banner,
                company_checkbox: self.form.company_checkbox,
                companyName: self.form.companyName,
                businessIdNumber: self.form.businessIdNumber,
                form_name: self.form.name,
                banner: self.form.banner,
                totalScoreTitle_checkbox: self.form.totalScoreTitle_checkbox,
                totalScoreChart_checkbox: self.form.totalScoreChart_checkbox,
                totalScoreTitle_content: self.form.totalScoreTitle_content,
                fullMarks_content: self.form.fullMarks_content,
                averageScore_content: self.form.averageScore_content,
                distribution_checkbox: self.form.distribution_checkbox,
                distribution_content: self.form.distribution_content,
                capability_checkbox: self.form.capability_checkbox,
                capability_content: self.form.capability_content,
                capabilityNote_checkbox: self.form.capabilityNote_checkbox,
                capabilityNote_content: self.form.capabilityNote_content,
                analytical_checkbox: self.form.analytical_checkbox,
                analytical_content: self.form.analytical_content,
                analyticalNote_checkbox: self.form.analyticalNote_checkbox,
                analyticalNote_content: self.form.analyticalNote_content,
                sectionOne_checkbox: self.form.sectionOne_checkbox,
                sectionOne_content: self.form.sectionOne_content,
                sectionTwo_checkbox: self.form.sectionTwo_checkbox,
                sectionTwo_content: self.form.sectionTwo_content,
                technology_checkbox: self.form.technology_checkbox,
                technology_content: self.form.technology_content,
                technologyNote_checkbox: self.form.technologyNote_checkbox,
                technologyNote_content: self.form.technologyNote_content,
                sectionThree_checkbox: self.form.sectionThree_checkbox,
                sectionThree_content: self.form.sectionThree_content,
                suggest_checkbox: self.form.suggest_checkbox,
                suggest_content: self.form.suggest_content,
                image_checkbox: self.form.image_checkbox,
                image: self.form.image,
                maxScore: maxScore,
                questionNumber_checkbox: self.form.questionNumber_checkbox,
                blue_checkbox: self.form.blue_checkbox,
                quadrantChart_checkbox: self.form.quadrantChart_checkbox,
                quadrantChart_x: self.form.quadrantChart_x,
                quadrantChart_y: self.form.quadrantChart_y,
                quadrantChart_x_avg: self.form.quadrantChart_x_avg,
                quadrantChart_y_avg: self.form.quadrantChart_y_avg,
                quadrantChart_one: self.form.quadrantChart_one,
                quadrantChart_two: self.form.quadrantChart_two,
                quadrantChart_three: self.form.quadrantChart_three,
                quadrantChart_four: self.form.quadrantChart_four,
                // exampleCases 轉為靜態內容，先設為空值 gem
                // exampleCases: self.form.exampleCases,
                exampleCases: "",
              })
              .then((response) => {
                this.$swal({
                  icon: "success",
                  title: "更新完成",
                  showConfirmButton: false,
                  timer: 1500,
                }).then((response) => {
                  // self.$router.go(); //有重整的需要嗎? ans:可以不用
                });
              });
          },
        });
      }
    },
  },
};
</script>


<style scoped>
hr {
  border-top: 2px solid #8c8b8b;
}

.container {
  background-color: #fff;
}

.tab-content {
  height: 50%;
}

.form-control {
  padding: 0 0.35rem;
}

/*-------------------------------*/
/*     Nav tab styles            */
/*-------------------------------*/
.nav-tabs .nav-link {
  color: #fff;
  text-align: center;
  font-weight: 500;
  background-color: #a0bea0;
}

.nav-tabs .nav-link.active {
  color: #fff;
  background-color: #5a825a;
}

.nav-tabs .nav-link:hover {
  color: #fff;
  border: 1px solid #4b6edc;
  background-color: #5a825a;
}

.tab-content .tab-pane {
  height: 100%;
  background-color: #eaeff7;
}

.tab-content .active {
  display: flex;
  align-items: stretch;
}
</style>



