<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-lg-8 col-lg-offset-2">
        <h2 class="title">管理員操作紀錄</h2>
      </div>
    </div>
    <hr />

    <table class="table table-bordered table-striped">
      <thead>
        <tr>
          <th scope="col">使用者</th>
          <th scope="col">執行動作</th>
          <th scope="col">時間</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(form,index) in forms">
          <td>admin ({{ form.form_user }})</td>
          <td>{{ form.form_note }}</td>
          <td>{{ form.form_time }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      new_form_name: "",
      forms: []
    };
  },
  created() {
    var self = this;
    self.getFormList();
  },
  mounted() {
    $("#wrapper").addClass("toggled");
  },
  methods: {
    //一進來取表單資料
    getFormList() {
      var self = this;
      this.axios
        .get("/rise_api/public/index.php/log/admin_log") //本機
        // .get("https://115.85.156.12/rise_api/public/index.php/log/admin_log")
        .then(response => {
          var forms = (self.forms = []);
          $.each(response.data, function(key, value) {
            forms.push({
              form_user: value.user,
              form_note: value.note,
              form_time: value.time
            });
          });
        });
    }
  }
};
</script>

<style>
</style>



