<template>
  <div class="form-group row">
    <dyna-title
      :columnDisplay="columnDisplay"
      :columnName="columnName"
      :columnNullable="columnNullable"
    />
    <div class="form-inline col-lg-10">
      <div v-if="isDate(columnComponent)">
        <date-picker
          format="YYYY-MM-DD"
          type="date"
          value-type="date"
          placeholder="選擇日期"
          :id="columnName"
          :name="columnName"
          v-model="columnValue"
        ></date-picker>
      </div>
      <div v-else-if="isDateTime(columnComponent)">
        <date-picker
          format="YYYY-MM-DD HH:mm:ss"
          type="datetime"
          value-type="date"
          placeholder="選擇日期與時間"
          :id="columnName"
          :name="columnName"
          v-model="columnValue"
        ></date-picker>
      </div>
      <div v-else>
        <input
          type="date"
          class="form-control"
          placeholder="選擇日期"
          :id="columnName"
          :name="columnName"
          v-model="columnValue"
        />
      </div>
      &nbsp;
      <dyna-verifier
        :columnDisplay="columnDisplay"
        :verifier="verifier"
        :needVerify="needVerify"
        :valueGetter="getValue"
      />
      &nbsp;
      <dyna-helper
        :helper="helper"
        :callback="callback"
      />
    </div>
  </div>
</template>

<script>
import dayjs from "dayjs";

import DatePicker from "vue2-datepicker";
import "vue2-datepicker/index.css";

import DynaTitle from "../DynaTitle/index.vue";
import DynaVerifier from "../DynaVerifier/index.vue";
import DynaHelper from "../DynaHelper/index.vue";

import { getEchoMan, getTrueMan, isAsync } from "../../utils/lang";
import { getDefaultHelper } from "../../utils/form";

export default {
  props: {
    columnSpec: Object,
    formName: String
  },
  components: { DatePicker, DynaVerifier, DynaTitle, DynaHelper },
  data() {
    return {
      columnName: "",
      columnDisplay: "",
      columnComponent: "",
      columnLength: "",
      columnNullable: "",
      columnValue_: null,

      needVerify: false,
      needTransform: false,

      verifier: getTrueMan(),
      transformer: getEchoMan(),
      updater: getEchoMan(),
      helper: getDefaultHelper(),
      alertMsg: "",
      validMsg: ""
    };
  },
  computed: {
    columnValue: {
      get() {
        return !!this.columnValue_
          ? this.columnValue_
          : this.parseColumnValue(this.columnSpec.columnValue);
      },
      set(val) {
        this.columnValue_ = val;
        this.updater(val);
        this.$emit("updated", val);
      }
    }
  },
  created() {
    const columnSpec = this.columnSpec;

    this.columnName = columnSpec.columnName;
    this.columnDisplay = columnSpec.columnDisplay;
    this.columnComponent = columnSpec.columnComponent;
    this.columnLength = columnSpec.columnLength;
    this.columnNullable = columnSpec.columnNullable;
    this.columnValue_ = columnSpec.columnValue;

    this.needVerify = columnSpec.needVerify;
    this.needTransform = columnSpec.needTransform;

    this.verifier = columnSpec.verifier;
    this.transformer = columnSpec.transformer;
    this.updater = columnSpec.updater;
    this.helper = columnSpec.helper;
  },
  methods: {
    parseColumnValue(columnValue) {
      if (_.isDate(columnValue)) {
        return columnValue;
      } else {
        return dayjs(columnValue, "YYYY-MM-DD").toDate();
      }
    },
    getValue() {
      return this.columnValue;
    },
    callback(value) {
      const AD = this.parseRoc(value);
      const date = dayjs(AD, "YYYYMMDD");
      this.columnValue = date.toDate();
    },
    parseRoc(roc) {
      const rocDate = `${roc}`.trim();

      if (rocDate.length === 8) {
        return roc;
      } else if (rocDate.length === 7) {
        const AD = 19110000 + parseInt(roc);
        return AD.toString();
      } else {
        this.$swal({
          icon: "error",
          title: "Oops...",
          text: `工商查詢的日期格式有誤：${roc}，請直接輸入`
        });
      }
    },
    async runVerifier() {
      const value = this.columnValue;
      // console.log('run verifier for value: ', value)

      let verifyResult = true;
      if (isAsync(this.verifier)) {
        verifyResult = await this.verifier(value);
      } else {
        verifyResult = this.verifier(value);
      }

      if (!verifyResult) {
        this.alertMsg = `${this.columnDisplay} 驗證失敗`;
        this.validMsg = "";
      } else {
        this.validMsg = `${this.columnDisplay} 驗證成功`;
        this.alertMsg = "";
      }
    },
    isDate(columnComponent) {
      return columnComponent === "date";
    },
    isDateTime(columnComponent) {
      return columnComponent === "datetime";
    }
  }
};
</script>

<style>
.alert {
  color: red;
  font-size: 0.8em;
}

.valid {
  color: green;
  font-size: 0.8em;
}
</style>



