<!--
	題組題型樣式
-->
<template>
	<div class="row mx-0 my-2">
		<div class="group-area col-9 p-2">
			<div class="group-info mx-0 my-2">
				<div class="question-order mr-2">{{ question.number }}</div>
				<div class="question-name">{{question.name}}</div>
			</div>
		</div>
		<div class="option-area col-3 p-2"></div>
	</div>
</template>

<script>
	export default{
		props:['question'],
	}
</script>

<style>
.group-info{
	display: flex;
	align-items:center;
}
.group-area{
	border-right: 2px dashed #bbb;
}
.question-name{
	width: 100%;
	height: 40px;
	color: #000;
	font-weight: 900;
	line-height: 40px;
	text-align: center;
	background-color: #5a825a;
	border: 1px solid #41719c;
	border-radius: .3rem;
	opacity: 0.5;
}
.question-order{
	height: 30px;
	width: 40px;
	border: 1px solid #5b9bd5;
	border-radius: .5rem;
	text-align: center;
	line-height: 30px;
	font-size: 14px;
	font-weight: 500;
	color: #fff;
	background-color: #ccc;
}
</style>



